import { Component } from 'base-component';
// #---------------------------------------# //
// #---- Component (cmp-add-edit-cred) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	credential: '=',
	onNewCancel: '&',
	onAdd: '&',
	onDelete: '&',
	onSave: '&',
	singleEditing: '=', // used to only allow editing one credential at a given time
};

class AddEditCred extends Component {
	/*@ngInject*/
	controller($scope, ssAsync, ssCredentialsApi, ssToastHlp, ssValidationHlp, ssModalSvc, messagingSvc) {
		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope); // jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = {
			isNew: $scope.credential.id == null,
			isSaving: false,
			canAddEdit: !routeVm.isStateNational
		};

		$scope.validator = {
			name: ssValidationHlp.string({ minLength: 1, maxLength: 100, required: true }, [
				(val, field) => {
					let ret = true;
					','.split('').forEach(c => {
						if (ret !== true) { return; }
						if (val.indexOf(c) >= 0) { ret = `${field} cannot contain the '${c}' character`; }
					});
					return ret;
				},
				(val, field) => {
					if (val.trim().length === 0) { return `${field} is required`; }
					return true;
				}
			])
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			edit() {
				ssModalSvc.showDestructive({
					title: messagingSvc.getPlaceholderMessage('credentials', 'editConfirmationTitle', ['Credential']),
					content: messagingSvc.getPlaceholderMessage('credentials', 'editConfirmationMessage', ['credential']),
					primaryText: messagingSvc.getMessage('general', 'continueEditing'),
					secondaryText: messagingSvc.getMessage('general', 'cancel'),
				}).then(() => {
					$scope.singleEditing.value = true;
					$scope.credential.isEditing = true;
				});
			},
			cancel() {
				if (vm.isNew) {
					$scope.onNewCancel();
				} else {
					$scope.credential.$revert();
				}
				$scope.singleEditing.value = false;
				$scope.credential.isEditing = false;
			},
			remove() {
				ssModalSvc.showDestructive({
					title: messagingSvc.getPlaceholderMessage('credentials', 'deleteConfirmationTitle', ['Credential']),
					content: messagingSvc.getPlaceholderMessage('credentials', 'deleteConfirmationMessage', ['credential']),
					primaryText: messagingSvc.getMessage('general', 'delete'),
					secondaryText: messagingSvc.getMessage('general', 'cancel'),
				}).then(() => {
					if (vm.isSaving) { return; }
					vm.isSaving = true;
					ssAsync(function*() {
						try {
							let res = yield $scope.credential.$remove();
							if (res != null) {
								ssToastHlp.success('', `Credential "${$scope.credential._name}" Removed`, false);
								$scope.onDelete({ $credential: $scope.credential });
							}
							vm.isSaving = false;
						} catch (error) {
							ssToastHlp.error(error.message, 'Error', false);
							vm.isSaving = false;
						}
					})();
				});
			},
			save() {
				if (vm.isSaving) { return; }
				vm.isSaving = true;
				ssAsync(function*() {
					$scope.credential._name = $scope.credential._name.trim();
					let res;
					if (vm.isNew) {
						res = yield ssCredentialsApi.addCredential({}, $scope.credential);
					} else {
						res = yield $scope.credential.$save();
					}

					if (res != null) {
						if (vm.isNew) {
							res.isEditing = false;
							// this calls the parent component to add the new credential to the list
							// and null the newCred ref to remove this component
							$scope.onAdd({ $newCred: res });
						} else {
							$scope.credential.isEditing = false;
						}
						if ($scope.onSave) { $scope.onSave(); }
						ssToastHlp.success('', `Credential "${$scope.credential._name}" Saved`, false);
					}
					vm.isSaving = false;
				})();
			}
		};
	}
}

// #-- END Component (cmp-add-edit-cred) --# //
// #---------------------------------------# //

angular.module('ec-settings-components').directive('cmpAddEditCred', () => new AddEditCred(props, 'add-edit-cred'));
