import {Segment} from 'base-segment';
// #-----------------------------------------------# //
// #---- Segment (/permissions/segment-header) ----# //

class Header extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssModalSvc) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentPermissionsHeaderLogic');// jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			showGraphic() {
				ssModalSvc.showLarge({
					title: 'Understanding Permissions',
					content: '<cmp-infographic-svg force-load="true"></cmp-infographic-svg>'
				});
			}
		};
	}
}

// #-- END Segment (/permissions/segment-header) --# //
// #-----------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentPermissionsHeader', () => new Header('header'));
