import {Segment} from 'base-segment';
// #-------------------------------------------------------# //
// #---- Segment (/permissions/groups/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentPermissionsGroupsContentLogic');// eslint-disable-line


		$scope.groupsVm = routeVm;

		// view model for tracking state
		const vm = $scope.vm = { // eslint-disable-line
			autoCreatedGroups: [],
			customCreatedGroups: []
		};

		$scope.$watchCollection('routeVm.groups', () => {
			vm.autoCreatedGroups = routeVm.groups.filter(x => x._autoCreated);
			vm.customCreatedGroups = routeVm.groups.filter(x => !x._autoCreated);
		})

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
		};
	}
}

// #-- END Segment (/permissions/groups/segment-content) --# //
// #-------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentPermissionsGroupsContent', () => new Content('content'));
