import {Component} from 'base-component';
// #--------------------------------------# //
// #---- Component (cmp-pay-schedule) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	schedule: '=',
	org: '='
};

class PaySchedule extends Component {
	/*@ngInject*/
	controller($scope, $element, ssModalSvc, ssToastHlp, $log, ssValidationHlp, ssEnum, rAF, ssDateByOrgHlp, messagingSvc) {

		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope); // jshint ignore:line
		const validation = {
			minZero: ssValidationHlp.numeric({ required: true, min: 0 }),
			required: ssValidationHlp.required()
		};

		const org = $scope.org;
		const MAX_LANES_VISIBLE = 6;
		const MAX_LANES = 175;
		const MAX_STEPS = 175;

		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
			payRateOptions: ssEnum.salaryType.getEnums(),
			validation: validation,
			fromDateUtc: ssDateByOrgHlp.utcIsoToRelativeMoment($scope.schedule._fromDateUtc, org).format('MM/DD/YYYY'),
			toDateUtc: ssDateByOrgHlp.utcIsoToRelativeMoment($scope.schedule._toDateUtc, org).format('MM/DD/YYYY'),
			messages: {
				performanceWarning: messagingSvc.getMessage('settings', 'performanceWarning')
			}
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = {  // jshint ignore:line

			calculateWidth(schedule){

				var cols;

				if (schedule._payLanes.length > MAX_LANES_VISIBLE) {

					if(schedule.id) {
						cols = angular.element(document.getElementById(`grid-values-${schedule.id}`).getElementsByClassName('grid-col'));
					} else {
						cols = angular.element(document.getElementById('grid-values-00000').getElementsByClassName('grid-col'));
					}

					if(cols.length) {
						let colWidth = cols[0].offsetWidth;
						let amount = parseFloat(colWidth) * (schedule._payLanes.length - MAX_LANES_VISIBLE);
						return {'width': 'calc(100% + ' + amount + 'px)'};
					}
				}
				return {};
			},

			addLane(schedule) {

				if (schedule._payLanes.length === MAX_LANES) { return; }

				var newLane = {
					'name': `Lane ${schedule._payLanes.length + 1}`,
					'paySteps': []
				};

				schedule._payLanes[schedule._payLanes.length - 1].paySteps.forEach(step => {
					var psObj = {};
					psObj['name'] = step.name;
					psObj['active'] = step.active;
					psObj['payRate'] = step.payRate;
					newLane['paySteps'].push(psObj);
				});

				var lanes = schedule._payLanes.slice();
				lanes.push(newLane);
				schedule._payLanes = lanes;

				routeVm.updateLaneWidths(schedule);
			},

			removeLane(schedule) {
				if (schedule._payLanes.length <= schedule.minLanes) { return; }
				schedule._payLanes.splice(-1, 1);
				routeVm.updateLaneWidths(schedule);
			},

			addStep(schedule) {

				if (schedule._payLanes[0].paySteps.length === MAX_STEPS) { return; }

				schedule._payLanes.forEach(lane => {
					var psObj = {};
					psObj['name'] = `Step ${lane.paySteps.length + 1}`;
					psObj['active'] = true;
					psObj['payRate'] = lane.paySteps[lane.paySteps.length - 1].payRate;
					lane['paySteps'].push(psObj);
				});
			},

			removeStep(schedule) {
				if (schedule._payLanes[0].paySteps.length <= schedule.minSteps) { return; }

				schedule._payLanes.forEach(lane => {
					lane.paySteps.splice(-1, 1);
				});
			},

			editSchedule(schedule) {
				schedule.isEditing = true;
				schedule.showDetails = true;

				disableEdit();
				resizeTitle();

			//	watches a date change and resets the minimum # of lanes and steps based on the schedule that comes immediately before it
				$scope.$watch('schedule._fromDateUtc', function(nv,ov){
					if(nv === ov) { return; }
					setMinLanesAndSteps();
				}, $scope);
			},

			cancelEditSchedule(schedule) {
				if(schedule.isTemp) {
					let ind = routeVm.paySchedules.indexOf(schedule);
					routeVm.paySchedules.splice(ind,1);
					routeVm.addingPS = false;
					delete schedule.isTemp;
				} else {
					routeVm.forceChangeToPayLanes(schedule);
					schedule.$revert();

					/*
						Reverting the vm.fromDateUtc because this prop is part of component and $revert revert the changes to JSON API
						on revert we have to specifically revert the vm value.
					*/
					if(schedule === $scope.schedule) {
						vm.fromDateUtc = ssDateByOrgHlp.utcIsoToRelativeMoment(schedule._fromDateUtc, org).format('MM/DD/YYYY')
					}
					schedule.isEditing = false;
					schedule.showDetails = schedule.isCurrent;
				}
				$scope.$emit('ss-validate-reset');
				routeVm.updateLaneWidths(schedule);
			},

			removePaySchedule: function(ps){
				ssModalSvc.showDestructive({
					title: `<h2>Are you sure you want to delete this pay schedule?</h2>`,
					primaryText: 'Delete',
					secondaryText: 'Cancel',
				}).then(() => {
					ps.isSaving = true;
					let ind = routeVm.paySchedules.indexOf(ps);
					routeVm.paySchedules.splice(ind,1);
					routeVm.paySchedules.$save().then(() => {
						ssToastHlp.success(``, `Pay Schedule "${ps._name}" Deleted`, false);
						routeVm.fetchJobType(false, true);
					}).catch((error) => {
						ssToastHlp.error(``,`Error while deleting Pay Schedule "${ps._name}"`, false);
						$log.error(`Error while deleting Pay Schedule "${ps._name}"`, error);
					});
				});
			}
		};

		$scope.$watch('vm.fromDateUtc', (newVal, oldVal) => {
			if (newVal && newVal === oldVal) { return; }
			if(newVal instanceof Date) {
				$scope.schedule._fromDateUtc = ssDateByOrgHlp.startOfDayRelativeTo(moment(newVal), org);
			}
		});

		$scope.$watch('schedule._toDateUtc', (newVal, oldVal) => {
			if (newVal && newVal === oldVal) { return; }
			vm.toDateUtc = ssDateByOrgHlp.utcIsoToRelativeMoment(newVal, org).format('MM/DD/YYYY');
		});

		$scope.$watch('schedule._name', (newVal, oldVal) => {
			if (newVal && newVal === oldVal) { return; }
			resizeTitle();
		});

		function resizeTitle() {
			rAF.raw(function () {
				var titleLength;
				var titleEl = $element[0].querySelectorAll('.pay-schedule-title');
				var titleInput = angular.element(titleEl[0]).find('input');

				if (titleInput.val()) {
					titleLength = titleInput.val().length;
					titleInput.attr('size', titleLength);
				} else {
					titleInput.attr('size', 30);
				}
			});
		}

	//	method to only allow one schedule to be edited at a time
		function disableEdit() {
			routeVm.paySchedules.forEach((ps) => {
				if(ps !== $scope.schedule) {
					go.cancelEditSchedule(ps);
				}
			});
		}

	//	Find the closest previously dated schedule of the target schedule
		function getPreviousSchedule(schedule) {
			let start = ssDateByOrgHlp.utcIsoToRelativeMoment(schedule._fromDateUtc, org);
			return routeVm.paySchedules.filter((ps) => {
				let psTime = ssDateByOrgHlp.utcIsoToRelativeMoment(ps._fromDateUtc, org);
				return !ps.isDefault && psTime.isBefore(start);
			}).reduce((prev, curr) => {
				if(prev === 0) { return curr; }
				let prevFromDateUtc = ssDateByOrgHlp.utcIsoToRelativeMoment(prev._fromDateUtc, org);
				let currentFromDateUtc = ssDateByOrgHlp.utcIsoToRelativeMoment(curr._fromDateUtc, org)
				return prevFromDateUtc.isAfter(currentFromDateUtc) ? prev : curr;
			}, 0);
		}

	//	Set the minimum # of lanes and steps based on the schedule that comes immediately before it
		function setMinLanesAndSteps() {
			let prevSchedule = getPreviousSchedule($scope.schedule);
			$scope.schedule.minLanes = prevSchedule ? prevSchedule._payLanes.length : 1;
			$scope.schedule.minSteps = prevSchedule ? prevSchedule._payLanes[0].paySteps.length : 1;
		}

	}
}

// #-- END Component (cmp-pay-schedule) --# //
// #--------------------------------------# //

angular.module('ec-settings-components').directive('cmpPaySchedule', () => new PaySchedule(props, 'pay-schedule') );
