import {State} from 'base-state';
// #-----------------------------------------------# //
// #---- State (/permissions/users/user-detail) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const stateName = 'permissions.users.detail';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/{userId:[0-9A-Za-z-]{36}}',
	templateUrl: 'routes/permissions/users/user-detail/user-detail.html',
	resolve: {
		emp: /*@ngInject*/ function($stateParams, ssAsync, ssEmployeeApi) {
			return ssAsync(function*() {
				routeVm.emp = yield ssEmployeeApi.get({
					id: $stateParams.userId,
					fields: 'firstName,lastName'
				});
			})();
		},
		promises: /*@ngInject*/ (ssSecurityGroupsApi, ssPermissionProfilesApi) => {
			[routeVm.securityGroupsPromise, routeVm.profilesPromise] = [
				ssSecurityGroupsApi.query({
					include: 'securityScopes',
					limit: 50
				}),
				/*

					limit: 50
					Is the max number of record the end point can send at a time
					further records can be retrieved iteratively.

					We are showing the list of Permission Profiles in the dropdown and
					we have to either iteratively load all the Profiles and then show them
					in drop down or drop downs have to support lazy loading.

					Mel has mentioned in the ticket https://frontlinetechnologies.atlassian.net/browse/PLAT-4804
					that she is fine with limit of 50 and expect that districts will not going
					to exceed the limit.
				*/
				ssPermissionProfilesApi.query({
					include: 'permissionSets',
					limit: 50
				})
			];
		},
		$title: /*@ngInject*/ (emp) => {
			if (emp == null) {
				return 'Frontline Central - User Detail';
			}
			return `Frontline Central - ${emp.fullName}`;
		}
	}
};

// #-- END State (/permissions/users/user-detail) --# //
// #-----------------------------------------------# //

angular.module('ec-settings').config( /*@ngInject*/ ($stateProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
