import { Component } from 'base-component';
// #----------------------------------------# //
// #---- Component (cmp-manage-issuers) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
};

class ManageIssuers extends Component {
	/*@ngInject*/
	controller($scope, ssAsync, ssToastHlp, ssCredentialDataAccessSvc, ssCredentialBusinessEngineSvc, ssDateByOrgHlp) {
		const { credentialConstants } = ssCredentialBusinessEngineSvc;
		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope); // jshint ignore:line
		var org = routeVm.org; 

		// view model for tracking state
		const vm = $scope.vm = {
			loading: true,
			items: [],
			newIssuer: null,
			searchText: {},
			isEditing: false,
			canAddEdit: !routeVm.isStateNational,
		};

		ssAsync(function*() {
			try {
				let type = routeVm.isStateNational ? credentialConstants.STATE_NATIONAL : credentialConstants.DISTRICT_CUSTOM;
				vm.items = yield ssCredentialDataAccessSvc.getIssuers(type);
				vm.items.forEach(item => item.isEditing = false);
				sort();
			} catch (error) {
				ssToastHlp.error(error.message, 'Error', false);
			} finally {
				vm.loading = false;
			}
		})();

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			add() {
				vm.newIssuer = { 
					_name: '',
					_updatedUtc: ssDateByOrgHlp.utcIsoToRelativeMoment(new Date().toISOString(), org).toISOString(),
					isEditing: true
				};
				vm.isEditing = true;
			},
			onNewCancel() {
				vm.newIssuer = null;
				vm.isEditing = false;
			},
			onAdd(newIssuer) {
				vm.items.push(newIssuer);
				vm.newIssuer = null;
			},
			onDelete(issuer) {
				vm.items = vm.items.filter(item => item.id !== issuer.id);
				vm.isEditing = false;
			},
			onSave() {
				vm.isEditing = false;
				sort();
			},
		};

		function sort() {
			vm.items.sort((a, b) => {
				// ignore case
				var nameA = a._name.toUpperCase();
				var nameB = b._name.toUpperCase();
				if (nameA < nameB) {
					return -1;
				} else if (nameA > nameB) {
					return 1;
				}
				// names must be equal
				return 0;
			});
		}
	}
}

// #-- END Component (cmp-manage-issuers) --# //
// #----------------------------------------# //

angular.module('ec-settings-components').directive('cmpManageIssuers', () => new ManageIssuers(props, 'manage-issuers'));
