/*@ngInject*/
function logic(enumSecurityGroupScope, createPermissionsHlp, ssStringHlp) {

	// logic api
	return {

		createScopeObj(config) {
			var obj = {};
			var key = ssStringHlp.toCamelCase(config.type);
			obj[key] = getScopeControl(config.type, config.context, config.total, config.emp);
			return obj;
		},

		getContextPermissions(contexts, profiles) {
			let permissions = {};
			contexts.forEach(context => {
				permissions[context.id] = [];

				let registration = context._protectedResourceRegistration;
				profiles.forEach(profile => {
					if (profile == null) { return; }

					let permissionSet = profile._permissionSets.find(s => s._protectedContextRegistrationId === context.id);

					if (permissionSet == null) {
						permissionSet = createPermissionsHlp.permissionSet();
						permissionSet._protectedContextRegistrationId = context.id;
						permissionSet._permissions.resourceName = registration.resourceName;

						profile._permissionSets.push(permissionSet);
					}
					permissions[context.id].push(permissionSet._permissions);
				});
			});
			return permissions;
		},

		removeUserFromGroup(emp, group) {
			let eIndex = group._employeeIds.indexOf(emp.id);
			if (eIndex < 0) {
				return;
			}
			group._employeeIds.splice(eIndex, 1);
			// create a new reference for json api to pick up
			group._employeeIds = [...group._employeeIds];
			return group;
		}
	};

	function getScopeControl(type, context, count, emp) {
		switch(context) {
			case enumSecurityGroupScope.none:
				return `No ${type}s`;
			case enumSecurityGroupScope.self:
				return `${emp._firstName}'s Own ${type}`;
			case enumSecurityGroupScope.specific:
				let label = count === 1 ? type : `${type}s`;
				return `Specific: ${label}`;
			case enumSecurityGroupScope.all:
				return `Any`;
			case enumSecurityGroupScope.directReports:
				return `${count} Direct Report${count !== 1 ? 's' : ''}`;
		}
	}

}
angular.module('ec-settings-segment-logic').factory('segmentPermissionsUsersUserDetailContentLogic', logic);
