import { Component } from 'base-component';
// #----------------------------------------# //
// #---- Component (cmp-security-scope) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	model: '=?',
	profiles: '=',
	onRemove: '&',
	onUpdate: '&',
	group: '='
};

class SecurityScope extends Component {
	/*@ngInject*/
	controller($scope, ssAsync, enumSecurityGroupScope, securityScopeHlp, createPermissionsHlp, ssSecurityScopeApi, ssToastHlp, ssLocationSvc, ssJobTypeSvc, ssDepartmentSvc, ssEmployeeApi, ssArrayHlp, ssModalSvc, ssValidationHlp) {
		const vm = $scope.vm = { // eslint-disable-line
			editMode: !$scope.model.id,
			locationSecurityGroupScope: securityScopeHlp.location(),
			departmentSecurityGroupScope: securityScopeHlp.department(),
			jobTypeSecurityGroupScope: securityScopeHlp.jobType(),
			employeeSecurityGroupScope: securityScopeHlp.employee(),
			employeeSecurityDisabledScopes: [],
			selectedLocations: [],
			selectedJobTypes: [],
			selectedDepartments: [],
			selectedEmployees: [],
			profileOptions: [],
			isSaving: false,
			intended: {}
		};

		const validation = $scope.validation = {
			required: ssValidationHlp.required(),
			externalId: ssValidationHlp.string({ maxLength: 100 })
		}

		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope);

		var currentScope,
			saveContext,
			assignedLocations,
			assignedDepartments,
			assignedJobTypes,
			assignedEmployees

		//	save a copy of the current security scope model so we can revert to it upon edit cancellation
		currentScope = Object.assign({}, $scope.model);

		vm.intended = updateIntendedValues();

		//	determine which profiles should be seen in the options menu and assign then to view
		vm.profileOptions = $scope.profiles.filter(p => !p._locked);

		const go = $scope.go = { // eslint-disable-line
			selectProfile(profile) {
				$scope.$emit('ss-validate-reset');
				vm.intended.profile = profile;
				$scope.$emit('ss-menu-close');
			},

			edit() {
				vm.editMode = true;
			},

			cancel() {

				//	If this is a newly added scope, defer cancel functionality to the parent controller & return
				if (!$scope.model.id && $scope.onRemove) {
					$scope.onRemove({ $securityScope: $scope.model });
					return;
				}
				//	reset any validation errors
				$scope.$emit('ss-validate-reset');

				//	reset the bound permission profile & scope control values
				vm.intended = updateIntendedValues();

				vm.selectedLocations = angular.copy(assignedLocations) || [];
				vm.selectedDepartments = angular.copy(assignedDepartments) || [];
				vm.selectedJobTypes = angular.copy(assignedJobTypes) || [];
				vm.selectedEmployees = angular.copy(assignedEmployees) || [];

				//	switch to view mode
				vm.editMode = false;

				//	close the profile menu
				$scope.$emit('ss-menu-close');
			},

			save() {

				vm.isSaving = true;
				//	assign intended values to the model
				$scope.model._permissionProfileId = vm.intended.profile.id;
				$scope.model.permissionProfile = vm.intended.profile;
				$scope.model._locationAdministrationContext = vm.intended.locationAdministrationContext;
				$scope.model._departmentAdministrationContext = vm.intended.departmentAdministrationContext;
				$scope.model._jobTypeAdministrationContext = vm.intended.jobTypeAdministrationContext;
				$scope.model._employeeAdministrationContext = vm.intended.employeeAdministrationContext;

				//	re-map & assign any specifically chosen scope control categories and assign to the model
				let locationIds = vm.selectedLocations.map(l => l.id);
				let departmentIds = vm.selectedDepartments.map(l => l.id);
				let jobTypeIds = vm.selectedJobTypes.map(l => l.id);
				let employeeIds = vm.selectedEmployees.map(e => e.id);

				$scope.model._locationIds = locationIds;
				$scope.model._departmentIds = departmentIds;
				$scope.model._jobTypeIds = jobTypeIds;
				$scope.model._employeeIds = employeeIds;

				//	re-copy the values of any specifically chosen scope control categories
				assignedLocations = angular.copy(vm.selectedLocations);
				assignedDepartments = angular.copy(vm.selectedDepartments);
				assignedJobTypes = angular.copy(vm.selectedJobTypes);
				assignedEmployees = angular.copy(vm.selectedEmployees);

				//	If this is a new scope, the save context is the group, otherwise it is the model (securityScope)
				if (!$scope.model.id) {
					saveContext = $scope.group;
				} else {
					saveContext = $scope.model;
				}

				saveContext.$save().then((res) => {
					if (res != null) {
						ssToastHlp.success('', 'Permission Profile Saved', false);
					}

					//	re-set a copy of the current security scope model
					currentScope = Object.assign({}, $scope.model);

					// update the intended values to the currently assigned model values
					vm.intended = updateIntendedValues();

					vm.editMode = false;
					vm.isSaving = false;

					if ($scope.onUpdate) {
						$scope.onUpdate();
					}
				}).catch((e) => {
					vm.isSaving = false;
					ssToastHlp.error('There was a problem saving the permission profile and/or security scope', '', false);
				});

			},

			remove() {
				vm.isSaving = true;
				$scope.model.$remove().then(() => {
					vm.editMode = false;
					vm.isSaving = false;
					$scope.$emit('ss-menu-close');
					if ($scope.onRemove) {
						$scope.onRemove({ $securityScope: $scope.model });
					}
					ssToastHlp.success(`"${$scope.model.permissionProfile._permissionProfileName}" and its security scope were successfully removed"`, '', false);
				}).catch((e) => {
					vm.isSaving = false;
					ssToastHlp.error(`"${$scope.model.permissionProfile._permissionProfileName}" and its security scope were not successfully removed"`, '', false);
					console.log('Error removing "${model.permissionProfile._permissionProfileName}" and its security scope"', e);
				});
			},

			validateProfile(profile) {
				//	Adding this here so we can leverage validation reset
				$scope.$emit('ss-validate-reset');

				if (!profile.id) {
					return 'Profile is Required';
				}

				if (routeVm.uniqueProfiles.indexOf(profile) === -1 || profile.id === currentScope.permissionProfile.id) {
					return true;
				}
				return 'This profile is already in use';
			}
		};

		$scope.$on('$destroy', $scope.$watchCollection('model', (nv, ov) => {
			vm.locationScope = labelDisplay('location', nv._locationAdministrationContext, nv._locationIds.length);
			vm.departmentScope = labelDisplay('dept', nv._departmentAdministrationContext, nv._departmentIds.length);
			vm.jobTypeScope = labelDisplay('job type', nv._jobTypeAdministrationContext, nv._jobTypeIds.length);
			vm.employeeScope = labelDisplay('employee', nv._employeeAdministrationContext, nv._employeeIds.length);
		}));

		$scope.$on('$destroy', $scope.$watch('vm.intended.locationAdministrationContext', (nv, ov) => {
			vm.selectedLocations = [];

			//Specific
			if (nv === 2) {
				ssAsync(function* () {
					try {
						vm.locations = yield ssLocationSvc.getAll();
						if (vm.intended.locationIds.length > 0) {
							vm.intended.locationIds.forEach(id => {
								let location = vm.locations.find(l => l.id === id);
								if (location != null) {
									vm.selectedLocations.push(location);
								}
							});

							//	assign a copy of the currently selected locations for revert
							assignedLocations = angular.copy(vm.selectedLocations);
						}

					} catch (e) {
						console && console.log && console.log('Error getting locations', e);
					}
				})();
			} else {
				// vm.selectedLocations = [];
				vm.intended.locationIds = [];
			}
		}));

		$scope.$on('$destroy', $scope.$watch('vm.intended.departmentAdministrationContext', (nv, ov) => {
			vm.selectedDepartments = [];
			//Specific
			if (nv === 2) {
				ssAsync(function* () {
					try {
						vm.departments = yield ssDepartmentSvc.getAll();
						if (vm.intended.departmentIds.length > 0) {
							vm.intended.departmentIds.forEach(id => {
								let department = vm.departments.find(l => l.id === id);
								if (department != null) {
									vm.selectedDepartments.push(department);
								}
							});

							//	assign a copy of the currently selected departments for revert
							assignedDepartments = angular.copy(vm.selectedDepartments);
						}

					} catch (e) {
						console && console.log && console.log('Error getting departments', e);
					}
				})();
			} else {
				vm.intended.departmentIds = [];
			}
		}));

		$scope.$on('$destroy', $scope.$watch('vm.intended.jobTypeAdministrationContext', (nv, ov) => {
			vm.selectedJobTypes = [];
			//Specific
			if (nv === 2) {
				ssAsync(function* () {
					try {
						vm.jobTypes = yield ssJobTypeSvc.getAll();
						if (vm.intended.jobTypeIds.length > 0) {
							vm.intended.jobTypeIds.forEach(id => {
								let jobType = vm.jobTypes.find(l => l.id === id);
								if (jobType != null) {
									vm.selectedJobTypes.push(jobType);
								}
							});

							//	assign a copy of the currently selected job types for revert
							assignedJobTypes = angular.copy(vm.selectedJobTypes);
						}

					} catch (e) {
						console && console.log && console.log('Error getting job types', e);
					}
				})();
			} else {
				vm.intended.jobTypeIds = [];
			}
		}));

		$scope.$on('$destroy', $scope.$watch('vm.intended.employeeAdministrationContext', (nv, ov) => {
			vm.selectedEmployees = [];
			if (nv === 2) {
				if (vm.intended.employeeIds.length > 0) {
					vm.intended.employeeIds.forEach(id => {
						try {
							ssAsync(function* () {
								let employee = yield ssEmployeeApi.get({
									id: id
								});

								if (vm.selectedEmployees.find(e => e.id === id) == null) {
									vm.selectedEmployees.push(employee);
								}

								//	assign a copy of the currently selected employees for revert
								assignedEmployees = angular.copy(vm.selectedEmployees);

							})();
						} catch (e) {
							console && console.log && console.log('Error getting employee', e);
						}
					});
				}
			} else {
				vm.intended.employeeIds = [];
			}
		}));

		function updateIntendedValues() {
			return {
				profile: currentScope.permissionProfile,
				locationAdministrationContext: currentScope._locationAdministrationContext,
				locationIds: currentScope._locationIds,
				departmentAdministrationContext: currentScope._departmentAdministrationContext,
				departmentIds: currentScope._departmentIds,
				jobTypeAdministrationContext: currentScope._jobTypeAdministrationContext,
				jobTypeIds: currentScope._jobTypeIds,
				employeeAdministrationContext: currentScope._employeeAdministrationContext,
				employeeIds: currentScope._employeeIds
			}
		}

		function labelDisplay(type, context, count) {
			switch (context) {
				case enumSecurityGroupScope.none:
					return `No ${type}s`;
				case enumSecurityGroupScope.self:
					return `Member's own ${type}`;
				case enumSecurityGroupScope.specific:
					let label = (count === 1) ? type : `${type}s`;
					return `${count} specific ${label}`;
				case enumSecurityGroupScope.all:
					return `Any ${type}`;
				case enumSecurityGroupScope.directReports:
					return `Direct Reports`;
			}

			return '';
		}

	}
}

// #-- END Component (cmp-security-scope) --# //
// #----------------------------------------# //

angular.module('ec-settings-components').directive('cmpSecurityScope', () => new SecurityScope(props, 'security-scope'));
