import { Segment } from 'base-segment';
// #-----------------------------------------------------------------------------------# //
// #---- Segment (/general-config/drivers-license/segment-drivers-license-content) ----# //

class DriversLicenseContent extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssListApi, ssAsync, ssActiveUserSvc) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentGeneralConfigDriversLicenseDriversLicenseContentLogic'); // jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = {
			config: {
				gridColumns: [{
					label: 'Drivers License Type',
					sortBy: '_name',
					viewTemplate: '<span ng-bind="item._name"></span>',
					editTemplate: '<super-input-text model="item._name" ss-validate="validation.required(item._name, \'Drivers License Type\')"></super-input-text>'
				}],
				cacheKey: ''
			},
			permissions: {
				canView: $scope.$root.isPermitted.listHeaders.view || false,
				canCreate: $scope.$root.isPermitted.listHeaders.create || false,
				canEdit: $scope.$root.isPermitted.listHeaders.edit || false,
				canDelete: $scope.$root.isPermitted.listHeaders.delete || false
			}
		};

		ssAsync(function*() {
			try {
				const orgId = yield ssActiveUserSvc.getOrgId();
				vm.config.cacheKey = `${orgId}_list_employee.driverslicense`;
			} catch (e) {
				console && console.error && console.error('ERROR', e);
			}
		})();

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			getDriversLicense() {
				return ssListApi.query({
					externalId: 'employee.driverslicense',
					include: 'Items',
					limit: 500
				});
			}
		};
	}
}

// #-- END Segment (/general-config/drivers-license/segment-drivers-license-content) --# //
// #-----------------------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentGeneralConfigDriversLicenseDriversLicenseContent', () => new DriversLicenseContent('drivers-license-content'));
