/*@ngInject*/
function logic() {
	// logic api
	return {
		calculateDateBoundaries,
		byMoreRecent
	};

	function byMoreRecent(a, b){
		return new Date(b._toDateUtc).getTime() - new Date(a._fromDateUtc).getTime();
	};

	function calculateDateBoundaries({ list, fromProp = '_fromDateUtc', toProp = '_toDateUtc' }) {
		list.sort(byMoreRecent);
		list.forEach(function(item, index, arr) {
			let currentItem = item;
			let nextItem = arr[index + 1];
			if(nextItem) {
				currentItem.dtOptions = { minDate: moment(nextItem[fromProp]).add(1, 'days').toDate() }
			}else {
				currentItem.dtOptions = {};
			}
		});
		return list;
	}
}
angular.module('ec-settings-segment-logic').factory('segmentGeneralConfigJobTypesJobTypeDetailJobTypeDetailLogic', logic);
