import {Segment} from 'base-segment';
// #-------------------------------------------------------------------------# //
// #---- Segment (/general-config/signatures/segment-signatures-content) ----# //

class SignaturesContent extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssAsync, FileUploader, ssActiveUserSvc, ssSignatureApi, ssEnum, ssToastHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line
		const parentVm = $scope.parentVm;

		const itemCache = {};

		// hookup segment logic
		const logic = $injector.get('segmentGeneralConfigSignaturesSignaturesContentLogic'); // jshint ignore:line
		routeVm.onBeforeCancel = function(item) {
			vm.config.tempFile = [];
		}

		routeVm.onBeforeSave = function(item) {
			var result = {}
			if (item._displayMethod !== ssEnum.signatureDisplayMethod.image) { 
				cacheItem(item);

				if (item._fileId) { item._fileId = null; }
				if (item._imageUrl) { item._imageUrl = null; }
				result.success = true;
				return result;
			}
			if (item._displayMethod === ssEnum.signatureDisplayMethod.image){
				// No temp file and no existing file
				if (vm.config.tempFile.length == 0 && item._fileId == null){
					result.success = false;
					result.errorMessage = 'Please upload a file before saving.'
				// No temp file but does have existing file
				}else if (vm.config.tempFile.length == 0 && item._fileId != null) {
					result.success = true;
				// Temp file available
				}else if (vm.config.tempFile[0].id){
					item._fileId = vm.config.tempFile[0].id;
					result.success = true;
				}
				return result;
			}
		}

		// view model for tracking state
		const vm = $scope.vm = {
			config: {
				tempFile: [],
				getFile: function(signature){
					return go.getFile(signature);
				},
				customExtend: function(signature) {
					return go.extendSignature(signature);
				},
				displayEnums: ssEnum.signatureDisplayMethod.getEnums(),
				filteredFileTypes: ['jpg','jpeg','png'],
				useUiFilter: true,
				gridColumns: [{
					label: 'Name',
					required: true,
					sortBy: 'name',
					viewTemplate: `<p ng-bind="item._name"></p>`,
					editTemplate: `<super-input-text placeholder="Name" aria-label="{{item._name}}" model="item._name" ss-validate="validation.required(item._name, 'Name')" required="true"></super-input-text>`
				},{
					label: 'Title',
					required: true,
					viewTemplate: `<p ng-bind="item._title"></p>`,
					editTemplate: `<super-input-text model="item._title" aria-label="{{item._name}}" ss-validate="validation.required(item._title, 'Title')" placeholder="Title"></super-input-text>`
				},{
					label: 'Display Method',
					required: true,
					viewTemplate: `<p ng-bind="item.displayMethod"></p>`,
					editTemplate: `<p><super-select-list model="item._displayMethod" ss-validate="validation.required(item._displayMethod, 'Display Method')" required="true"><super-select-list-item ng-repeat="method in config.displayEnums" label="method.name" value="method.value"></super-select-list-item></super-select-list></p>`
				},{
					label: 'Signature Preview',
					viewTemplate: `<a class="document" ss-icon-before="file-photo-o" href="#" ng-if="item._displayMethod == 1 && !item._imageUrl" ng-click="config.getFile(item)">Preview</a>
								<img ng-if="item._displayMethod == 1 && item._imageUrl" ng-src="{{item._imageUrl}}" alt="{{item._name}}" style="width: 100%"></img>
								<div ng-if="item._displayMethod == 0" class="signature" ng-bind="item._name"></div>`,
					editTemplate: `<super-file-upload prevent-multiple-uploads="true" filtered-file-types="config.filteredFileTypes" label="" ng-if="item._displayMethod == 1" model="config.tempFile" ></super-file-upload>
									<div ng-if="item._displayMethod == 0" class="signature" ng-bind="item._name">`
				}],
				cacheKey: ''
			},
			permissions: {
				canView: $scope.$root.isPermitted.signatures.manage || false,
				canCreate: $scope.$root.isPermitted.signatures.manage || false,
				canEdit: $scope.$root.isPermitted.signatures.manage || false,
				canDelete: $scope.$root.isPermitted.signatures.manage || false
			}
		};

		ssAsync(function*() {
			try {
				const orgId = yield ssActiveUserSvc.getOrgId();
				vm.config.cacheKey = `${orgId}_departments`;
			} catch (e) {
				console && console.error && console.error('ERROR', e);
			}
		})();

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			getSignatures(filter) {
				return ssSignatureApi.query(filter);
			},
			extendSignature(signature) {
				signature.displayMethod = ssEnum.signatureDisplayMethod.byValue(signature._displayMethod);
			},
			getFile(signature){
				ssAsync(function*() {
					var sigWithFileUrl = yield ssSignatureApi.get({ id: signature.id });
					try {
						signature._imageUrl = sigWithFileUrl._imageUrl ? sigWithFileUrl._imageUrl : ''

					} catch (e) {
						ssToastHlp.error("Image not found! Please upload a new image.", 'Error', false);
						console && console.error && console.error('ERROR', e);
					}
				})();
			}
		};

		// cache signature row (item) in the event user cancels prior to saving
		function cacheItem(item) {
			itemCache.item = item;
		}
		
	}
}

// #-- END Segment (/general-config/signatures/segment-signatures-content) --# //
// #-------------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentGeneralConfigSignaturesSignaturesContent', () => new SignaturesContent('signatures-content') );

