import {Component} from 'base-component';
// #-----------------------------------------# //
// #---- Component (cmp-job-type-detail) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	jobtypeDetail: '=',
	org: '='
};

class JobTypeDetail extends Component {
	/*@ngInject*/
	controller($scope, ssOrganizationSvc, ssValidationHlp, ssDateHlp, ssAsync, moment, ssModalSvc, ssToastHlp, $log, ssDateByOrgHlp) {
		// view model for tracking state
		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope); // jshint ignore:line
		var jobtypeDetail = $scope.jobtypeDetail;
		const org = $scope.org;
		jobtypeDetail.isProcessing = false;
		jobtypeDetail.editMode = (jobtypeDetail.editMode) ? jobtypeDetail.editMode : false;
		jobtypeDetail.expanded = jobtypeDetail.expanded ? jobtypeDetail.expanded : (jobtypeDetail.isCurrent || jobtypeDetail.isPast);
		const validation = $scope.validation = {
			minZero: ssValidationHlp.numeric({ required: true, min: 0 }),
			required: ssValidationHlp.required()
		};

		var cols = [
			[{
				prop: '_fTE',
				displayText: 'FTE'
			},{
				prop: '_hoursPerDay',
				displayText: 'Hours Per Day'
			},{
				prop: '_workDaysPerYear',
				displayText: 'Work Days Per Year'
			},{
				prop: '_payPeriodsPerYear',
				displayText: 'Pays Per Year'
			}],[{
				prop: '_paidHolidaysPerSchoolYear',
				displayText: 'Paid Holidays Per Year'
			},{
				prop: '_sickDaysPerSchoolYear',
				displayText: 'Sick Days Per Year'
			},{
				prop: '_personalDaysPerSchoolYear',
				displayText: 'Personal Days Per Year'
			},{
				prop: '_vacationDaysPerSchoolYear',
				displayText: 'Vacation Days Per Year'
			}]
		];

		const vm = $scope.vm = {
			cols,
			startDateTxt: ssDateByOrgHlp.utcIsoToRelativeMoment(jobtypeDetail._fromDateUtc, org).format('MM/DD/YYYY'),
			endDateTxt: ssDateHlp.isForeverFuture(jobtypeDetail._toDateUtc) ? null : ssDateByOrgHlp.utcIsoToRelativeMoment(jobtypeDetail._toDateUtc,org).format('MM/DD/YYYY'),
			hasForeverPast: false
		};


		$scope.$watch('jobtypeDetail', function(nv, ov){
			if(nv === ov) { return; }
			evaluateModel(nv, { editMode: jobtypeDetail.editMode, expanded: jobtypeDetail.expanded });
		});


		$scope.$watch('vm.startDateTxt', (nv, ov) => {
			if(nv === ov) { return; }
			if((nv instanceof Date) && !vm.hasForeverPast){
				jobtypeDetail._fromDateUtc = ssDateByOrgHlp.startOfDayRelativeTo(moment(nv), org);;
			}
		});

		function evaluateModel(m, override = null){
			jobtypeDetail = $scope.jobtypeDetail = m;
			if(override !== null){
				Object.assign(jobtypeDetail, override);
			}
			if(m != null){
				vm.hasForeverPast = ssDateHlp.isForeverPast(m._fromDateUtc);
				vm.startDateTxt = vm.hasForeverPast ? ssDateByOrgHlp.utcIsoToRelativeMoment(org._createdUtc, org).format('MM/DD/YYYY') : ssDateByOrgHlp.utcIsoToRelativeMoment(m._fromDateUtc, org).format('MM/DD/YYYY');
				vm.endDateTxt = ssDateHlp.isForeverFuture(m._toDateUtc) ? null : ssDateByOrgHlp.utcIsoToRelativeMoment(m._toDateUtc, org).format('MM/DD/YYYY');
			}
		}




		// behavior that needs to be bound to the view
		const go = $scope.go = { //jshint ignore:line
			toggle() {
				jobtypeDetail.expanded = !jobtypeDetail.expanded;
			},
			delete() {
				ssModalSvc.showDestructive({
					title: `<h2>Are you sure you want to delete this Job type detail?</h2>`,
					primaryText: 'Delete',
					secondaryText: 'Cancel',
				}).then(() => {
					jobtypeDetail.isProcessing = true;
					ssAsync(function* () {
						try {
							let index = routeVm.jobType._jobTypeDetails.indexOf(jobtypeDetail);
							routeVm.jobType._jobTypeDetails.splice(index,1);
							yield routeVm.jobType._jobTypeDetails.$save();
							routeVm.fetchJobType(true, false);
							ssToastHlp.success(``, `Job Type Deleted`, false);
						} catch (e) {
							ssToastHlp.error(``,`Error while deleting Job Type detail`, false);
							$log.error(`Error while deleting Job Type detail`, e);
						} finally{
							jobtypeDetail.isProcessing = false;
						}
					})();
				});

			},
			edit(){
				jobtypeDetail.editMode = !jobtypeDetail.editMode;
				if(!jobtypeDetail.expanded) { jobtypeDetail.expanded = true; }
			},
			cancel(){
				if(jobtypeDetail.id <= 0) {
					jobtypeDetail = null;
				}else{
					jobtypeDetail.editMode = !jobtypeDetail.editMode;
					jobtypeDetail.expanded = jobtypeDetail.isCurrent;
					jobtypeDetail.$revert();
				}
				evaluateModel(jobtypeDetail);
				$scope.$emit('ss-validate-reset');
			}
		};
	}
}

// #-- END Component (cmp-job-type-detail) --# //
// #-----------------------------------------# //

angular.module('ec-settings-components').directive('cmpJobTypeDetail', () => new JobTypeDetail(props, 'job-type-detail') );
