import { Segment } from 'base-segment';
// #---------------------------------------------------------------------------# //
// #---- Segment (/general-config/departments/segment-departments-content) ----# //

class DepartmentsContent extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssAsync, ssDepartmentsApi, ssActiveUserSvc) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line
		const parentVm = $scope.parentVm

		parentVm.HRMSmessage = 'Departments are editable in the HRMS application, and are referred to as "Organizations"';

		// hookup segment logic
		const logic = $injector.get('segmentGeneralConfigDepartmentsDepartmentsContentLogic'); // jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = {
			config: {
				gridColumns: [{
					label: 'Department',
					required: true,
					sortBy: 'name',
					viewTemplate: `<span ng-bind="item._name"></span>`,
					editTemplate: `<super-input-text placeholder="Name" model="item._name" ss-validate="validation.required(item._name, 'Department name')" required="true"></super-input-text>`
				}, {
					label: 'External Id',
					required: true,
					viewTemplate: `<span ng-bind="item._externalId"></span>`,
					editTemplate: `<super-input-text placeholder="External ID" model="item._externalId" ss-validate="validation.externalId(item._externalId, 'External Id')" required="true"></super-input-text>`
				}],
				cacheKey: ''
			},
			permissions: {
				canView: $scope.$root.isPermitted.listHeaders.view || false,
				canCreate: $scope.$root.isPermitted.listHeaders.create && !parentVm.isReadonly || false,
				canEdit: $scope.$root.isPermitted.listHeaders.edit && !parentVm.isReadonly || false,
				canDelete: $scope.$root.isPermitted.listHeaders.delete && !parentVm.isReadonly || false
			}
		};

		ssAsync(function*() {
			try {
				const orgId = yield ssActiveUserSvc.getOrgId();
				vm.config.cacheKey = `${orgId}_departments`;
			} catch (e) {
				console && console.error && console.error('ERROR', e);
			}
		})();

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			getDepartments(filter) {
				return ssDepartmentsApi.query(filter);
			}
		};
	}
}

// #-- END Segment (/general-config/departments/segment-departments-content) --# //
// #---------------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentGeneralConfigDepartmentsDepartmentsContent', () => new DepartmentsContent('departments-content'));
