import { Component } from 'base-component';
// #------------------------------------------# //
// #---- Component (cmp-add-edit-subject) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	subject: '=',
	org: '=',
	onNewCancel: '&',
	onAdd: '&',
	onDelete: '&',
	onSave: '&',
	singleEditing: '=', // used to only allow editing one subject at a given time
};

class AddEditSubject extends Component {
	/*@ngInject*/
	controller($scope, ssAsync, ssCredentialsApi, ssToastHlp, ssValidationHlp, ssModalSvc, ssDateByOrgHlp, messagingSvc) {
		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope); // jshint ignore:line
		var org = routeVm.org;
		
		// view model for tracking state
		const vm = $scope.vm = {
			isNew: $scope.subject.id == null,
			updatedUtcTxt: $scope.subject._updatedUtc != null ? ssDateByOrgHlp.utcIsoToRelativeMoment($scope.subject._updatedUtc, org).format('MM/DD/YYYY') : null,
			isSaving: false,
			canAddEdit: !routeVm.isStateNational
		};

		$scope.validator = {
			name: ssValidationHlp.string({ minLength: 1, maxLength: 100, required: true }, [
				(val, field) => {
					let ret = true;
					','.split('').forEach(c => {
						if (ret !== true) { return; }
						if (val.indexOf(c) >= 0) { ret = `${field} cannot contain the '${c}' character`; }
					});
					return ret;
				},
				(val, field) => {
					if (val.trim().length === 0) { return `${field} is required`; }
					return true;
				}
			])
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			edit() {
				ssModalSvc.showDestructive({
					title: messagingSvc.getPlaceholderMessage('credentials', 'editConfirmationTitle', ['Subject']),
					content: messagingSvc.getPlaceholderMessage('credentials', 'editConfirmationMessage', ['subject']),
					primaryText: messagingSvc.getMessage('general', 'continueEditing'),
					secondaryText: messagingSvc.getMessage('general', 'cancel'),
				}).then(() => {
					$scope.singleEditing.value = true;
					$scope.subject.isEditing = true;
				});
			},
			cancel() {
				if (vm.isNew) {
					$scope.onNewCancel();
				} else {
					$scope.subject.$revert();
				}
				$scope.singleEditing.value = false;
				$scope.subject.isEditing = false;
			},
			remove() {
				ssModalSvc.showDestructive({
					title: messagingSvc.getPlaceholderMessage('credentials', 'deleteConfirmationTitle', ['Subject']),
					content: messagingSvc.getPlaceholderMessage('credentials', 'deleteConfirmationMessage', ['subject']),
					primaryText: messagingSvc.getMessage('general', 'delete'),
					secondaryText: messagingSvc.getMessage('general', 'cancel'),
				}).then(() => {
					if (vm.isSaving) { return; }
					vm.isSaving = true;
					ssAsync(function*() {
						try {
							let res = yield $scope.subject.$remove();
							if (res != null) {
								ssToastHlp.success('', `Subject "${$scope.subject._name}" Removed`, false);
								$scope.onDelete({ $subject: $scope.subject });
							}
							vm.isSaving = false;
						} catch (error) {
							ssToastHlp.error(error.message, 'Error', false);
							vm.isSaving = false;
						}
					})();
				});
			},
			save() {
				if (vm.isSaving) { return; }
				vm.isSaving = true;
				ssAsync(function*() {
					$scope.subject._name = $scope.subject._name.trim();
					let res;
					if (vm.isNew) {
						res = yield ssCredentialsApi.addSubject({}, $scope.subject);
					} else {
						res = yield $scope.subject.$save();
					}

					if (res != null) {
						if (vm.isNew) {
							res.isEditing = false;
							// this calls the parent component to add the new subject to the list
							// and null the newSubject ref to remove this component
							$scope.onAdd({ $newSubject: res });
						} else {
							vm.updatedUtcTxt = ssDateByOrgHlp.utcIsoToRelativeMoment($scope.subject._updatedUtc, org).format('MM/DD/YYYY');
							$scope.subject.isEditing = false;
						}
						if ($scope.onSave) { $scope.onSave(); }
						ssToastHlp.success('', `Subject "${$scope.subject._name}" Saved`, false);
					}
					vm.isSaving = false;
				})();
			}
		};
	}
}

// #-- END Component (cmp-add-edit-subject) --# //
// #------------------------------------------# //

angular.module('ec-settings-components').directive('cmpAddEditSubject', () => new AddEditSubject(props, 'add-edit-subject'));
