import { Segment } from 'base-segment';
// #--------------------------------------------------# //
// #---- Segment (/user-accounts/segment-content) ----# //

class Content extends Segment {

	/*@ngInject*/
	controller($injector, $sce, $scope, $filter, $window, $log, rAF, ssAsync, ssEmployeeApi, ssOrganizationApi, ssActiveUserSvc, ssToastHlp, ssValidationHlp, enumFlInviteStatus, ssStringHlp, ssModalSvc) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentUserAccountsContentLogic');// jshint ignore:line

		let validation = $scope.validation = {
			required: ssValidationHlp.required(),
			email: ssValidationHlp.email({ required: true })
		};

		const batchSize = 50;

		// view model for tracking state
		const vm = $scope.vm = {
			selectedAll: false,
			inviteStatus: enumFlInviteStatus,
			initalLimit: batchSize,
			emps: [],
			selectedEmps: [],
			filterSelected: false,
			loading: false,
			loadingStatus: '',
			search: '',
			loadingCredentials: true,
			allSelected: []
		};


		const pageSize = 10000;

		var filtering = false;

		$scope.filtered = [];

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			selectAll() {
				let dataHolder = [];
				vm.emps.filter(x => x.isVisible).forEach(x => x.$selected = vm.selectedAll);
				if (vm.selectedAll) {
					dataHolder = vm.selectedEmps.concat(vm.emps.filter(x => x.isVisible));
				} else {
					dataHolder = vm.selectedEmps.filter(function (selRow) {
						return vm.emps.filter(function (x) { return x.isVisible && x.id === selRow.id; }).length === 0;
					});
				}
				vm.selectedEmps = dataHolder;
			},
			selectOne(emp) {
				if (emp.$selected) {
					vm.selectedEmps.push(emp);
				} else {
					vm.selectedEmps = vm.selectedEmps.filter(function (v) { return v !== emp; });
				}
				vm.selectedAll = vm.emps.every(x => x.$selected === true)
			},
			loadNext() {
				if (vm.initalLimit <= vm.emps.length) {
					vm.initalLimit = vm.initalLimit + batchSize;
				}
			},
			editModeOn(emp, domId) {
				emp.editMode = true;
				rAF(function () {
					const superInputText = document.getElementById(domId);
					const inputEl = superInputText.getElementsByTagName('input');
					if (inputEl) { inputEl[0].focus(); }
				}, $scope);
			},

			filterResults(category) {

				go.clearAll();

				if (category && category !== 'all') {
					category = ssStringHlp.toCamelCase(category);
					vm.categoryVal = enumFlInviteStatus[category];
					vm.selectedFilter = category;
				} else {
					vm.categoryVal = '';
					vm.selectedFilter = 'all';
				}

				filterAndSort();

				vm.categoryTotal = $scope.filtered.length;
			},

			invite(emps, isBulk) {

				if (emps == null) { return; }

				vm.loading = true;
				vm.loadingStatus = 'Sending Invitations...';

				if (!angular.isArray(emps)) {
					emps = [emps];
				}

				let invitedEmps = emps.map((e) => {
					return {
						employeeId: e.id,
						email: e._flUser.invitationEmailAddress,
						returnUrl: $window.location.origin,
						firstName: e._firstName,
					};
				});

				ssAsync(function* () {
					try {
						yield ssEmployeeApi.invite(invitedEmps);

						let employees = [];

						vm.emps.forEach(e => {
							if (emps.find(iEmps => iEmps.id === e.id)) {
								e._flUser.invitationSentUtc = new Date();
								employees.push(e);
							}
						});

						yield employees.map(emp => emp.$save());

						vm.loading = false;
						yield refreshResults();

						if (isBulk) {
							ssModalSvc.showStandard({
								emps: emps,
								content: `<h2>You successfully sent <ng-pluralize count="emps.length" when="{'1': 'an invitation','other': 'invitations'}"></ng-pluralize> to <br /><ng-pluralize count="emps.length" when="{'1': '${emps[0].fullName}','other': '{} people'}"></ng-pluralize></span></h2>`,
								primaryText: 'Continue',
								showSecondary: false
							});
						} else {
							ssToastHlp.success(`An invitation has been successfully sent to ${emps[0].fullName}`, `Invitation Sent`, false);
						}

					} catch (err) {
						vm.loading = false;
						ssToastHlp.error('There was a problem performing this action. Please try again later.', 'Error', false);
					}
				})();
			},
			revoke(emps) {
				if (emps == null) { return; }

				vm.loading = true;
				vm.loadingStatus = 'Revoking Access...';

				if (!angular.isArray(emps)) {
					emps = [emps];
				}

				ssAsync(function* () {

					try {
						yield emps.map(emp => ssEmployeeApi.revoke(emp.id));

						emps.forEach(e => {
							e._flUser.invitationSentUtc = null;
							e.flStatus = vm.inviteStatus.noAccess;
						});

						yield emps.map(e => e.$save());

						vm.loading = false;
						yield refreshResults();

						ssToastHlp.success(`Access has been revoked for ${emps[0].fullName}`, 'Acccess Revoked', false);

					} catch (e) {
						if (e.status !== 404) { } // jshint ignore:line
						vm.loading = false;
						ssToastHlp.error('There was a problem performing this action. Please try again later.', 'Error', false);
					}

				})();
			},
			deactivate(emps, isBulk) {

				if (emps == null) { return; }

				vm.loading = true;
				vm.loadingStatus = 'Pausing Access...';

				if (!angular.isArray(emps)) {
					emps = [emps];
				}

				let ids = emps.map(e => e.id);

				ssAsync(function* () {
					try {
						yield ssEmployeeApi.deactivate(ids);

						vm.loading = false;
						yield refreshResults();

						if (isBulk) {
							ssModalSvc.showStandard({
								emps: emps,
								content: `<h2>You successfully paused access for <br /><ng-pluralize count="emps.length" when="{'1': '${emps[0].fullName}','other': '{} people'}"></ng-pluralize></h2>`,
								primaryText: 'Continue',
								showSecondary: false
							});
						} else {
							ssToastHlp.success(`Access has been paused for ${emps[0].fullName}`, 'Access Paused', false);
						}

					} catch (e) {
						vm.loading = false;
						ssToastHlp.error('There was a problem performing this action. Please try again later.', 'Error', false);
					}
				})();
			},

			activate(emps, isBulk) {

				if (emps == null) { return; }

				vm.loading = true;
				vm.loadingStatus = 'Resuming Access...';

				if (!angular.isArray(emps)) {
					emps = [emps];
				}

				let ids = emps.map(e => e.id);

				ssAsync(function* () {
					try {
						yield ssEmployeeApi.activate(ids);

						vm.loading = false;
						yield refreshResults();

						if (isBulk) {
							ssModalSvc.showStandard({
								emps: emps,
								content: `<h2>You successfully resumed access for <br /><ng-pluralize count="emps.length" when="{'1': '${emps[0].fullName}','other': '{} people'}"></ng-pluralize></h2>`,
								primaryText: 'Continue',
								showSecondary: false
							});
						} else {
							ssToastHlp.success(`Access has been resumed for ${emps[0].fullName}`, 'Access resumed', false);
						}

					} catch (e) {
						vm.loading = false;
						ssToastHlp.error('There was a problem performing this action. Please try again later.', 'Error', false);
					}
				})();
			},

			saveEmail(emp) {
				emp.saving = true;
				emp.$save().then(res => {
					emp.editMode = false;
					rAF(function () {
						emp.saving = false;
					}, $scope);
				});
			},

			cancelEdit(emp) {
				emp.saving = true;
				emp.$revert();

				if (emp._flUser.invitationEmailAddress == null) {
					emp._flUser.invitationEmailAddress = emp.bestContactEmail;
				}

				rAF(function () {
					emp.saving = false;
				}, $scope);

				emp.editMode = false;
			},

			sendMultiple() {
				let selected = vm.selectedEmps.filter(e =>
					e._flUser.invitationEmailAddress &&
					validation.email(e._flUser.invitationEmailAddress) === true &&
					(e.flStatus === vm.inviteStatus.sent || e.flStatus === vm.inviteStatus.noAccess));

				let modalConfig = {
					selected: selected,
					content: '',
					primaryText: '',
					secondaryText: 'Cancel'
				};

				// Determine the modal messaging
				if (selected.length === vm.selectedEmps.length) {
					modalConfig.content = `<h2>You are sending <ng-pluralize count="selected.length" when="{'1': 'an invitation','other': 'invitations'}"></ng-pluralize> to <br />${selected.length} <ng-pluralize count="selected.length" when="{'1': 'person.','other': 'people.'}"></ng-pluralize></h2>`;
				} else {
					modalConfig.content = `<h2>You <ng-pluralize count="selected.length" when="{'0': 'can not','1': 'can only', 'other': 'can only'}"></ng-pluralize> send <ng-pluralize count="selected.length" when="{'one': 'an invitation','other': 'invitations'}"></ng-pluralize> to <br /><ng-pluralize count="selected.length" when="{'0': 'any of the people selected.', 'one': '{} of the ${vm.selectedEmps.length} people selected.', 'other': '{} of the ${vm.selectedEmps.length} people selected.'}"></ng-pluralize>
					</h2>`;
				}

				// Determine what buttons are shown and their label text
				if (selected.length < 1) {
					modalConfig.showPrimary = false;
				} else if (selected.length === 1) {
					modalConfig.primaryText = `Send an Invitation to 1 person`;
				} else {
					modalConfig.primaryText = `Send Invitations to ${selected.length} people`;
				}

				ssModalSvc.showStandard(modalConfig).then(() => {
					go.invite(selected, true);
				});
			},

			activateMultiple() {
				let selected = vm.selectedEmps.filter(e => e.flStatus === vm.inviteStatus.inactive);

				let modalConfig = {
					selected: selected,
					content: '',
					primaryText: '',
					secondaryText: 'Cancel'
				};

				// Determine the modal messaging
				if (selected.length === vm.selectedEmps.length) {
					modalConfig.content = `<h2>You are resuming access for ${selected.length} <ng-pluralize count="selected.length" when="{'1': 'person.','other': 'people.'}"></ng-pluralize></h2>`;
				} else {
					modalConfig.content = `<h2>You <ng-pluralize count="selected.length" when="{'0': 'can not','1': 'can only', 'other': 'can only'}"></ng-pluralize> resume access for <br /><ng-pluralize count="selected.length" when="{'0': 'any of the people selected.', 'one': '{} of the ${vm.selectedEmps.length} people selected.', 'other': '{} of the ${vm.selectedEmps.length} people selected.'}"></ng-pluralize>
					</h2>`;
				}

				// Determine what buttons are shown and their label text
				if (selected.length < 1) {
					modalConfig.showPrimary = false;
				} else if (selected.length === 1) {
					modalConfig.primaryText = `Resume Access for 1 person`;
				} else {
					modalConfig.primaryText = `Resume Access for ${selected.length} people`;
				}

				ssModalSvc.showStandard(modalConfig).then(() => {
					go.activate(selected, true);
				});
			},

			deactivateMultiple() {
				let selected = vm.selectedEmps.filter(e => e.flStatus === vm.inviteStatus.active);

				let modalConfig = {
					selected: selected,
					content: '',
					primaryText: '',
					secondaryText: 'Cancel'
				};

				// Determine the modal messaging
				if (selected.length === vm.selectedEmps.length) {
					modalConfig.content = `<h2>You are pausing access for <ng-pluralize count="selected.length" when="{'1': '{} person','other': '{} people'}"></ng-pluralize></h2>`;
				} else {
					modalConfig.content = `<h2>You <ng-pluralize count="selected.length" when="{'0': 'can not','1': 'can only', 'other': 'can only'}"></ng-pluralize> pause access for <br /><ng-pluralize count="selected.length" when="{'0': 'any of the people selected.', 'one': '{} of the ${vm.selectedEmps.length} people selected.', 'other': '{} of the ${vm.selectedEmps.length} people selected.'}"></ng-pluralize>
					</h2>`;
				}

				// Determine what buttons are shown and their label text
				if (selected.length < 1) {
					modalConfig.showPrimary = false;
				} else if (selected.length === 1) {
					modalConfig.primaryText = `Pause Access for 1 person`;
				} else {
					modalConfig.primaryText = `Pause Access for ${selected.length} people`;
				}

				ssModalSvc.showStandard(modalConfig).then(() => {
					go.deactivate(selected, true);
				});
			},

			showCredentials(emp) {
				vm.loadingCredentials = true;
				vm.credentialHtml = '';
				ssEmployeeApi.getUserAccountCredentials({
					flId: emp.flId
				}).then((creds) => {
					let html = `
						<dl class="meta-list">
							<dt class="meta-list-term">Username:</dt>
								<dd class="meta-list-desc">${creds._userName}
							</dd>
							<dt class="meta-list-term">Email:</dt>
								<dd class="meta-list-desc">${creds._email}
							</dd>
						</dl>
						`
					vm.credentialHtml = $sce.trustAsHtml(html);
				}).catch((e) => {
					console && console.error && console.error(e, 'Error fetching credentials');
				}).finally(() => { vm.loadingCredentials = false; });
			},
			showInvitationStatus(emp) {
				vm.invitationHtml = $sce.trustAsHtml(`<h5>Invitation status:</h5>
					<p>
					sent
					<span ng-if="emp._flUser.invitationEmailAddress">
						to ${emp._flUser.invitationEmailAddress}
					</span>
					<span class="sent-date">
						on ${$filter('date')(emp._flUser.invitationSentUtc, 'MM/dd/yy')}
					</span>
					</p>
				`);
			},
			clearAll() {
				vm.initalLimit = batchSize;
				vm.selectedEmps = [];
				vm.allSelected = [];
				vm.search = '';
				vm.filterSelected = false;
				vm.emps.forEach(x => x.$selected = false);
				vm.selectedAll = false;
			}
		};

		refreshResults();

		$scope.$watch('vm.filterSelected', (nv) => {
			if (nv) {
				vm.search = '';
			}
			filterAndSort();
			vm.selectedAll = vm.emps.filter(x => x.isVisible).every(x => x.$selected === true);
		});

		$scope.$watch('vm.search', (nv) => {
			if (nv) {
				vm.filterSelected = false;
			}
			filterAndSort();
			vm.selectedAll = vm.emps.filter(x => x.isVisible).every(x => x.$selected === true);
		});

		$scope.$watchCollection('vm.selectedEmps', (nv, ov) => {

			if (filtering) { return; }

			const removedRows = ov.filter(oldRow => !nv.includes(oldRow));
			const addedRows = nv.filter(newRow => !ov.includes(newRow));

			addedRows.forEach((row) => {
				if (!vm.allSelected.includes(row)) {
					vm.allSelected.push(row);
				}
			});

			vm.allSelected = vm.allSelected.filter((row) => {
				return !removedRows.includes(row);
			});

			if (vm.allSelected.length === 0) {
				vm.filterSelected = false;
			}
		});

		$scope.$watchCollection('filtered', (nv) => {
			nv.forEach((visibleRow) => {
				if (vm.allSelected.includes(visibleRow) && !vm.selectedEmps.includes(visibleRow)) {
					vm.selectedEmps.push(visibleRow);
				}
			});
		});


		function refreshResults() {
			if (vm.loading) { return; }

			let currentPage = 0;

			// set to prevent multiple calls from firing at the same time on scroll
			vm.loading = true;

			return (ssAsync(function* () {
				try {
					var orgId = yield ssActiveUserSvc.getOrgId();
					let employeeList = [];
					vm.selectedEmps = [];
					vm.search = '';
					currentPage++;
					let activeCount = 0;
					let inactiveCount = 0;
					let sentCount = 0;
					let noAccessCount = 0;
					var filter = Object.assign({}, {
						fields: 'firstname,lastname,fluser,ownerid,ownerversionnumber',
						include: 'emails',
						offset: (currentPage - 1) * pageSize,
						limit: pageSize
					});

					//	Get the first set of employees
					vm.loadingStatus = 'Loading users. This may take a while.';
					employeeList = yield ssEmployeeApi.getEmployeeAccess(filter);
					vm.totalEmployees = vm.categoryTotal = parseInt(employeeList.$headers['total-results'], 10);

					//	Get the number of total pages, and since we've already loaded the first page,
					//	subtract it from the total
					let pages = getPages(vm.totalEmployees, pageSize) - 1;

					// If there is more than 1 page, get the rest
					for (var i = 0; i < pages; i++) {
						currentPage++;
						filter.offset = (currentPage - 1) * pageSize;
						//vm.loadingStatus = Math.round((filter.offset / vm.totalEmployees) * 100) + '%';
						//	employeeList no longer a json-api collection
						employeeList = [...employeeList, ...(yield ssEmployeeApi.getEmployeeAccess(filter))];
					}

					//	Get all FL users and match them with EC users to determine their product access status
					let flUsers = (yield ssEmployeeApi.productAccessStatus()).data
					let userDictionary = flUsers.reduce(
						function (prev, curr) {
							var user = curr.identities.find(
								function (a) {
									return a.type === 'employee'
								});

							prev[user.id] = {
								flId: curr.flId,
								enabled: curr.enabled,
								productAccess: curr
							};

							return prev;
					}, {});

					employeeList.forEach(emp => {
						// Clear the selected flag that the grid uses to determined selected-ness
						emp.$selected = false;

						// EC App access uses the best contact email if the FL record doesn't specify the address
						if (emp._flUser.invitationEmailAddress == null) {
							emp._flUser.invitationEmailAddress = emp.bestContactEmail;
						}

						// Pre-format the name since we're in here.
						emp.displayName = `${emp._lastName}, ${emp._firstName}`;

						// Reset the record's edit status
						emp.editMode = false;
						emp.isVisible = true;

						// Set the employee's FLID and their enabled-ness
						let flUser = userDictionary[emp.id];
						emp.flId = null;
						emp.enabled = false;
						if (flUser) {
							emp.productAccessUser = flUser;
							emp.flId = flUser.flId;
							emp.enabled = flUser.enabled;
						}

						// Set the user's status according to product access info
						emp.flStatus = setFlStatus(emp);

						if (emp.flStatus === vm.inviteStatus.active) { activeCount++; }
						if (emp.flStatus === vm.inviteStatus.inactive) { inactiveCount++; }
						if (emp.flStatus === vm.inviteStatus.sent) { sentCount++; }
						if (emp.flStatus === vm.inviteStatus.noAccess) { noAccessCount++; }
					});

					vm.emps = employeeList;

					go.filterResults();

					vm.activeCount = activeCount;
					vm.inactiveCount = inactiveCount;
					vm.sentCount = sentCount;
					vm.noAccessCount = noAccessCount;
					vm.totalCount = vm.activeCount + vm.inactiveCount + vm.sentCount + vm.noAccessCount;

				} catch (e) {
					ssToastHlp.error('Error retrieving employees. Please try again later.');
					$log.error(e)
				} finally {
					rAF(() => vm.loading = false, $scope);
				}
			})());
		}

		function filterAndSort() {
			filtering = true;

			if (vm.filterSelected) {
				$scope.filtered = vm.allSelected;
			} else {
				$scope.filtered = $filter('filter')(vm.emps, {
					flStatus: vm.categoryVal,
					fullName: vm.search
				});
			}
			vm.emps.forEach((emp) => {
				emp.isVisible = $scope.filtered.includes(emp);
			});

			rAF.raw(() => filtering = false);
		}

		/**
		 * Sets the product access status (for display only) of an EC User based on their FL user status
		 *
		 * @param {any} emp - a Frontline Central employee record with attached Product Access record attached
		 * @returns enumFlInviteStatus value
		 */
		function setFlStatus(emp) {
			return logic.setFLUserStatus(emp, enumFlInviteStatus);
		}


		function getPages(count, size) {
			var remainder = count % size;
			var div = (count - remainder) / size;
			return div + (remainder > 0 ? 1 : 0);
		}
	}
}

// #-- END Segment (/user-accounts/segment-content) --# //
// #--------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentUserAccountsContentOld', () => new Content('content'));
