import {State} from 'base-state';
// #----------------------------------------------------------# //
// #---- State (/general-config/job-types/job-type-detail) ----# //


// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const stateName = 'generalConfig.jobTypes.jobTypeDetail';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/{jobTypeId:[0-9A-Za-z-]{36}}?action',
	templateUrl: 'routes/general-config/job-types/job-type-detail/job-type-detail.html',
	resolve: {
		$title: /*@ngInject*/ () => {
			return 'Frontline Central - Job Type Detail';
		},
		editDetails: ['$stateParams', function($stateParams){
			return $stateParams.edit;
		}]

	}
};


// #-- END State (/general-config/job-types/job-type-detail) --# //
// #----------------------------------------------------------# //

angular.module('ec-settings').config(/*@ngInject*/ ($stateProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
