/*@ngInject*/
function propertyActionLabelSvc() {
// #------------------------------------------# //
// #---- Service (propertyActionLabelSvc) ----# //

	// External service API
	const numberToValueDictionary = {
		0: 'Not Visible',
		1: 'Can View',
		3: 'Can Edit',
		4: 'Visible but redacted (numbers as ****)',
		5: 'Visible, redacted w/ option to view',
		7: 'Redacted but editable'
	};

	function getNumberToValueDictionary(){
		return Object.assign({}, numberToValueDictionary);
	}

	function isAtLeastViewable(levelNumber){
		// I know that this code basically uses  magic-numbers; However, there are unit tests that ensure that the code doesn't regress
		//     ... so please do not xdescribe them! :)
		if(levelNumber >= 1){
			return true;
		}
		return false;
	}
	
	function isAtLeastEditable(levelNumber){
		// I know that this code basically uses  magic-numbers; However, there are unit tests that ensure that the code doesn't regress
		//     ... so please do not xdescribe them! :)
		if(levelNumber === 3 || levelNumber === 7){
			return true;
		}
		return false;
	}
	
	return {
		getNumberToValueDictionary,
		isAtLeastViewable,
		isAtLeastEditable,
	}

// #-- END Service (propertyActionLabelSvc) --# //
// #------------------------------------------# //
}

angular.module('ec-settings-services').factory('propertyActionLabelSvc', propertyActionLabelSvc);
