import { Segment } from 'base-segment';
// #--------------------------------------------------------------------------------# //
// #---- Segment (/permissions/profiles/profile-detail/tab/segment-tab-content) ----# //

class TabContent extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $state, $stateParams, ssAsync, createPermissionsHlp, ssObjectHlp, propertyActionLabelSvc, ssStringHlp, ssModalSvc) {
		const routeVm = $scope.routeVm; // eslint-disable-line

		// hookup segment logic
		const logic = $injector.get('segmentPermissionsProfilesProfileDetailTabTabContentLogic'); // eslint-disable-line

		const profilesVm = $scope.profilesVm;

		let profile = $scope.profile;

		//	Get the context object (Department, Employee etc.) based on the value of the 'tab' state param
		const context = $scope.permissionsVm.contexts.find(c => c._protectedResourceRegistration.resourceName === $stateParams.tab);

		const vm = $scope.vm = {
			editMode: $stateParams.action === 'edit',
			context,
			registration: context._protectedResourceRegistration,
			selectedProfile: null,
		};

		if (!angular.isArray(profile._permissionSets)) {
			profile._permissionSets = [];
		}
		let permissionSet = profile._permissionSets.find(s => s._protectedContextRegistrationId === context.id);
		if (permissionSet == null) {
			permissionSet = createPermissionsHlp.permissionSet();
			permissionSet._protectedContextRegistrationId = context.id;
			permissionSet._permissions.resourceName = vm.registration.resourceName;
			profile._permissionSets.push(permissionSet);
		}

		vm.permissions = [permissionSet._permissions];

		$scope.$watch('vm.selectedProfile', (nv, ov) => {
			if (nv === ov) { return; }

			let p = profilesVm.profiles.find(p => p.id === nv);

			let chosenSet = p._permissionSets.find(s => s._protectedContextRegistrationId === context.id);
			if (chosenSet == null) {
				chosenSet = createPermissionsHlp.permissionSet();
				chosenSet._protectedContextRegistrationId = context.id;
				chosenSet._permissions.resourceName = vm.registration.resourceName;
				p._permissionSets.push(chosenSet);
			}
			vm.permissions = [permissionSet._permissions, chosenSet._permissions];
		});

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			edit() {
				$state.go('.', {
					action: 'edit'
				});
			},
			cancelTabEdit() {
				profile.$revert();
				$state.go('.', {
					action: 'view'
				});
			},
			validateAndSaveTab() {
				const permissionSetRefToReplace = profile._permissionSets.find(s => s._protectedContextRegistrationId === context.id);
				const { actionObjectsThatRequireChecking, theFirstProtextPropertyToRequireHigher } = findMissingPermissionLevels(permissionSetRefToReplace);
				if (actionObjectsThatRequireChecking && actionObjectsThatRequireChecking.length && theFirstProtextPropertyToRequireHigher) {
					const contentStrForModal = `Due to selecting attributes such as ${ssStringHlp.toTitleCase(theFirstProtextPropertyToRequireHigher.description) || 'a protected field'}` +
						', you are required to also select: </br>' +
						actionObjectsThatRequireChecking.map(anAction => {
							return `"Can ${ssStringHlp.toTitleCase(anAction.description)}"</br>`;
						}).join('') +
						'Would you like to accept those additional permissions?';

					promptForModification(contentStrForModal, () => {
						const indexToReplace = profile._permissionSets.indexOf(permissionSetRefToReplace);
						const objectActionStrsToSet = [];
						Object.keys(permissionSetRefToReplace._permissions).forEach(aPermKey => {
							if (actionObjectsThatRequireChecking.find(aRequiredAction => aRequiredAction.id === aPermKey)) {
								objectActionStrsToSet.push(aPermKey);
								permissionSetRefToReplace._permissions[aPermKey] = true;
							}
						});
						permissionSetRefToReplace._permissions.objectActions = permissionSetRefToReplace._permissions.objectActions.concat(objectActionStrsToSet);
						profile._permissionSets[indexToReplace] = ssObjectHlp.deepCopy(permissionSetRefToReplace);
						saveTab();
					});
				} else {
					saveTab();
				}
			}
		};

		function promptForModification(modalBodyContentStr, doneCallback) {
			let modalConfig = {
				content: modalBodyContentStr,
				primaryText: 'Accept',
				secondaryText: 'Cancel'
			};

			return ssModalSvc.showStandard(modalConfig).then(doneCallback);
		}

		function findMissingPermissionLevels(thePermProfile) {
			const EDIT_ACTION = 'Edit';
			const VIEW_ACTION = 'View';
			var resourceActions = {
				editActionName: null,
				viewActionName: null,
			};

			const protectedProperties = [];
			Object.keys(thePermProfile._permissions).forEach(aPermKey => {
				const resourceActionNameTriple = aPermKey.split('_');
				// Confirm that it is a simple resource action and not some nested resource
				if (resourceActionNameTriple.length === 3) {
					const [resource, permissionType, actionLevel] = resourceActionNameTriple;
					// Find the action that corresponds to the primary DB resource for this tab
					if (permissionType === 'Action' && actionLevel === EDIT_ACTION) {
						resourceActions.editActionName = aPermKey;
					}
					if (permissionType === 'Action' && actionLevel === VIEW_ACTION) {
						resourceActions.viewActionName = aPermKey;
					}

					if (permissionType === 'Property') {
						protectedProperties.push(thePermProfile._permissions[aPermKey]);
					}
				}
			});

			function theySelectedThisAction(anActionString) {
				return thePermProfile._permissions.objectActions.includes(anActionString);
			}

			const propertiesThatRequireEdit = [];
			const propertiesThatRequireView = [];
			protectedProperties.forEach(aProtectedPropertyObj => {
				// Did they forget to click edit or view for the main resource?
				if (propertyActionLabelSvc.isAtLeastEditable(aProtectedPropertyObj.value) && !theySelectedThisAction(resourceActions.editActionName)) {
					propertiesThatRequireEdit.push(aProtectedPropertyObj);
				}
				if (propertyActionLabelSvc.isAtLeastViewable(aProtectedPropertyObj.value) && !theySelectedThisAction(resourceActions.viewActionName)) {
					propertiesThatRequireView.push(aProtectedPropertyObj);
				}
			})

			let actionObjectsThatRequireChecking = [];
			let theFirstProtextPropertyToRequireHigher = '';
			if (propertiesThatRequireEdit.length > 0) {
				actionObjectsThatRequireChecking.push(vm.registration.actions.find(anAction => anAction.actionName === EDIT_ACTION));

			}
			if (propertiesThatRequireView.length > 0) {
				actionObjectsThatRequireChecking.push(vm.registration.actions.find(anAction => anAction.actionName === VIEW_ACTION));
			}

			const propertiesThatRequireHigherLevel = [].concat(propertiesThatRequireEdit, propertiesThatRequireView);
			if (propertiesThatRequireHigherLevel.length > 0) {
				theFirstProtextPropertyToRequireHigher = vm.registration.properties.find(aProperty => aProperty.id === propertiesThatRequireHigherLevel[0].key);
			}

			return {
				theFirstProtextPropertyToRequireHigher,
				actionObjectsThatRequireChecking,
			};
		}

		function saveTab() {
			ssAsync(function*() {
				vm.saving = true;
				try {
					yield profile.$save();
					$state.go('.', {
						action: 'view'
					});
				} catch (err) {
					console && console.error && console.error('problem saving permission profile', err);
				} finally {
					vm.saving = false;
				}
			})();
		}
	}
}

// #-- END Segment (/permissions/profiles/profile-detail/tab/segment-tab-content) --# //
// #--------------------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentPermissionsProfilesProfileDetailTabTabContent', () =>
	new TabContent('tab-content'));
