import {Segment} from 'base-segment';
// #---------------------------------------------------------------------# //
// #---- Segment (/permissions/profiles/profile-all/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $state, ssAsync, ssPermissionProfilesApi, ssToastHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // eslint-disable-line

		// hookup segment logic
		const logic = $injector.get('segmentPermissionsProfilesProfileAllContentLogic');// eslint-disable-line

		const profilesVm = $scope.profilesVm;

		var addProfile;

		const vm = $scope.vm = {
			formOpened: false,
			errors: '',
			profile: {
				_permissionProfileName: '',
				_permissionProfileDescription: '',
				_permissionSets: []
			},
			saving: false
		};

		const go = $scope.go = { // eslint-disable-line
			add() {
				if (addProfile == null) {
					vm.saving = true;
					addProfile = ssAsync(function*() {
						try {
							let profile = yield ssPermissionProfilesApi.add({
								'include': 'permissionSets'
							}, vm.profile, false);

							if (profile.id != null) {
								profilesVm.profiles.push(profile);

								$state.go('base.permissions.profiles.detail', {
									profileId: profile.id
								});
							} else {
								addProfile = null;
								ssToastHlp.error('There was an error creating the permission profile. Please try again later.');
							}
						} catch (err) {
							ssToastHlp.error('There was an error creating the permission profile. Please try again later.');
							addProfile = null;
						} finally {
							vm.saving = false;
						}
					})();
				}
			},

			close() {
				vm.formOpened = false;
				vm.errors = '';
				vm.profile = {
					_permissionProfileName: '',
					_permissionProfileDescription: '',
					_permissionSets: []
				};
				$scope.$emit('ss-menu-close');
			}
		};

		$scope.$on('ss-menu-closed', (e, elem, ddElem) => {
			if (!vm.formOpened) {return;}
			if (ddElem.hasClass('create-form')) {
				vm.formOpened = false;
				$scope.$root.safeDigest($scope);
			}
		});
	}
}

// #-- END Segment (/permissions/profiles/profile-all/segment-content) --# //
// #---------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentPermissionsProfilesProfileAllContent', () => new Content('content'));
