import {Segment} from 'base-segment';
// #------------------------------------------------------------# //
// #---- Segment (/permissions/users/index/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $q, $filter, ssAsync, ssEmployeeApi, ssDebounce, ssToastHlp) {
		const routeVm = $scope.routeVm;
		// hookup segment logic
		const logic = $injector.get('segmentPermissionsUsersIndexContentLogic');// jshint ignore:line
		const pageSize = 10000;
		const batchSize = 20;
		let currentPage = 0;
		var searchString = '';
		var emps, groups, profiles, getUsersPromise;

		const vm = $scope.vm = {
			selectedAll: false,
			limit: batchSize,
			loading: false,
			showInfScroll: false,
			employeesRetrieved: [],
			employeesDisplayed: [],
			selectedEmps: [],
			search: '',
			loadingStatus: 'Loading users... This may take a while.'
		};

		const go = $scope.go = { // jshint ignore:line
			selectAll(){
				if(vm.selectedAll){
					vm.employeesDisplayed.forEach(x => x.$selected = true)
				}else{
					vm.employeesDisplayed.forEach(x => x.$selected = false)
				}
				vm.selectedEmps = vm.employeesDisplayed.filter(x => x.$selected);
			},
			selectOne(emp){
				if(emp.$selected){
					vm.selectedEmps.push(emp);
				}else{
					vm.selectedEmps = vm.selectedEmps.filter(function (v) { return v !== emp; });
				}
				vm.selectedAll = vm.employeesDisplayed.every(x => x.$selected === true)
			},
			addUsersToGroup(groupId) {
				let updatedGroup = logic.addUsersToGroup(vm.selectedEmps, groups, groupId);
				updatedGroup.$save().then((group) => {
					ssToastHlp.success(`${group._groupName} has been updated`, 'Add To Group', false);
					//reset Add To Group dropdown
					vm.toGroup = null;
					vm.selectedEmps = [];
				});
			},

			getUserGroups(emp) {
				return logic.getUserGroups(emp, groups);
			},
			loadNext(){
				if(vm.limit <= vm.employeesDisplayed.length){
					vm.limit = vm.limit + batchSize;
				}
			}
		};

	//	debounce method for filtering
		const debounceResults = ssDebounce(() => {
			currentPage = 0;
			getUsers(true);
		}, 350);

	//	watch the filter menus and filter
		$scope.$watch('vm.selectedGroupFilter', (nv, ov) => {
			if (nv === ov) {return;}
			vm.employeesDisplayed = logic.filterUsersByGroup(vm.employeesRetrieved, groups, nv);
		});

	//	watch the 'add to group' menu
		$scope.$watch('vm.toGroup', (nv, ov) => {
			if (nv != null) {
				$scope.go.addUsersToGroup(nv);
			}
		});

	//	watch the search field, trim any value, reset the data & get the results
		$scope.$watch('vm.search', (nv, ov) => {
			if (nv === ov) { return; }
			if (typeof nv === 'string') {
				searchString = nv.trim();
			}
			debounceResults();
		});

	//	method for scrolling
		// $scope.loadNext = function() {
		// 	if (vm.isScrolling) { return; }
		// 	vm.isScrolling = true;
		// 	getUsers();
		// };

	//	Get the Users
		function getUsers(reset) {

		//	abort if a call is already in progress
			if (getUsersPromise != null) {
				getUsersPromise.$abort();
			}

		//	show the initial loader only when first entering the page, otherwise we show the scrolling loader
			vm.loading = !vm.isScrolling;

			getUsersPromise = ssAsync(function*() {
				try {
				//	increment the current page
					currentPage++;
				//	set the endpoint config
					var config = Object.assign({}, {
						fields: 'firstname,lastname,flUser',
						include: 'emails',
						offset: (currentPage - 1) * pageSize,
						limit: pageSize
					},
						logic.makeSearchFilter(searchString, 'firstname'),
						logic.makeSearchFilter(searchString, 'lastname')
					);
				//	make the endpoint call
					emps = yield ssEmployeeApi.query(config);
				//	get the total amount for the view
					vm.employeeTotal = parseInt(emps.$headers['total-results'], 10);
				//	reset the data arrays if needed (i.e. when searching)
					if(reset) {
						vm.employeesDisplayed = [];
						vm.employeesRetrieved = [];
					}
				//	Add the newly retrieved data to the current set
					vm.employeesRetrieved = [...vm.employeesRetrieved, ...emps];
				//	create a copy of the data retrieved for filtering purposes
					vm.employeesDisplayed = vm.employeesRetrieved.slice();
				//	determine whether or not to show the infinite scroll element
					//vm.showInfScroll = (emps.length > pageSize) || (vm.employeesDisplayed.length < vm.employeeTotal);
				} catch (e) {
					console.error('Error getting employees', e);
				} finally {
					vm.loading = false;
				}
			})();
		}

		function init() {
			ssAsync(function*() {
				vm.loading = true;
				try {
				//	get the groups & profiles
					[groups, profiles] = yield $q.all([routeVm.securityGroupsPromise, routeVm.profilesPromise]);
				//	Perform necessary data manipulation
				//	/* Is this necessary here? */
					// groups = logic.permProfiles(groups, profiles);
				//	create the add to group menu options
					vm.addToGroupOptions = logic.makeMenuOptions(groups);
				//	create the filter menu options
					vm.groupFilterOptions = [{
						name: 'All',
						value: null
					}, ...vm.addToGroupOptions];
				//	set the initial filter option
					vm.selectedGroupFilter = vm.groupFilterOptions[0].value;
				//	get the users
					yield getUsers();
				} catch(e) {
					console.error('Error loading data', e);
					vm.loading = false;
				}
			})();
		}

		init();

	}
}

// #-- END Segment (/permissions/users/index/segment-content) --# //
// #------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentPermissionsUsersIndexContent', () => new Content('content'));
