import {Segment} from 'base-segment';
// #------------------------------------------------# //
// #---- Segment (/permissions/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssMultiInitiativeFeatureDetectorSvc) {
	    const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentPermissionsContentLogic');// jshint ignore:line

		$scope.permissionsVm = routeVm;
		// filter out the contexts for subroutes here
		$scope.permissionsVm.contexts = ssMultiInitiativeFeatureDetectorSvc.filterProtectedContexts($scope.permissionsVm.contexts);

		const vm = $scope.vm = { // jshint ignore:line
		};

		const go = $scope.go = { // jshint ignore:line
		};
	}
}

// #-- END Segment (/permissions/segment-content) --# //
// #------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentPermissionsContent', () => new Content('content') );
