import {Segment} from 'base-segment';
// #--------------------------------------------------------------------# //
// #---- Segment (/permissions/groups/group-detail/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $state, $stateParams, ssToastHlp) {
		const routeVm = $scope.routeVm; // eslint-disable-line

		// hookup segment logic
		const logic = $injector.get('segmentPermissionsGroupsGroupDetailContentLogic'); // eslint-disable-line

		let group = $scope.group = $scope.groupsVm.groups.find(g => g.id === $stateParams.groupId);


		const vm = $scope.vm = {
			editTitle: false,
			editDescription: false,
		};

		vm.currentTitle = group._groupName;
		vm.currentDescription = group._groupDescription;

		const go = $scope.go = {
			rename() {
				go.cancel();
				vm.editTitle = true;
			},

			editDescription() {
				go.cancel();
				vm.editDescription = true;
			},

			cancel() {
				vm.editTitle = false;
				vm.editDescription = false;

				vm.currentTitle = group._groupName;
				vm.currentDescription = group._groupDescription;
			},

			delete() {
				group.$remove().then(() => {
					ssToastHlp.success(`${group._groupName} deleted"`, 'Delete Group', false);
					$state.go('base.permissions.groups', null, {reload: true});
				}).catch((e) => {
					ssToastHlp.error(`${group._groupName} was not successfully deleted"`, 'Delete Group', false);
					console.log('Error deleting "${group._groupName}"', e);
				});
			},

			save() {
				group._groupName = vm.currentTitle;
				group._groupDescription = vm.currentDescription;

				group.$save().then(res => {
					vm.editTitle = false;
					vm.editDescription = false;
				});
			},
		};
	}
}

// #-- END Segment (/permissions/groups/group-detail/segment-content) --# //
// #--------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentPermissionsGroupsGroupDetailContent', () => new Content('content') );
