import {Segment} from 'base-segment';
import { log } from 'util';
// #----------------------------------------------------------------------------------# //
// #---- Segment (/permissions/groups/group-detail/group-members/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
		controller($q, $injector, $scope, $stateParams, $filter, ssListSvc, ssAsync,
					ssEmployeeApi, ssToastHlp, ssArrayHlp){
		const groupsVm = $scope.groupsVm; // eslint-disable-line


		// hookup segment logic
		const logic = $injector.get('segmentPermissionsGroupsGroupDetailGroupMembersContentLogic');// eslint-disable-line

		let group = $scope.group;

		let positionTypes;

		const vm = $scope.vm = {
			formOpened: false,
			employeeHolder: [],
			loading: false,
			statusMsg: '',
			search: null,
			employeesInAndThereCurrentGroups: [],
			showingModalUi: false,
			removeEmployeeFromResult: []
		};



		const go = $scope.go = { // eslint-disable-line
			checkGroupsConflicts() {
				vm.employeesInAndThereCurrentGroups = logic.findEmployeesInGroups(groupsVm.groups, vm.employeeHolder);
				const anyUserBelongsToOtherGroups = logic.anyUserBelongsToOtherGroups(vm.employeesInAndThereCurrentGroups);
				vm.showingModalUi = anyUserBelongsToOtherGroups;
				if(!anyUserBelongsToOtherGroups) {
					let employees = vm.employeesInAndThereCurrentGroups.map(e => e.employee);
					ssAsync(function*(){
						try{
							yield addMembers(employees);
							yield refreshData();
						}catch (e){
							ssToastHlp.error(e);
						}
					})();
				}
			},
			addMembersToGroup(){
				vm.showingModalUi = false;
				// All Employees selected will be added to new Group
				let allEmployeesSelected = vm.employeesInAndThereCurrentGroups.map(e => e.employee);
				// Employees that are part of other group will be removed from there old group
				let employeesPartOfSomeGroup = vm.employeesInAndThereCurrentGroups.filter(e => e.groups.length).map(e => e.employee);
				let groups = ssArrayHlp.unique(logic.flattenTheGroups(vm.employeesInAndThereCurrentGroups));
				ssAsync(function*(){
					try{
						yield addMembers(allEmployeesSelected);
						yield removeMembersFromGroups(groups, employeesPartOfSomeGroup);
						yield refreshData();
					} catch(e) {
						ssToastHlp.error(e);
					}
				})();
			},
			addMembers: () => addMembers(vm.employeeHolder).then(refreshData),
			toggleMenu() {
				vm.employeeHolder = [];
				vm.formOpened = !vm.formOpened;
			},
			removeMember(emp) {
				vm.statusMsg = "Removing user from group...";
				vm.loading = true;
				let index = group._employeeIds.indexOf(emp.id);
				group._employeeIds.splice(index, 1);

				// create a new reference for json api to pick up
				group._employeeIds = [...group._employeeIds];

				group.$save().then((success) => {
					let successMsg = `${emp.fullName} has been successfully removed from ${group._groupName}`;
					ssToastHlp.success(successMsg, null);
					refreshData();
				}).catch((e) => {
					vm.loading = false;
					vm.statusMsg = '';
					ssToastHlp.error(`There was an error removing "${emp._fullName}" from ${group._groupName}`, 'Remove User', false);
					console.error(e);
				});
			},
			haveGroups(){	return function(item) {	return item.groups.length > 0;	}	},
			showScroll: (employeesInAndThereCurrentGroups) => employeesInAndThereCurrentGroups.filter(a => a.employee).length > 8
		};

		function loadEmployees(initialLoad) {
			if(initialLoad){
				vm.loading = true;
			};
			vm.statusMsg = 'Loading group members...';
			let getEmps = ssAsync(function*() {
				group.employees = (!group._employeeIds.length) ?
									[] :
									yield ssEmployeeApi.queryByIds({ include: 'assignments,positions' }, { ids: group._employeeIds });
			});

			return getEmps().then(() => {
				//	spin through all returned employees to get their position info so we can display it
				group.employees.forEach(emp => {
					if (!emp._assignments) {return;}
					let primaryAssignment = emp._assignments.find(a => a._isPrimary);
					if(!primaryAssignment) { return; }
					emp.position = emp._positions.find(p => p.id === primaryAssignment._positionId);
					// create the user-friendly position name
					if (emp.position != null) {
						emp.position.name = ssListSvc.mapName(emp.position._name, positionTypes);
					}
				});
				//	create a clone of the employees collection for filtering
				vm.filteredEmps = group.employees.slice();
				vm.removeEmployeeFromResult = vm.removeEmployeeFromResult.concat(vm.filteredEmps.map(e => e.id));
			});
		};

		//	function to allow refresh of data after adding/removing group members. Passes in a custom message for toast
		function refreshData() {
			return ssAsync(function*(){
				try{
					yield loadEmployees();
				}
				catch(e) {
					ssToastHlp.error(`There was an error loading members of ${group._groupName}`, '');
					console.error(e);
				}
				finally{
					vm.loading = false;
				 	vm.statusMsg = '';
				}
			})();
		}

		function removeMembersFromGroups(groups, employees){
			const empNames = employees.map(logic.fullName).join();
			const employeeIds = employees.map(e => e.id);
			let promises = [];
			vm.statusMsg = `Removing ${empNames} from there previous groups...`;
			vm.loading = true;
			groups.forEach(grp => {
				grp._employeeIds = grp._employeeIds.filter(function(i) { return employeeIds.indexOf(i) < 0 });
				promises.push(grp.$save());
			});
			return ssAsync(function*(){
				try{
					yield $q.all(promises);
				}
				catch (e) {
					vm.statusMsg = '';
					vm.loading = false;
					ssToastHlp.error(`There was an error removing ${empNames} from there previous groups`, '', false);
					console.error(e);
				}
			})();
		}

		function addMembers(employees){
			const empNames = employees.map(logic.fullName).join();
			vm.statusMsg = 'Adding user(s) to group...';
			vm.loading = true;
			let employeeIds = employees.map(e => e.id);
			group._employeeIds = [...group._employeeIds, ...employeeIds];

			go.toggleMenu();

			return ssAsync(function*(){
				try{
					yield group.$save();
					let successMsg = `${empNames} has been successfully added to ${group._groupName}`;
					ssToastHlp.success(successMsg, null);
				}catch (e){
					vm.loading = false;
					vm.statusMsg = '';
					ssToastHlp.error(`There was an error adding ${empNames} to ${group._groupName}`, 'Add User', false);
					console.error(e);
				}
			})();
		}



		$scope.$on('$destroy', $scope.$watchCollection('vm.employeeHolder',(nv, ov) => {
			if(nv===ov) { return; }
			vm.removeEmployeeFromResult = vm.employeeHolder.map(e => e.id);
		}));


		//	Initiate the loading of group members and create the watch for search filtering
		ssAsync(function*() {
			positionTypes = yield ssListSvc.getByExternalId('position.types');
			loadEmployees(true).then(() => {
			//	create a watch to filter the displayed list upon search
				$scope.$on('$destroy', $scope.$watch('vm.search', (nv,ov) => {
					if(nv===ov) {return;};
					vm.filteredEmps = $filter('filter')(group.employees, (emp) => {
						let fullName = `${emp._firstName} ${emp._lastName}`;
						let reg = new RegExp(nv.trim(), 'i');
						return reg.test(fullName);
					});
				}));
			}).catch((e)=> {
				ssToastHlp.error(`There was an error loading members of ${group._groupName}`, '');
				console.error(e);
			}).finally(()=> {
				vm.loading = false;
				vm.statusMsg = '';
			});
		})();

	}
}

// #-- END Segment (/permissions/groups/group-detail/group-members/segment-content) --# //
// #----------------------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentPermissionsGroupsGroupDetailGroupMembersContent', () => new Content(
	'content'));
