import { Segment } from 'base-segment';
// #-----------------------------------------------------------------------# //
// #---- Segment (/general-config/job-types/segment-job-types-content) ----# //

class JobTypesContent extends Segment {
	/*@ngInject*/
	controller($injector, $sce, $scope, $state, ssEnum, ssAsync, ssJobTypesApi, ssActiveUserSvc, ssListSvc, ssDateHlp, ssDateByOrgHlp, ssOrganizationSvc, ssMultiInitiativeFeatureDetectorSvc, ssConfigurationHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;
		const parentVm = $scope.parentVm

		parentVm.HRMSmessage = 'Job Types are editable in the HRMS application, but are referred to as "Role Types"';

		// hookup segment logic
		const logic = $injector.get('segmentGeneralConfigJobTypesJobTypesContentLogic'); // jshint ignore:line
		var org = null;
		const now = new Date();

		// view model for tracking state
		const vm = $scope.vm = {
			config: {
				dataCategoryEnum: 'jobTypeCategories',
				dataCategoryOptions: ssEnum.jobTypeCategories.getEnums(),
				customExtend: function(JobType) {
					return go.extendJobType(JobType);
				},
				gridColumns: [{
					label: 'Job Type',
					required: true,
					selector: 'job-type-col',
					sortBy: 'name',
					viewTemplate: ssMultiInitiativeFeatureDetectorSvc.showJobTypeInfo() ? `<div class="job-type-toggle-group">
							<super-popover link="{{ item._name }}" ss-icon-after="exclamation-circle" ng-click="item.loadDetails()">
								<div ng-bind-html="item.templateHtml"></div>
							</super-popover>
						</div>` : `<span ng-bind="item._name"></span>`,
					editTemplate: `<super-input-text placeholder="Name" model="item._name" ss-validate="validation.required(item._name, 'Name')" required="true"></super-input-text>`,
				}, {
					label: 'Employment Group',
					required: true,
					selector: 'job-type-emp-group-col',
					viewTemplate: `<p ng-bind="item.selectedEmpGroup"></p>`,
					editTemplate: `<super-select-list id="bargainingUnitControl" model="item._bargainingUnit.id" ss-validate="validation.required(item._bargainingUnit.id, 'Employment Group')" required="true"><super-select-list-item ng-repeat="option in routeVm.empGroupOptions" label="option.name" value="option.value"></super-select-list-item>
						</super-select-list>`,
				}, {
					label: 'Job Type External Id',
					required: true,
					selector: 'job-type-ext-id-col',
					viewTemplate: `<span ng-bind="item._externalId"></span>`,
					editTemplate: `<super-input-text placeholder="External ID" model="item._externalId" ss-validate="validation.externalId(item._externalId, 'External ID')" required="true"></super-input-text>`,
				}, {
					label: 'Pay Schedule External Id',
					required: true,
					selector: 'pay-schedule-ext-id-col',
					viewTemplate: `<span ng-bind="item.payScheduleExtId"></span>`,
					editTemplate: `<super-input-text placeholder="External ID" model="item.payScheduleExtId" ss-validate="validation.externalId(item.payScheduleExtId, 'External ID')" required="true"></super-input-text>`,
				}, {
					label: 'Data Analysis',
					selector: 'job-type-data-category-col',
					info: `These categories help Frontline Education aggregate data to provide tools which better serve your needs.`,
					viewTemplate: `<span ng-bind="item.selectedDataCategory"></span>`,
					editTemplate: `<super-select-list model="item._dataCategory" ss-validate="validation.required(item._dataCategory)"><super-select-list-item ng-repeat="option in config.dataCategoryOptions | orderBy: 'name'" label="option.name" value="option.value"></super-select-list-item></super-select-list>`,
				}],
				customActions: [{
					label: 'Edit',
					fn: function(actions, item) {
						return actions.editItem(item);
					}	
				}, {
					label: 'Assign Details',
					fn: function(actions, item) {
						$state.go('base.generalConfig.jobTypes.jobTypeDetail', { 'jobTypeId': item.id });
						// return routeVm.assignDetails(item);
					}
				}],
				cacheKey: ''
			},
			permissions: {
				canView: $scope.$root.isPermitted.listHeaders.view || false,
				canCreate: $scope.$root.isPermitted.listHeaders.create && !parentVm.isReadonly || false,
				canEdit: $scope.$root.isPermitted.listHeaders.edit && !parentVm.isReadonly || false,
				canDelete: $scope.$root.isPermitted.listHeaders.delete && !parentVm.isReadonly || false
			}
		};

		if (!ssMultiInitiativeFeatureDetectorSvc.showEmploymentGroups()) {
			vm.config.gridColumns = vm.config.gridColumns.filter(col => col.selector !== 'job-type-emp-group-col');
		}

		ssAsync(function*() {
			try {

				org = yield ssOrganizationSvc.getActiveOrg();

				console && console.log && console.log(org);

				const orgId = yield ssActiveUserSvc.getOrgId();
				vm.config.cacheKey = `${orgId}_jobTypes`;
				//	create this data on routeVm so the grid-group component has access to it
				routeVm.empGroupOptions = yield ssListSvc.getByExternalId('jobtype.employmentgroups');
			} catch (e) {
				console && console.error && console.error('ERROR', e);
			}
		})();

		// this method navigates to the details page for a job type and is placed in routeVm so that the grid component can access it
		// routeVm.assignDetails = function(item) {
		// 	$state.go('base.generalConfig.jobTypes.jobTypeDetail', { 'jobTypeId': item.id });
		// };

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			getJobTypes(filter) {
				return ssJobTypesApi.query(Object.assign({include: 'paySchedules'}, filter));
			},

			extendJobType(jobType) {
				if (jobType._bargainingUnit == null) {
					jobType._bargainingUnit = { id: '' };
				}
				if (jobType._currentJobTypeDetail != null) {
					// Determine if a job type is new
					if (ssDateHlp.isForeverPast(jobType._currentJobTypeDetail.changedUtc) || jobType._currentJobTypeDetail.changedUtc === null) {
						jobType.isNew = true;
					}
					// check to see if there are any job type detail values other than 0
					let detailProps = ['fte', 'hoursPerDay', 'workDaysPerYear', 'payPeriodsPerYear', 'paidHolidaysPerSchoolYear', 'sickDaysPerSchoolYear', 'personalDaysPerSchoolYear',
						'vacationDaysPerSchoolYear'
					];

					jobType.hasCustomDetails = detailProps.reduce((prev, curr) => {
						return prev + jobType._currentJobTypeDetail[curr];
					}, 0);

				}
				//	Determine the currently assigned employment group
				var assignedEmpGroup = routeVm.empGroupOptions.find(function(group) {
					return group.value === jobType._bargainingUnit.id;
				});

				jobType.payScheduleExtId = jobType._paySchedules.find((schedule) => {
					return schedule.id !== 0 && ssDateByOrgHlp.isCurrent({obj: schedule, org})
				})._externalId;

				console && console.log && console.log('jobType.payScheduleExtId', jobType.payScheduleExtId);
				jobType.selectedEmpGroup = assignedEmpGroup != null ? assignedEmpGroup.name : 'Please Select';
				jobType.templateHtmlLoaded = false;
				jobType.templateHtml = $sce.trustAsHtml('<div></div>');
				jobType.loadDetails = function() {
					if (!jobType.templateHtmlLoaded) {
						jobType.templateHtml = $sce.trustAsHtml('<div>Loading...</div>');
						ssJobTypesApi.get({
							id: jobType.id,
							include: 'JobTypeDetails,PaySchedules'
						}).then(function(jt) {
							jobType.templateHtmlLoaded = true;
							jobType.templateHtml = getDetailHtml(jt);
						});
					}
				};
			}
		};

		function getDetailHtml(jobType) {
			//	determine if the jobType has an active schedule
			let hasActivePaySchedule = jobType._paySchedules.filter((ps) => {
				return ps._fromDateUtc && !ssDateHlp.isForeverPast(ps._fromDateUtc);
			}).find((ps) => {
				let fromDate = new Date(ps._fromDateUtc);
				let toDate = new Date(ps._toDateUtc);
				return fromDate <= now && toDate > now;
			});
			let psVal = hasActivePaySchedule ? 'Yes' : 'No';
			let hasActiveJTD = jobType._jobTypeDetails
				.filter(x => !ssDateHlp.isForeverPast(x._fromDateUtc))
				.filter(x => x.isCurrent).length > 0;
			let jtdVal = hasActiveJTD ? 'Yes' : 'No';
			return $sce.trustAsHtml(`
				<div>
					<dl><dt>Active Job Type Details:&nbsp;</dt>
					<dd>${jtdVal}</dd>
					<dt>Active Pay Schedule:&nbsp;</dt>
					<dd>${psVal}</dd>
					</dl>
				</div>
			`);

		}
	}
}

// #-- END Segment (/general-config/job-types/segment-job-types-content) --# //
// #-----------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentGeneralConfigJobTypesJobTypesContent', () => new JobTypesContent('job-types-content'));
