import { State } from 'base-state';
// #--------------------------------------------------# //
// #---- State (/general-config/employment-groups) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const stateName = 'generalConfig.employmentGroups';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/employment-groups',
	templateUrl: 'routes/general-config/employment-groups/employment-groups.html',
	resolve: {
		$title: /*@ngInject*/ () => {
			return 'Frontline Central - Employment Groups';
		},
		loader: /*@ngInject*/ (ssAsync, ssMultiInitiativeFeatureDetectorSvc, ssEventSvc) => {
			return ssAsync(function*() {
				if (!ssMultiInitiativeFeatureDetectorSvc.showEmploymentGroups()) {
					ssEventSvc.emit('error-404');
				}
			})();
		},
	},
};

// #-- END State (/general-config/employment-groups) --# //
// #--------------------------------------------------# //

angular.module('ec-settings').config( /*@ngInject*/ ($stateProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
