import {Segment} from 'base-segment';
// #------------------------------------------------------------------# //
// #---- Segment (/permissions/users/user-detail/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $q, enumSecurityGroupScope, createPermissionsHlp, ssAsync, ssArrayHlp, ssToastHlp) {
		const routeVm = $scope.routeVm;
		// hookup segment logic
		const logic = $injector.get('segmentPermissionsUsersUserDetailContentLogic');// jshint ignore:line

		const vm = $scope.vm = {
			securityGroupScope: enumSecurityGroupScope,
			selected: false,
			addAdditional: false,
			expanded: false,
			permissions: {},
			itemsInScope: {}
		};

		var groups, profiles;
		var allGroupProfiles = [];

		const go = $scope.go = { // eslint-disable-line

			removeUserFromGroup(emp, group) {
				let updatedGroup = logic.removeUserFromGroup(emp, group);
				if(!updatedGroup) {
					ssToastHlp.error(`${emp.fullName} does not belong to "${group._groupName}"`, 'Remove User From Group', false);
					return;
				}
				updatedGroup.$save().then(res => {
					vm.groups.splice(vm.groups.indexOf(group), 1);
					ssToastHlp.success(`${emp.fullName} has been successfully removed from "${group._groupName}"`, 'Remove User From Group', false);
				}).catch((e) => {
					ssToastHlp.error(`${emp.fullName} was not successfully removed from "${group._groupName}"`, 'Remove User From Group', false);
					console.log('Error saving "${group._groupName}"', e);
				});
			}
		};

		function createDisplayedScopes(sScope) {
			vm.itemsInScope[sScope.id] = Object.assign({},
				logic.createScopeObj({type: 'Location', context: sScope._locationAdministrationContext, total: sScope._locationIds.length, emp: routeVm.emp}),
				logic.createScopeObj({type: 'Dept', context: sScope._departmentAdministrationContext, total: sScope._departmentIds.length, emp: routeVm.emp}),
				logic.createScopeObj({type: 'Job Types', context: sScope._jobTypeAdministrationContext, total: sScope._jobTypeIds.length, emp: routeVm.emp}),
				logic.createScopeObj({type: 'Employees', context: sScope._employeeAdministrationContext, total: sScope._employeeIds.length, emp: routeVm.emp})
				);
		};

		function assignGroupsToProfiles(sScope, group) {
			sScope.permissionProfile = profiles.find(p => sScope._permissionProfileId === p.id);
			if (sScope.permissionProfile != null) {
				let profile = sScope.permissionProfile;
				if (!angular.isArray(profile.groups)) {
					profile.groups = [];
				}
				if (profile.groups.indexOf(group) < 0) {
					profile.groups.push(group);
				}
			}
		}

		function getAllProfiles(group) {
			let groupProfiles = group._securityScopes.map(s => s.permissionProfile);
			allGroupProfiles = [...allGroupProfiles, ...groupProfiles];
		};

		function extendGroups(groups) {
			groups.forEach((group) => {
				group._securityScopes.forEach(sScope => {
					assignGroupsToProfiles(sScope, group);
					createDisplayedScopes(sScope);
				});
				getAllProfiles(group);
			});
		};

	//	initialize the data
		function init() {
			ssAsync(function*() {
				vm.loading = true;
				try {
				//	get the groups & profiles
					[groups, profiles] = yield $q.all([routeVm.securityGroupsPromise,routeVm.profilesPromise]);
				//	filter on groups that have this employee as a child
					vm.groups = groups.filter(g => g._employeeIds.find(e => e === routeVm.emp.id) || (g._appliesToAllEmployees && !g._locked));
				//	get the item in scope
					extendGroups(vm.groups);
				//	find unique profiles
					vm.uniqueProfiles = ssArrayHlp.unique(allGroupProfiles);
				//	get permission sets
					vm.permissions = logic.getContextPermissions($scope.permissionsVm.contexts, vm.uniqueProfiles);
				} catch(e) {
					console.error('Error initializing Data', e);
				} finally {
					vm.loading = false;
				}
			})();
		}

		init();

	}
}

// #-- END Segment (/permissions/users/user-detail/segment-content) --# //
// #------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentPermissionsUsersUserDetailContent', () => new Content(
	'content'));
