import { State } from 'base-state';
// #---------------------------------------------# //
// #---- State (/general-config/credentials) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const ROUTE_CONSTANTS = {
	CREDENTIAL_GROUPS: {
		STATE_NATIONAL: 'state-national',
		CUSTOM: 'custom',
		CUSTOM_ISSUERS: 'custom-issuers'
	},
	CREDENTIAL_TYPES: {
		CERTIFICATE_LICENSE: 'certificate-licenses',
		CLEARANCES_EMPLOYMENT: 'clearances-employment',
		ORG_SPECIFIC: 'org-specific'
	}
};

const stateName = 'generalConfig.credentials';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/credentials/:credentialGroup/:credentialType',
	templateUrl: 'routes/general-config/credentials/credentials.html',
	params: {
		credentialGroup: 'custom',
		credentialType: 'certificate-licenses'
	},
	controller: function ($scope, $stateParams) {
		$scope.portfolioId = $stateParams.portfolioId;
	},
	resolve: {
		contextLoader: /*@ngInject*/ ($stateParams, ssAsync, ssOrganizationSvc) => {
			routeVm.routeConstants = ROUTE_CONSTANTS;

			routeVm.isStateNational = $stateParams.credentialGroup === ROUTE_CONSTANTS.CREDENTIAL_GROUPS.STATE_NATIONAL;
			routeVm.isCustomCreds = $stateParams.credentialGroup === ROUTE_CONSTANTS.CREDENTIAL_GROUPS.CUSTOM;
			routeVm.isCustomIssuer = $stateParams.credentialGroup === ROUTE_CONSTANTS.CREDENTIAL_GROUPS.CUSTOM_ISSUERS;
			routeVm.credentialGroup = $stateParams.credentialGroup;
			routeVm.credentialType = $stateParams.credentialType;
			routeVm.showIssuers = (routeVm.credentialGroup === 'custom-issuers' && routeVm.credentialType === 'org-specific');
			routeVm.showCredentials = ($stateParams.credentialGroup === 'state-national') ||
				($stateParams.credentialGroup === 'custom' && $stateParams.credentialType === 'certificate-licenses');

			return ssAsync(function* () {
				routeVm.org = yield ssOrganizationSvc.getActiveOrg();
			})();
		},
		loader: /*@ngInject*/ (ssAsync, ssFlagSvc, ssEventSvc, $location) => {
			return (ssAsync(function* () {
				try {
					if (!ssFlagSvc.checkState('fc-new-credentials-enabled')) {
						// OH HAI SUPAR HACK!  I can't wait to release and delete this!
						$location.url('ec-settings/general-config/departments');
					}
				} catch (error) {
					ssEventSvc.emit('error-404');
				}
			})());
		},
		$title: /*@ngInject*/ () => {
			return 'Employee Records - Credentials';
		},
	},
};

// #-- END State (/general-config/credentials) --# //
// #---------------------------------------------# //

angular.module('ec-settings').config( /*@ngInject*/ $stateProvider => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
