/*@ngInject*/
function logic() {

	// logic api
	return {
		makeSearchFilter(val, type) {
			var o = {};
			if (val != null) {
				let joinedValue = val.split(' ').join(',');
				o['filter[' + type + ']'] = joinedValue;
				return o;
			} else {
				return null;
			}
		},

		filterUsersByGroup(users, groups, groupId) {
			if(groupId) {
				let group = findById(groups, groupId);
				return users.filter(u => group._employeeIds.indexOf(u.id) >= 0);
			} else {
				return users;
			}
		},

		addUsersToGroup(users, groups, groupId) {
			let selectedEmployeeIds = users.map(u => u.id);
			let group = findById(groups, groupId);
			group._employeeIds = [...group._employeeIds, ...selectedEmployeeIds];
			return group;
		},

		getUserGroups(user, groups) {
			return groups.filter(g => g._employeeIds.includes(user.id) || (g._appliesToAllEmployees && !g._locked));
		},

		/* Not sure what this was used for or if it's needed here */
		// permProfiles(groups, profiles) {
		// 	groups.forEach(group => {
		// 		group._securityScopes.forEach(s => {
		// 			s.permissionProfile = findById(profiles, s._permissionProfileId);
		// 			if (s.permissionProfile != null) {
		// 				let profile = s.permissionProfile;
		// 				if (!angular.isArray(profile.groups)) {
		// 					profile.groups = [];
		// 				}
		// 				if (profile.groups.indexOf(group) < 0) {
		// 					profile.groups.push(group);
		// 				}
		// 			}
		// 		});
		// 	});
		// 	return groups;
		// },

		makeMenuOptions(groups) {
			let validGroups = groups.filter(g => !g._appliesToAllEmployees && !g._locked);
			return validGroups.map(g => {
				return {
					name: g._groupName,
					value: g.id
				};
			});
		}

	};

	function findById(arr, id) {
		return arr.find(g => g.id === id);
	}

}
angular.module('ec-settings-segment-logic').factory('segmentPermissionsUsersIndexContentLogic', logic);