import {State} from 'base-state';
// #------------------------------# //
// #---- State (/permissions) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {
	contexts: []
};

const stateName = 'permissions';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/permissions',
	$permissions: (permissionSvc) => permissionSvc.isPermittedAction('permissionProfiles', 'edit'),
	templateUrl: 'routes/permissions/permissions.html',
	resolve: {
		data: /*@ngInject*/ (ssAsync, ssProtectedContextApi) => {
			return ssAsync(function*() {
				routeVm.contexts = yield ssProtectedContextApi.query({
					productId: 'ec',
					limit: 50
				});
			})();
		},
		$title: /*@ngInject*/ () => {
			return 'Frontline Central - Permissions';
		}
	}
};

// #-- END State (/permissions) --# //
// #------------------------------# //

angular.module('ec-settings').config( /*@ngInject*/ ($stateProvider, $urlRouterProvider) => {
	const defaultRoute = '/ec-settings/permissions/groups';
	const baseRoute = '/ec-settings/permissions';

	$urlRouterProvider.when(baseRoute, defaultRoute);
	$urlRouterProvider.when(`${baseRoute}/`, defaultRoute);

	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
