/*@ngInject*/
function logic() {
	// logic api
	return {
		setFLUserStatus
	};

	/**
	 * Sets the Frontline User's Display Status on the Applicaiont Access Page
	 *
	 * @emp {any} An employee record of Frontline Central
	 * @returns enumFlInviteSstatus value for the employee/product access record
	 */
	function setFLUserStatus(emp, enumFlInviteStatus) {
		// The default status if everything fails.
		//	Minimum displayable status should be no access since we can't figure it out
		let status = enumFlInviteStatus.noAccess;

		// It would be odd if this property wasn't there, so no access for you if it doesn't exist!
		if (emp.productAccessUser == null) {
			return status;
		}

		/**
		 * ***** DUPLICATED FROM THE SOR APPLICATION ACCESS LOGIC ******
		 * Check if the user has pending invitations that are not expired
		 * The expired check is done because IsPending is not changed to false immediately on expiration
		 */
		let hasPendingInvite = false;
		let productAccess = emp.productAccessUser.productAccess || { invitations: [] }; // another safety check on invitations.

		if (productAccess.invitations) {
			productAccess.invitations.forEach((invitation) => {
				if (invitation.isPending && !invitation.isExpired) {
					hasPendingInvite = true;
				}
			});
		}
		/************************************************
		 *END SOR LOGIC DUPLICATION
		 ************************************************/

		if (emp.flId) {
			if (emp.enabled) { // => enabled is set from a corresponding ProductAccess record for that user
				status = enumFlInviteStatus.active;
			} else {
				status = enumFlInviteStatus.inactive;
			}
		}

		if (!emp.flId) {
			if (hasPendingInvite) {
				status = enumFlInviteStatus.sent;
			} else {
				status = enumFlInviteStatus.noAccess;
			}
		}

		return status
	}
}
angular.module('ec-settings-segment-logic').factory('segmentUserAccountsContentLogic', logic);
