import { Segment } from 'base-segment';
// #---------------------------------------------------# //
// #---- Segment (/general-config/segment-sidebar) ----# //

class Sidebar extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssFlagSvc, ssMultiInitiativeFeatureDetectorSvc, ssPermissionsSvc) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line

		// hookup segment logic
		const logic = $injector.get('segmentGeneralConfigSidebarLogic'); // jshint ignore:line
		// Need to check both the FF and the permissions here
		const showSignatureNav =  ssFlagSvc.checkState('fc-enable-signatures') && ssPermissionsSvc.isPermittedAction('signatures', 'manage')
		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
			tabs: [
				{ visibility: true, link: 'base.generalConfig.credentials', label: 'Credentials' },
				{ visibility: true, link: 'base.generalConfig.departments', label: 'Departments' },
				{ visibility: ssMultiInitiativeFeatureDetectorSvc.showEmploymentGroups(), link: 'base.generalConfig.employmentGroups', label: 'Employment Groups' },
				{ visibility: true, link: 'base.generalConfig.jobTypes', label: 'Job Types' },
				{ visibility: true, link: 'base.generalConfig.locations', label: 'Locations' },
				{ visibility: true, link: 'base.generalConfig.positions', label: 'Position Names' },
				{ visibility: showSignatureNav, link: 'base.generalConfig.signatures', label: 'Signatures'},
				{ visibility: ssMultiInitiativeFeatureDetectorSvc.showStatusReasons(), link: 'base.generalConfig.statusReasons', label: 'Status Reasons' }
			]
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			tabFilter: (tab) => {
				return logic.tabFilter(tab);
			}
		};
	}
}

// #-- END Segment (/general-config/segment-sidebar) --# //
// #---------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentGeneralConfigSidebar', () => new Sidebar('sidebar'));
