import { Segment } from 'base-segment';
// #---------------------------------------------------------------------------------# //
// #---- Segment (/general-config/status-reasons/segment-status-reasons-content) ----# //

class StatusReasonsContent extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssActiveUserSvc, ssAsync) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line
		const parentVm = $scope.parentVm;
		const listType = 'employee.statuschangereasons';

		// hookup segment logic
		const logic = $injector.get('segmentGeneralConfigStatusReasonsStatusReasonsContentLogic'); // jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
			config: {
				gridColumns: [{
					label: 'Status Reason',
					required: true,
					sortBy: 'name',
					viewTemplate: '<span ng-bind="item._name"></span>',
					editTemplate: '<super-input-text placeholder="Name" model="item._name" ss-validate="validation.required(item._name, \'Status Reason\')" required="true"></super-input-text>'
				}],
				cacheKey: ''
			},
			permissions: {
				canView: $scope.$root.isPermitted.listHeaders.view || false,
				canCreate: $scope.$root.isPermitted.listHeaders.create && !parentVm.isHRMSEnabled || false,
				canEdit: $scope.$root.isPermitted.listHeaders.edit && !parentVm.isHRMSEnabled || false,
				canDelete: $scope.$root.isPermitted.listHeaders.delete && !parentVm.isHRMSEnabled || false
			}
		};

		ssAsync(function*() {
			try {
				const orgId = yield ssActiveUserSvc.getOrgId();
				vm.config.cacheKey = `${orgId}_list_${listType}`;
			} catch (e) {
				console && console.log && console.log('ERROR', e);
			}
		})();

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			getList(filter) {
				return parentVm.getList(filter, listType);
			},
		};
	}
}

// #-- END Segment (/general-config/status-reasons/segment-status-reasons-content) --# //
// #---------------------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentGeneralConfigStatusReasonsStatusReasonsContent', () => new StatusReasonsContent('status-reasons-content'));
