import {State} from 'base-state';
// #------------------------------------------------------# //
// #---- State (/permissions/profiles/profile-detail) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const stateName = 'permissions.profiles.detail';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/{profileId:[0-9A-Za-z-]{36}}',
	templateUrl: 'routes/permissions/profiles/profile-detail/profile-detail.html',
	resolve: {
		$title: /*@ngInject*/ () => {
			return 'Frontline Central - Permissions Profile';
		}
	}
};

// #-- END State (/permissions/profiles/profile-detail) --# //
// #------------------------------------------------------# //

angular.module('ec-settings').config( /*@ngInject*/ ($stateProvider, $urlRouterProvider) => {
	const defaultRoute = '/ec-settings/permissions/profiles/{profileId:[0-9A-Za-z-]{36}}/view/Employee';
	const baseRoute = '/ec-settings/permissions/profiles/{profileId:[0-9A-Za-z-]{36}}';

	$urlRouterProvider.when(baseRoute, defaultRoute);
	$urlRouterProvider.when(`${baseRoute}/`, defaultRoute);

	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
