import {State} from 'base-state';
// #-------------------------------------------------# //
// #---- State (/permissions/groups/group-detail) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const stateName = 'permissions.groups.detail';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/{groupId:[0-9A-Za-z-]+}',
	templateUrl: 'routes/permissions/groups/group-detail/group-detail.html',
	resolve: {
		$title: /*@ngInject*/ () => {
			return 'Frontline Central - Permissions Group';
		}
	}
};

// #-- END State (/permissions/groups/group-detail) --# //
// #-------------------------------------------------# //

angular.module('ec-settings').config( /*@ngInject*/ ($stateProvider, $urlRouterProvider) => {
	const defaultRoute = '/ec-settings/permissions/groups/{groupId:[0-9A-Za-z-]{36}}/group-members';
	const baseRoute = '/ec-settings/permissions/groups/{groupId:[0-9A-Za-z-]{36}}';

	$urlRouterProvider.when(baseRoute, defaultRoute);
	$urlRouterProvider.when(`${baseRoute}/`, defaultRoute);

	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
