import { Segment } from 'base-segment';
// #-----------------------------------------------------------------------# //
// #---- Segment (/general-config/locations/segment-locations-content) ----# //

class LocationsContent extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssLocationsApi, ssActiveUserSvc, ssAsync, ssEnum) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line
		const parentVm = $scope.parentVm

		parentVm.HRMSmessage = 'Locations are editable in the HRMS application.';

		// hookup segment logic
		const logic = $injector.get('segmentGeneralConfigLocationsLocationsContentLogic'); // jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = {
			config: {
				stateEnums: ssEnum.states.getEnums(),
				dataCategoryEnum: 'locationCategories',
				dataOptions: ssEnum.locationCategories.getEnums(),
				gridColumns: [{
					label: 'Location',
					required: true,
					sortBy: 'name',
					viewTemplate: `<span ng-bind="item._name"></span>`,
					editTemplate: `<super-input-text placeholder="Name" model="item._name" ss-validate="validation.required(item._name, 'Location name')" required="true"></super-input-text>`
				}, {
					label: 'Address',
					options: ssEnum.states.getEnums(),
					viewTemplate: `<p ng-if="item._address.street1" ng-bind="item._address.street1"></p>
									<p><span ng-if="item._address.city">{{item._address.city}},&nbsp;</span>
									<span ng-if="item._address.state">{{item._address.state}}&nbsp;&nbsp;</span>
									<span ng-if="item._address.zip">{{item._address.zip}}</span></p>`,
					editTemplate: `<p><super-input-text placeholder="Address" model="item._address.street1"></super-input-text></p>
						<p><super-input-text placeholder="City" model="item._address.city"></super-input-text></p>
						<p><super-select-list model="item._address.state"><super-select-list-item ng-repeat="state in config.stateEnums" label="state.name" value="state.value"></super-select-list-item></super-select-list></p>
						<p><super-input-text placeholder="Zip" model="item._address.zip" ss-validate="validation.zip(item._address.zip)"></super-input-text></p>`
				}, {
					label: 'Contact',
					viewTemplate: `<p ng-if="item.formattedPhone" ng-bind="item.formattedPhone"></p>
									<p ng-bind="item._email"></p>`,
					editTemplate: `<p><super-input-text placeholder="Phone" model="item._phone"></super-input-text></p><p><super-input-text placeholder="Email" model="item._email" ss-validate="validation.email(item._email)"></super-input-text></p>`
				}, {
					label: 'External Id',
					required: true,
					viewTemplate: `<span ng-bind="item._externalId"></span>`,
					editTemplate: `<super-input-text placeholder="External ID" model="item._externalId" ss-validate="validation.externalId(item._externalId, 'External Id')" required="true"></super-input-text>`
				}, {
					label: 'Data Analysis',
					info: `These categories help Frontline Education aggregate data to provide tools which better serve your needs.`,
					viewTemplate: `<span ng-bind="item.selectedDataCategory"></span>`,
					editTemplate: `<super-select-list model="item._dataCategory"><super-select-list-item ng-repeat="option in config.dataOptions | orderBy: 'name'" label="option.name" value="option.value"></super-select-list-item></super-select-list>`
				}],
				cacheKey: ''
			},
			permissions: {
				canView: $scope.$root.isPermitted.listHeaders.view || false,
				canCreate: $scope.$root.isPermitted.listHeaders.create && !parentVm.isReadonly || false,
				canEdit: $scope.$root.isPermitted.listHeaders.edit && !parentVm.isReadonly || false,
				canDelete: $scope.$root.isPermitted.listHeaders.delete && !parentVm.isReadonly || false
			}
		};

		ssAsync(function*() {
			try {
				const orgId = yield ssActiveUserSvc.getOrgId();
				vm.config.cacheKey = `${orgId}_locations`;
			} catch (e) {
				console && console.error && console.error('ERROR', e);
			}
		})();

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			getLocations(filter) {
				return ssLocationsApi.query(filter);
			}
		};
	}
}

// #-- END Segment (/general-config/locations/segment-locations-content) --# //
// #-----------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentGeneralConfigLocationsLocationsContent', () => new LocationsContent('locations-content'));
