import {State} from 'base-state';
// #--------------------------------# //
// #---- State (/user-accounts) ----# //


// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const stateName = 'userAccounts';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/user-accounts',
	templateUrl: 'routes/user-accounts/user-accounts.html',
	$permissions: (permissionSvc) => permissionSvc.isPermittedAction('users', 'view') || permissionSvc.isPermittedAction('employees', 'manageAccountAccess'),
	resolve: {
		$title: /*@ngInject*/ () => {
			return 'Frontline Central - Application Access';
		}
	}
};


// #-- END State (/user-accounts) --# //
// #--------------------------------# //

angular.module('ec-settings').config(/*@ngInject*/ ($stateProvider) => {
	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
angular.module('super-suit').run(($rootScope, $animate) => {
	$rootScope.$on('$stateChangeStart',
	function(event, toState, toParams, fromState, fromParams, options){
		if(toState.name === 'base.userAccounts') { $animate.enabled(false); }
		else{ $animate.enabled(true); }
	})
})
