import {Segment} from 'base-segment';
// #------------------------------------------------------------------------# //
// #---- Segment (/permissions/profiles/profile-detail/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $state, $stateParams, ssPermissionProfilesApi, ssToastHlp, ssEventSvc, $log) {
	    const routeVm = $scope.routeVm; // eslint-disable-line

		// hookup segment logic
		const logic = $injector.get('segmentPermissionsProfilesProfileDetailContentLogic'); // eslint-disable-line

		const protectedOptions = ['rename', 'delete', 'description'];

		let profile = null;

		const vm = $scope.vm = {
			editTitle: false,
			editDescription: false,
			contexts: $scope.permissionsVm.contexts,
		};

		const go = $scope.go = { // eslint-disable-line
			rename() {
				go.cancel();
				vm.editTitle = true;
			},

			editDescription() {
				go.cancel();
				vm.editDescription = true;
			},

			duplicate() {
				ssPermissionProfilesApi.duplicate({
					id: profile.id
				}).then(profile => {
					$state.go('base.permissions.profiles.detail', {
						profileId: profile.id
					}, {reload: true});

					ssToastHlp.success(`${profile._permissionProfileName} copied successfully`, 'Profile Copied', false);
				});
			},

			delete() {
				profile.$remove().then(() => {
					$state.go('base.permissions.profiles', null, {reload: true});
				});
			},

			cancel() {
				vm.editTitle = false;
				vm.editDescription = false;
				vm.currentTitle = profile._permissionProfileName;
				vm.currentDescription = profile._permissionProfileDescription;
			},

			save() {
				profile._permissionProfileName = vm.currentTitle;
				profile._permissionProfileDescription = vm.currentDescription;
				profile.$save().then(res => {
					vm.editTitle = false;
					vm.editDescription = false;
				});
			},
			getEditOptions() {

				let options = [{
					label : "Rename",
					func : 'rename',
					method : () => { return go.rename(); }
				}, {
					label : `${profile._permissionProfileDescription ? 'Edit' : 'Add'} Description`,
					func : 'description',
					method : () => { return go.editDescription(); }
				}, {
					label : "Duplicate Profile",
					func : 'duplicate',
					method : () => { return go.duplicate(); }
				}, {
					label : "Delete Profile",
					func : 'delete',
					method : () => { return go.delete(); }
				}];

				return options.filter((o) => {
					return !profile._locked && !profile._autoCreated || profile._autoCreated && (!protectedOptions.includes(o.func)) || profile._permissionProfileName.includes('Copy')
				})
			},
			shouldDisabledThisTab(thisTabsContext){
				return thisTabsContext._protectedResourceRegistration.actions.every(x => x.isPrivileged);
			},
			getDisabledPopoverText,
		};

		function getDisabledPopoverText(thisTabsContext){
			return `<div class="popover-for-disabled-tab" 
						ss-tool-tip="Editing this tab is not recommended for the View-Self profile. If you would like to modify the properties for this tab, please do so from within another profile." 
						ng-if="${go.shouldDisabledThisTab(thisTabsContext)}" 
						ng-click="$event.preventDefault();$event.stopImmediatePropagation();" 
					></div>`;
		}

		(function() {
			try {
			//	find the current profile
				profile = $scope.profile = $scope.profilesVm.profiles.find((p) => {
					return p.id === $stateParams.profileId;
				});

				vm.currentTitle = profile._permissionProfileName;
				vm.currentDescription = profile._permissionProfileDescription;
				vm.editOptions = go.getEditOptions();
				
			} catch(e) {
				$log.error('404');
				ssEventSvc.emit('error-404');
				return;
			}
		})();
	}
}

// #-- END Segment (/permissions/profiles/profile-detail/segment-content) --# //
// #------------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentPermissionsProfilesProfileDetailContent', () => new Content(
	'content'));
