import { State } from 'base-state';
// #---------------------------------# //
// #---- State (/general-config) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.

let lists = [];

const routeVm = {
	tabs: [],
	credsEnabled: false
};

const stateName = 'generalConfig';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/general-config',
	templateUrl: 'routes/general-config/general-config.html',
	$permissions: (permissionSvc) => permissionSvc.isPermittedAction('listHeaders', 'view'),
	resolve: {
		lists: /*@ngInject*/ (ssAsync, ssListApi) => {
			routeVm.getList = (filter, listName) => {
				if (filter == null || listName == null || lists == null || lists.length < 1) { return []; }
				let item = lists.find(item => item._externalId === listName);
				if (item == null || item.id == null) { return []; }

				filter.id = item.id;
				return ssListApi.getListItems(filter);
			};

			return ssAsync(function*() {
				// get all the lists so the child states can get the list ids
				lists = yield ssListApi.query({ limit: 1000 });
			})();
		},
		enableCreds: /*@ngInject*/ (ssFlagSvc) => {
			return routeVm.credsEnabled = ssFlagSvc.checkState('fc-new-credentials-enabled');
		},
		$title: /*@ngInject*/ () => {
			return 'Frontline Central - General Config';
		},
		isHRMSEnabled: /*@ngInject*/ function(ssMultiInitiativeFeatureDetectorSvc) {
			routeVm.isHRMSEnabled = ssMultiInitiativeFeatureDetectorSvc.isHRMSEnabled();
			routeVm.isReadonly = ssMultiInitiativeFeatureDetectorSvc.isConfigurationListManagementReadonly();
		}
	}
};

// #-- END State (/general-config) --# //
// #---------------------------------# //

angular.module('ec-settings').config( /*@ngInject*/ ($stateProvider, $urlRouterProvider) => {
	const baseRoute = `/ec-settings`;
	let defaultRoute = 'ec-settings/general-config/credentials/custom/certificate-licenses';

	sidekick.events.on('feature-flags-ready', () => {
		$urlRouterProvider.when(baseRoute, defaultRoute);
		$urlRouterProvider.when(`${baseRoute}/`, defaultRoute);
		$urlRouterProvider.when(`${baseRoute}/general-config`, defaultRoute);

		$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
	}, { greedy: true });
});
