import { Segment } from 'base-segment';
// #-----------------------------------------------------------------------# //
// #---- Segment (/general-config/positions/segment-positions-content) ----# //

class PositionsContent extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssAsync, ssActiveUserSvc) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line
		const parentVm = $scope.parentVm;
		const listType = 'position.types';

		parentVm.HRMSmessage = 'Position Names are editable in the HRMS application.';

		// hookup segment logic
		const logic = $injector.get('segmentGeneralConfigPositionsPositionsContentLogic'); // jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = {
			config: {
				gridColumns: [{
					label: 'Position Name',
					required: true,
					sortBy: 'name',
					viewTemplate: '<span ng-bind="item._name"></span>',
					editTemplate: `<super-input-text placeholder="Name" model="item._name" ss-validate="validation.required(item._name, 'Position Name')" required="true"></super-input-text>`
				}, {
					label: 'External Id',
					required: true,
					viewTemplate: `<span ng-bind="item._externalId"></span>`,
					editTemplate: `<super-input-text placeholder="External ID" model="item._externalId" ss-validate="validation.externalId(item._externalId, 'External Id')" required="true"></super-input-text>`
				}],
				cacheKey: ''
			},
			permissions: {
				canView: $scope.$root.isPermitted.listHeaders.view || false,
				canCreate: $scope.$root.isPermitted.listHeaders.create && !parentVm.isReadonly || false,
				canEdit: $scope.$root.isPermitted.listHeaders.edit && !parentVm.isReadonly || false,
				canDelete: $scope.$root.isPermitted.listHeaders.delete && !parentVm.isReadonly || false
			}
		};

		ssAsync(function*() {
			try {
				const orgId = yield ssActiveUserSvc.getOrgId();
				vm.config.cacheKey = `${orgId}_list_${listType}`;
			} catch (e) {
				console && console.log && console.log('ERROR', e);
			}
		})();

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			getList(filter) {
				return parentVm.getList(filter, listType);
			},
		};
	}
}

// #-- END Segment (/general-config/positions/segment-positions-content) --# //
// #-----------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentGeneralConfigPositionsPositionsContent', () => new PositionsContent('positions-content'));
