import {Component} from 'base-component';
// #--------------------------------------------# //
// #---- Component (cmp-user-access-return) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	text: '@'
};

class UserAccessReturn extends Component {
	/*@ngInject*/
	controller($scope, $location, $window) {
		// view model for tracking state
		const vm = $scope.vm = {
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			/**
			 * Create a link to the new SystemOfRecord Application Access Page and go there
			 * @param {*} evt
			 */
			returnToSAAP(evt) {
				const protocol = $location.protocol();
				const host = $location.host();
				const base = `${protocol}://${host}`;
				const url = `${base}/ec-settings/user-accounts`;
				const urlEncoded = encodeURIComponent(url);

				$window.location.href = `${base}/ec-useraccess?oaap=${urlEncoded}`;
			}
		};
	}
}

// #-- END Component (cmp-user-access-return) --# //
// #--------------------------------------------# //

angular.module('ec-settings-components').directive('cmpUserAccessReturn', () => new UserAccessReturn(props, 'user-access-return') );
