import { Component } from 'base-component';
// #---------------------------------------# //
// #---- Component (cmp-profile-table) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	permissions: '=',
	registration: '=',
	dimMatching: '=',
	minColumnDisplay: '@?',
};

class ProfileTable extends Component {
	/*@ngInject*/
	controller($scope, $stateParams, enumPropertyAction, createPermissionsHlp, ssObjectHlp, propertyActionLabelSvc, cmpProfileTableLogic) {
		let deregWatchActions = null;

		const isIdmAdmin = ssObjectHlp.glean($scope, _ => _.$root.isPermitted.activeUser.idmAdministrator, false);

		// view model for tracking state
		const vm = $scope.vm = {
			editMode: $stateParams.action === 'edit',
			isViewSelf: ssObjectHlp.glean($scope, _ => _.$root.utils.findInParentScope('profile', $scope)._isViewSelf, false),
			properties: [],
			selectedActions: {},
			permissions: [],
			fullPropertyAction: enumPropertyAction.getEnums(),
			editResourceActionIsNowAllowed: false,
			viewResourceActionIsNowAllowed: false,
			isIdmAdmin: isIdmAdmin,
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // eslint-disable-line
			shouldDimTheAction(action) {
				return vm.isViewSelf && action.isPrivileged;
			},
			calculateLabel(thesePermissions, permissionId) {
				return propertyActionLabelSvc.getNumberToValueDictionary()[thesePermissions[permissionId].value];
			},
			isDim(row) {
				// nothing to match unless we have more than 1 row
				if ($scope.permissions.length < 2) { return false; }

				let id = row.id;
				let val = $scope.permissions[0][id].value;

				// returns a count of permissions that don't have the same val as the first
				let count = $scope.permissions.filter(p => p[id].value !== val).length;

				// return true if we don't have any permissions with a different value
				return count === 0;
			},
			triggerChange() {
				// this just triggers a json api change
				let perm = $scope.permissions[0];

				perm.propertyActions = [...perm.propertyActions];
			},
		};

		$scope.$watch('permissions.length', () => {
			vm.totalColumns = Math.max($scope.permissions.length, ($scope.minColumnDisplay == null ? 1 : parseInt($scope.minColumnDisplay, 10)));

			loadResource($scope.registration);
		});

		function loadResource(registration) {
			// Pull off Action Permissions as name value pair
			vm.actions = registration.actions.map(a => {
				vm.selectedActions[a.id] = false;
				return {
					id: a.id,
					name: a.description,
					isPrivileged: a.isPrivileged
				};
			});

			function determineSelectableActions(property) {
				let actions = cmpProfileTableLogic.isARedactablePermission(property.id) ? enumPropertyAction.getEnums() : cmpProfileTableLogic.filterActionsByAlwaysAndNever(property);

				if (property.allowedActions != null) {
					// this supercedes anything else allowed in the above calls
					actions = actions.filter(action => property.allowedActions.includes(action.value));
				}

				return actions;
			}
			// Calculate various presentation/UI concerns
			vm.properties = registration.properties
				.map(aProperty => {
					const actionsToShow = determineSelectableActions(aProperty);
					actionsToShow.forEach(anAction => anAction.label = propertyActionLabelSvc.getNumberToValueDictionary()[anAction.value]);
					return {
						id: aProperty.id,
						name: aProperty.description,
						actions: actionsToShow
					};
				});

			$scope.permissions.forEach((permission, i) => {

				vm.actions.forEach(row => {
					if (i === 0) {
						vm.selectedActions[row.id] = permission.objectActions.indexOf(row.id) >= 0;
					}
					// See if we're trying to safeguard the user from doing things that are unbefitting of a view-self scenario
					if (vm.isViewSelf) {
						if (row.id.endsWith('_Action_Edit') && row.isPrivileged === true) {
							vm.editResourceActionIsNowAllowed = true;
						}
						if (row.id.endsWith('_Action_View') && row.isPrivileged === true) {
							vm.viewResourceActionIsNowAllowed = true;
							// If you can't view something, then you certainly shouldn't be able to edit it
							vm.editResourceActionIsNowAllowed = true;
						}
					}
					permission[row.id] = permission.objectActions.indexOf(row.id) >= 0;
				});

				vm.properties.forEach(property => {
					let prop = permission.propertyActions.find(a => a.key === property.id);

					// Do some safeguarding
					if (prop == null) {
						prop = {
							key: property.id,
							value: enumPropertyAction.notVisible
						};

						// use the spread operator to update the array reference so $save will pick it up
						permission.propertyActions = [...permission.propertyActions, prop];
					}

					property.actions.forEach(anAction => {
						anAction.disableDueToViewSelf = cmpProfileTableLogic.shouldDimBasedOnViewSelf({
							areOnAViewSelfPage: vm.isViewSelf,
							editResourceActionIsNowAllowed: vm.editResourceActionIsNowAllowed,
							viewResourceActionIsNowAllowed: vm.viewResourceActionIsNowAllowed,
							actionLevelEnumValue: anAction.value
						});
					});

					permission[property.id] = prop;
				});

				if (vm.editMode) {
					if (deregWatchActions != null) {
						deregWatchActions();
						deregWatchActions = null;
					}

					deregWatchActions = $scope.$watchCollection('vm.selectedActions', (nv, ov) => {
						if (nv === ov) { return; }

						// clear the array
						permission.objectActions = [];

						let keys = Object.keys(vm.selectedActions);
						keys.forEach(key => {
							if (vm.selectedActions[key]) {
								permission.objectActions = [...permission.objectActions, key];
							}
						});
					});
				}
			});

			// create children permission objects
			vm.permissions = [];
			$scope.registration.nestedResources.forEach(nestedReg => {
				var regPermissions = [];
				$scope.permissions.forEach(permission => {
					var chosenPermission = permission.nestedResources.find(s => s.resourceName === nestedReg.resourceName);
					if (chosenPermission == null) {
						chosenPermission = createPermissionsHlp.permission();
						chosenPermission.resourceName = nestedReg.resourceName;
						permission.nestedResources = [...permission.nestedResources, chosenPermission];
					}

					regPermissions.push(chosenPermission);
				});
				vm.permissions.push({
					registration: nestedReg,
					permissions: regPermissions
				});
			});
		}
	}
}

// #-- END Component (cmp-profile-table) --# //
// #---------------------------------------# //

angular.module('ec-settings-components').directive('cmpProfileTable', () => new ProfileTable(props, 'profile-table'));
