import { Segment } from 'base-segment';
// #-------------------------------------------------------------------------------------# //
// #---- Segment (/general-config/job-types/job-type-detail/segment-job-type-detail) ----# //

class JobTypeDetail extends Segment {
	/*@ngInject*/
	controller($scope, $injector, ssAsync, ssEnum, ssJobTypesApi, $stateParams, $location,
				ssToastHlp, $log, ssDateHlp, ssValidationHlp, ssModalSvc, $q, ssOrganizationSvc,
				ssStorage, $sce, ssDateByOrgHlp) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm; // jshint ignore:line
		// hookup segment logic
		const logic = $injector.get('segmentGeneralConfigJobTypesJobTypeDetailJobTypeDetailLogic');// jshint ignore:line

		let org;
		let orgId;
		let currentPaySchedule;
		routeVm.paySchedules = null;
		routeVm.jobType = null;
		routeVm.addingPS = null;

		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
			org: null,
			editDetails: false,
			selectedJobType: $stateParams.jobTypeId,
			selectedEmpGroup: '',
			hasValidPaySchedules: false,
			currentJobTypeDetail: null,
			futureJobTypeDetails: [],
			pastJobTypeDetails: [],
			newJobTypeDetail: null,
			showJTDhistory: false,
			addingJTD: false,
			dtOptionsForCurrentJTD: {},
			dtOptionsForFutureJTD: {},
			dtOptionsForNewJTD: {}
		};

		const validation = $scope.validation = {
			required: ssValidationHlp.required(),
			externalId: ssValidationHlp.string({ maxLength: 100 })
		}; // jshint ignore:line

	//	Get the ORG Id for use in clearing the cache
		ssAsync(function* () {
			try {
				vm.loadingJTD = vm.loadingPS = true;
				vm.org = org = yield ssOrganizationSvc.getActiveOrg();
				orgId = org.id;
				yield routeVm.fetchJobType();
			} catch (e) {
				console.error(e);
			} finally{
				vm.loadingJTD = vm.loadingPS = false;
			}
		})();

	//	watches the 'job type details' dropdown and changes the route to display details for the selected job type
		$scope.$watch('vm.selectedJobType', function (val) {
			let path = `/ec-settings/general-config/job-types/${val}`;
			$location.path(path);
		});

	//	checks for valid pay schedules
		$scope.$watch('jobType._paySchedules', function (nv, ov) {
			if(!nv && nv === ov) { return;}
			hasValidPaySchedules(nv);
		});

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			toggleJTDhistory(){
				vm.showJTDhistory = !vm.showJTDhistory;
			},
			addJobTypeDetail: () => {
				var latestJTD = getLatestJTD();
				var jobTypeDetailObj = jobTypeDetail(latestJTD);
				var valid = hasValidJTDs();
				if(valid) {
					setupMinDates();
				}
				vm.newJobTypeDetail = jobTypeDetailObj;
			},
			addSchedule: () => {
				routeVm.addingPS = true;
				let newSchedule = createNewSchedule();
				routeVm.paySchedules.push(newSchedule);
				routeVm.paySchedules.sort(logic.byMoreRecent)
				disableEditOnAdd(newSchedule);
			},

			cancelEditDetails: () => {
				routeVm.jobType._jobTypeDetails[0].$revert();
				vm.editDetails = false;
			},

			saveDetails: ssAsync(function* (){
				try {
					vm.savingDetails = true;
					yield routeVm.jobType._jobTypeDetails[0].$save().then(() => {
						ssStorage.clear(`${orgId}_jobTypes`);
					});
					ssToastHlp.success(``, 'Job Type Details Saved', false);
					vm.editDetails = false;
				} catch (error) {
					ssToastHlp.error(``, 'Error while saving Job Type Details', false);
					$log.error('Error while saving Job Type Details', error);
				} finally {
					vm.savingDetails = false;
				}
			}),

			savePaySchedule: (ps) => {
				checkSchedule(ps).then(() => {

					ps.isSaving = true;
					setActiveSteps(ps);
					routeVm.forceChangeToPayLanes(ps);

					whatToSave()().then(() => {
					 	ssToastHlp.success(``, `Pay Schedule "${ps._name}"" saved`, false);
					 	ssStorage.clear(`${orgId}_jobTypes`);
						ps.isEditing = false;
						ps.showDetails = ps.isCurrent;
						routeVm.addingPS = false;
						routeVm.fetchJobType(false, true);
					}).catch((error) => {
						ssToastHlp.error(``,`Error while saving Pay Schedule "${ps._name}"`, false);
						$log.error(`Error while saving Pay Schedule "${ps._name}"`, error);
					}).finally(() => {
						ps.isSaving = false;
						delete ps.isTemp;
					});

					function whatToSave(){
						// if the pay schedule has $save, it is an existing JSONApi Object and can save itself.
						if(ps.$save) {
							return function() {
								return ps.$save();
							};
						} else {
							// the pays schedule doesn't have $save and is therfore not a JSONApi Object, and is a new pay schedule.
							//	go up a level and have the collection save itself (and its children)
							return function() {
								return routeVm.paySchedules.$save(false);
							};
						}
					}

				});
			},
			saveJTD: function(jtd){
				ssAsync(function* () {
					try {
						jtd.isProcessing = true;
						if(jtd.id <= 0){
							routeVm.jobType._jobTypeDetails.push(jtd);
							yield routeVm.jobType._jobTypeDetails.$save();
							vm.newJobTypeDetail = null;
						}else{
							yield jtd.$save();
							if(jtd.isFuture) {jtd.expanded = false;}
						}
						jtd.editMode = false;
						routeVm.fetchJobType(true, false);
						ssToastHlp.success(``, `Job Type Detail saved`, false);
					} catch (e) {
						ssToastHlp.error(``,`Error while saving Job Type detail`, false);
						$log.error(`Error while saving Job Type detail`, e);
					} finally {
						jtd.isProcessing = false;
					}
				})();
			}
		};

		function hasValidJTDs() {
			return getValidJTDs(routeVm.jobType._jobTypeDetails).length > 0;
		}

		function setupMinDates(_pastJTDs = null, _currentJTD = null, _futureJTDs= null){
			var jobTypeDetails = getValidJTDs(routeVm.jobType._jobTypeDetails);
			logic.calculateDateBoundaries({ list: jobTypeDetails });
		}

		function getLatestJTD() {
			var valid = hasValidJTDs();
			if(valid){
				return routeVm.jobType._jobTypeDetails
						.reduce((prev, curr) => {
					return prev._fromDateUtc > curr._fromDateUtc ? prev : curr;
				}, routeVm.jobType._jobTypeDetails[0]);
			}else{
				return null;
			}
		}

	//	Pre-check to saving a schedule
		function checkSchedule(schedule) {

			var deferred = $q.defer();
			var errors = [];

			let prevSchedule = getPreviousSchedule(schedule);
			let nextSchedule = getNextSchedule(schedule);

			if(schedule._fromDateUtc > schedule._toDateUtc) {
				errors.push('This Pay Schedule cannot have a start date that is later than its end date.');
			} else {
				if(prevSchedule) {

					let errType, showLaneError, showStepError;

					if(schedule._payLanes.length < prevSchedule._payLanes.length) {
						showLaneError = true;
						errType = 'Lanes';
					}
					if(schedule._payLanes[0].paySteps.length < prevSchedule._payLanes[0].paySteps.length) {
						showStepError = true;
						errType = 'Steps';
					}

					if(showLaneError && showStepError) {
						errType = 'Lanes & Steps';
					}

					if(showLaneError || showStepError) {
						errors.push(`This Pay Schedule cannot have less ${errType} <br />than the previous Pay Schedule.`);
					}
				}

				if(nextSchedule) {

					let errType, showLaneError, showStepError;

					if(nextSchedule._payLanes.length < schedule._payLanes.length) {
						showLaneError = true;
						errType = 'Lanes';
					}
					if(nextSchedule._payLanes[0].paySteps.length < schedule._payLanes[0].paySteps.length) {
						showStepError = true;
						errType = 'Steps';
					}

					if(showLaneError && showStepError) {
						errType = 'Lanes & Steps';
					}

					if(showLaneError || showStepError) {
						errors.push(`This Pay Schedule cannot have more ${errType} <br />than the following Pay Schedule.`);
					}
				}
			}

			if(errors.length) {

				errors = errors.map((e) => {
					return $sce.trustAsHtml(e);
				});

				ssModalSvc.showStandard({
					errors: errors,
					title: `<h2>Invalid Pay Schedule</h2>`,
					content: '<ul class="error-list"><li ng-repeat="error in errors" ng-bind-html="error"></li></ul>',
					showPrimary: false,
					secondaryText: 'Ok',
				}).then(() => {
					deferred.reject();
				});

			} else {
				deferred.resolve();
			}

			return deferred.promise;

		}

	//	Sets the status of the pay rates based on value
		function setActiveSteps(schedule) {
			schedule._payLanes.forEach(lane => {
				lane.paySteps.forEach(step => {
					var value = step.payRate;
					// if the payRate is not filled in, set to 0 but allow it to be active.
					if (value === '') {
						step.payRate = 0;
						step.active = true;
						return;
					}
					if (typeof value === 'string') {
						// try to pull out a number from our value
						value = parseFloat(step.payRate);
					}
					// this step is inactive if the rate is 0
					step.active = (value >= 0);
				});
			});
		}

	//	gets the user defined job types
		function getJobTypes() {
			ssAsync(function* () {
				try {
					const allJobTypes = yield ssJobTypesApi.query({ limit: 500 });

					vm.jobTypeOptions = allJobTypes.map(function (item) {
						var obj = {};
						obj['name'] = item._name;
						obj['value'] = item.id;
						return obj;
					});
				} catch (e) {
					console.error('Error getting job Types', e);
				}
			})();
		}

	//	receives a date an increments it by one day
		function nextDay(serverFromDate){
			let startDate = ssDateByOrgHlp.utcIsoToRelativeMoment(serverFromDate, org);
			let newStartDate = startDate.clone().add(1, 'days');
			return newStartDate;
		}

		function createNewSchedule() {

			let latestSchedule;
			if(vm.hasValidPaySchedules) {
				latestSchedule = getLatestSchedule(routeVm.paySchedules);
			}
			var schedule = {
				'id': 0,
				'_name': null,
				'_fromDateUtc': ssDateByOrgHlp.todaysStartRelativeTo(org).toISOString(),
				'_toDateUtc': ssDateHlp.foreverFuture(),
				'_externalId': null,
				'_payRateFrequency': 0,
				'_payLanes': [],
				'isTemp': true,
				'isDefault': false
			};
			if (latestSchedule) {
				schedule._fromDateUtc = nextDay(latestSchedule._fromDateUtc).toISOString();
				//	copy over the lanes and steps from the latest schedule
				latestSchedule._payLanes.forEach(lane => {
					var newLane = {
						'name': lane.name,
						'paySteps': []
					};

					lane.paySteps.forEach(step => {
						var newStep = {
							'name': step.name,
							'active': step.active,
							'payRate': step.payRate
						};
						newLane.paySteps.push(newStep);
					});

					schedule._payLanes.push(newLane);

				});
			} else {
				schedule._payLanes.push({
					'name': 'Lane 1',
					'paySteps': [{
						'name': 'Step 1',
						'active': true,
						'payRate': 0
					}]
				});
			}

			extendSchedule(schedule);
			routeVm.updateLaneWidths(schedule);
			setMinValues(schedule);

			return schedule;
		}


	//	Find the active schedule
		function getCurrentPaySchedule(schedules) {
			const now = moment(ssDateByOrgHlp.todaysEndRelativeTo(org));
			return schedules.find((schedule) => {
				return ssDateByOrgHlp.utcIsoToRelativeMoment(schedule._fromDateUtc, org).isSameOrBefore(now) &&
				ssDateByOrgHlp.utcIsoToRelativeMoment(schedule._toDateUtc, org).isAfter(now)
			});
		}

	// Find the closest previously dated schedule of the target schedule
        function getPreviousSchedule(schedule) {
			if(schedule.isCurrent) { return schedule;}          
			let start = ssDateByOrgHlp.utcIsoToRelativeMoment(schedule._fromDateUtc, org);
			return routeVm.paySchedules.filter((ps) => {
					let psTime = ssDateByOrgHlp.utcIsoToRelativeMoment(ps._fromDateUtc, org);
					return !ps.isDefault && psTime.isBefore(start);
				}).reduce((prev, curr) => {
					if(prev === 0) { return curr; }
					let prevFromDateUtc = ssDateByOrgHlp.utcIsoToRelativeMoment(prev._fromDateUtc, org);
					let currentFromDateUtc = ssDateByOrgHlp.utcIsoToRelativeMoment(curr._fromDateUtc, org)
					return prevFromDateUtc.isAfter(currentFromDateUtc) ? prev : curr;
				}, 0);
		}

    //  Find the closest previously dated schedule of the target schedule
        function getNextSchedule(schedule) {
			let start = ssDateByOrgHlp.utcIsoToRelativeMoment(schedule._fromDateUtc, org);
			return routeVm.paySchedules.filter((ps) => {
					let psTime = ssDateByOrgHlp.utcIsoToRelativeMoment(ps._fromDateUtc, org);
					return psTime.isAfter(start);
				}).reduce((prev, curr) => {
					if(prev === 0) { return curr; }
					let prevFromDateUtc = ssDateByOrgHlp.utcIsoToRelativeMoment(prev._fromDateUtc, org);
					let currentFromDateUtc = ssDateByOrgHlp.utcIsoToRelativeMoment(curr._fromDateUtc, org)
					return prevFromDateUtc.isBefore(curr._fromDateUtc) ? prev : curr;
				}, 0);
		}


	//	Find the latest schedule based on the 'from' date of each schedule
		function getLatestSchedule(schedules) {
			return schedules.reduce((prev, curr) => {
				return prev._fromDateUtc > curr._fromDateUtc ? prev : curr;
			}, schedules[0]);
		}

	//	method to only allow one schedule to be edited at a time
		function disableEditOnAdd(schedule) {
			routeVm.paySchedules.forEach((ps) => {
				if(ps !== schedule && ps.isEditing && ps.$revert) {
					routeVm.forceChangeToPayLanes(ps);
					ps.$revert();
					ps.isEditing = false;
					ps.showDetails = ps.isCurrent;
				}
			});
		}

	//	checks to see if any schedules exist beside the default
		function hasValidPaySchedules(schedules) {
			return schedules.filter((ps) => {
				return !ps.isDefault;
			}).length;
		}

	//	set the minimum lane, step & start date values
		function setMinValues(schedule) {
		//	set the minimum start date
		if (schedule && vm.hasValidPaySchedules) {
			let currentStartDate = schedule ? ssDateByOrgHlp.utcIsoToRelativeMoment(schedule._fromDateUtc, org) : ssDateByOrgHlp.todaysStartRelativeTo(org);
			schedule.dtOptions = { minDate: currentStartDate.toDate() };
		}
		//	set the minimum lane and step values
			let prevSchedule = getPreviousSchedule(schedule);
			schedule.minLanes = prevSchedule ? prevSchedule._payLanes.length : 1;
			schedule.minSteps = prevSchedule ? prevSchedule._payLanes[0].paySteps.length : 1;
		}

	//	Extend the schedule with needed props
		function extendSchedule(schedule) {
		//	Find and ignore the default schedule
			if(schedule.isDefault) { return;}
		//	Determine the schedule's state
			schedule.isCurrent = schedule.id !== 0 && ssDateByOrgHlp.isCurrent({obj: schedule, org});
			schedule.isFuture = ssDateByOrgHlp.isInFuture({obj: schedule, org});
			schedule.isPast = ssDateByOrgHlp.isInPast({obj: schedule, org});
		//	schedule is in edit mode if it's being added
			schedule.isEditing = schedule.isTemp ? true : false;
		//	show the pay grid if the schedule is being added or active
			schedule.showDetails = schedule.isTemp || schedule.isCurrent;
		//	only show the end date if it exists and is not forever future
			schedule.showEndDate = schedule._toDateUtc && !ssDateHlp.isForeverFuture(schedule._toDateUtc);
		//	retrieve the saved pay rate name
			schedule.payRateFrequency = ssEnum.salaryType.byValue(schedule._payRateFrequency);
		}

	//	reassigns the pay lanes to a copy of itself to allow JSON API to detect a change
		routeVm.forceChangeToPayLanes = (schedule) => {
			schedule._payLanes = schedule._payLanes.slice();
		};

	//	calculate lane widths based on the number of lanes
		routeVm.updateLaneWidths = (schedule) => {
			schedule.colStyles = {};
			return schedule.colStyles['width'] = `calc(100% / ${schedule._payLanes.length})`;
		};

	//	fetch/refresh the data
		routeVm.fetchJobType = function(processJTD = true, processPS = true){

			var _defer = $q.defer();

			ssAsync(function* () {
				try {
					routeVm.jobType = $scope.jobType = yield ssJobTypesApi.get({
						id: $stateParams.jobTypeId,
						include: 'JobTypeDetails,PaySchedules'
					});

					if(processJTD){
						processJobTypeDetails(routeVm.jobType._jobTypeDetails);
					}

					if(processPS) {
						routeVm.paySchedules = routeVm.jobType._paySchedules;
						currentPaySchedule = getCurrentPaySchedule(routeVm.paySchedules);
						routeVm.paySchedules.forEach(extendSchedule);
						routeVm.paySchedules.forEach(routeVm.updateLaneWidths);
						logic.calculateDateBoundaries({ list: routeVm.paySchedules });
						vm.hasValidPaySchedules = hasValidPaySchedules(routeVm.paySchedules);
					}
					getJobTypes();
				} catch (e) {
					ssToastHlp.error(``, `Error Retrieving Job Type Details`, false);
					console.error('ERROR', e);
				} finally {
					_defer.resolve();
				}
			})();
			return _defer.promise;
		};

		function jobTypeDetail(jtd){
			const todayRelativeToStr = ssDateByOrgHlp.todaysStartRelativeTo(org, true);
			let fromDateUtc = (!jtd || ssDateHlp.isForeverPast(jtd._fromDateUtc)) ? todayRelativeToStr : nextDay(jtd._fromDateUtc).toISOString();
			let minDate = (!jtd || ssDateHlp.isForeverPast(jtd._fromDateUtc)) ? null : new Date(fromDateUtc);
			return {
				_fromDateUtc: fromDateUtc,
				_toDateUtc: ssDateHlp.foreverFuture(),
				_workDaysPerYear: (!jtd) ? 0 : jtd._workDaysPerYear,
				_payPeriodsPerYear: (!jtd) ? 0 : jtd._payPeriodsPerYear,
				_hoursPerDay: (!jtd) ? 0 : jtd._hoursPerDay,
				_fTE: (!jtd) ? 0 : jtd._fTE,
				_vacationDaysPerSchoolYear: (!jtd) ? 0 : jtd._vacationDaysPerSchoolYear,
				_sickDaysPerSchoolYear: (!jtd) ? 0 : jtd._sickDaysPerSchoolYear,
				_personalDaysPerSchoolYear: (!jtd) ? 0 : jtd._personalDaysPerSchoolYear,
				_paidHolidaysPerSchoolYear: (!jtd) ? 0 : jtd._paidHolidaysPerSchoolYear,
				isCurrent:false,
				isFuture:true,
				isPast:false,
				editMode: true,
				id: 0,
				expanded: true,
				dtOptions:{
					minDate
				}
			};
		}

		function getValidJTDs(_jobTypeDetails){
			return _jobTypeDetails.filter(x =>
					!ssDateHlp.isForeverPast( x._fromDateUtc) ||
					(x._fTE !== 0 ||
					x._hoursPerDay !== 0 ||
					x._workDaysPerYear !== 0 ||
					x._payPeriodsPerYear !== 0 ||
					x._paidHolidaysPerSchoolYear !== 0 ||
					x._sickDaysPerSchoolYear !== 0 ||
					x._personalDaysPerSchoolYear !== 0 ||
					x._vacationDaysPerSchoolYear !== 0));
		}

		function getFutureJTDs(_jobTypeDetails){
			var futureJTDs = _jobTypeDetails
								.filter(x => ssDateByOrgHlp.isInFuture({ obj: x, org }))
								.sort((a, b) => { return new Date(b._toDateUtc) - new Date(a._toDateUtc); });
			if(!futureJTDs.length) { return []; }
			return futureJTDs;
		}

		function getCurrentJTD(_jobTypeDetails){
			var currentJTD = _jobTypeDetails.find(x => ssDateByOrgHlp.isCurrent({ obj: x, org }));
			if(!currentJTD) { return null; }
			return currentJTD;
		}

		function getPastJTDs(_jobTypeDetails){
			var pastJTDs = _jobTypeDetails
							.filter(x => ssDateByOrgHlp.isInPast({ obj: x, org }))
							.sort((a, b) => { return new Date(b._toDateUtc) - new Date(a._toDateUtc); });;
			if(!pastJTDs.length) { return []; }
			return pastJTDs;
		}



		function processJobTypeDetails(_jobTypeDetails){
			var jobTypeDetails = getValidJTDs(_jobTypeDetails);
			vm.currentJobTypeDetail = getCurrentJTD(jobTypeDetails);
			vm.futureJobTypeDetails = getFutureJTDs(jobTypeDetails);
			vm.pastJobTypeDetails =  getPastJTDs(jobTypeDetails);
			setupMinDates(vm.pastJobTypeDetails, vm.currentJobTypeDetail, vm.futureJobTypeDetails);
		}

	}
}





// #-- END Segment (/general-config/job-types/job-type-detail/segment-job-type-detail) --# //
// #-------------------------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentGeneralConfigJobTypesJobTypeDetailJobTypeDetail', () => new JobTypeDetail('job-type-detail'));
