/*@ngInject*/
function logic() {
	// logic api
	function findEmployeesInGroups(groups, employees){
		let employeeAndGroupsItsAlreadyIn = [];
		employees.forEach(employee => {
			let employeeGroups = groups.filter(grp => {
				return grp._employeeIds.indexOf(employee.id) > -1;
			});
			let groupNames = employeeGroups.map(x => x._groupName).join();
			employeeAndGroupsItsAlreadyIn.push({ employee, groupNames, groups: employeeGroups });
		})
		return employeeAndGroupsItsAlreadyIn;
	}

	const fullName = e => e.fullName;
	const anyUserBelongsToOtherGroups = (employeesInGroups) => employeesInGroups.filter(g => g.groups.length).length > 0;
	const flattenTheGroups = (employeesInGroups) => employeesInGroups.reduce((prev, curr) => prev.concat(curr.groups),[]);

	return {
		fullName,
		flattenTheGroups,
		anyUserBelongsToOtherGroups,
		findEmployeesInGroups
	};

}
angular.module('ec-settings-segment-logic').factory('segmentPermissionsGroupsGroupDetailGroupMembersContentLogic', logic);
