const _hlp = {
	// #---------------------------------------# //
	// #---- Helper (createPermissionsHlp) ----# //

	securityScope() {
		return angular.extend({}, {
			_permissionProfileId: null,
			_locationAdministrationContext: 1,
			_locationIds: [],
			_jobTypeAdministrationContext: 3,
			_jobTypeIds: [],
			_departmentAdministrationContext: 0,
			_departmentIds: [],
			_employeeAdministrationContext: 1,
			_employeeIds: [],
			permissionProfile: {
				_permissionProfileName: '' 
			}
		});
	},

	permissionSet() {
		return angular.extend({}, {
			_protectedContextRegistrationId: null, // reference to the context registration
			_permissions: {
				resourceName: null, // maps to context registration resourceName
				objectActions: [], // create, archive, etc
				propertyActions: [], // propertyAction
				nestedResources: [] // permission sets
			}
		});
	},

	permission() {
		return angular.extend({}, {
			resourceName: null, // maps to registration resourceName
			objectActions: [], // create, archive, etc
			propertyActions: [], // propertyAction
			nestedResources: [] // permission sets
		});
	},

	propertyAction() {
		return angular.extend({}, { key: '', value: 0 });
	}

	// #-- END Helper (createPermissionsHlp) --# //
	// #---------------------------------------# //
};

function createPermissionsHlp() {return _hlp;}
angular.module('ec-settings-helpers').factory('createPermissionsHlp', createPermissionsHlp);
