import { Component } from 'base-component';
// #--------------------------------------------# //
// #---- Component (cmp-manage-credentials) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
};

class ManageCredentials extends Component {
	/*@ngInject*/
	controller($scope, $window, ssAsync, ssObjectHlp, ssCredentialDataAccessSvc, ssCredentialBusinessEngineSvc, ssDateHlp, ssDateByOrgHlp, ssToastHlp, ssOrganizationSvc) {
		const { credentialConstants } = ssCredentialBusinessEngineSvc;
		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope); // jshint ignore:line
		var org = {};

		// view model for tracking state
		const vm = $scope.vm = {
			issuers: [],
			issuer: null,
			creds: [],
			isEditing: { value: false },
			isExporting: false,
			loading: false,
			issuer: -1,
			canAddEdit: !routeVm.isStateNational,
		};

		// Get the issuers for the page
		ssAsync(function* () {
			try {
				org = yield ssOrganizationSvc.getActiveOrg();
				let type = routeVm.isStateNational ? credentialConstants.STATE_NATIONAL : credentialConstants.DISTRICT_CUSTOM;

				vm.issuers = {};
				vm.loading = true;

				if (routeVm.isStateNational) {
					if (routeVm.credentialType === 'certificate-licenses') {
						// **** CERTIFICATES & LICENSES ****
						vm.issuers = yield ssCredentialDataAccessSvc.getIssuersByCredentialType(0);
					}
					else {
						// **** CLEARANCES ****
						vm.issuers = yield ssCredentialDataAccessSvc.getIssuersByCredentialType(1);
					}
				}
				else {
					// **** CUSTOM ****
					vm.issuers = yield ssCredentialDataAccessSvc.getIssuers(type);
				}

				// If we have an org with a state, find the first matching issuer and pre-select it
				//	we don't need to worry about something coming in pre-selected as this isn't 
				//	an option for this view
				vm.issuer = vm.issuers[0];
				if (org && ssObjectHlp.glean(org, _=>_._address.state.length, false)) {
					// find the first matching issuer
					let i = vm.issuers.find((issr) => issr._stateId === org._address.state);
					vm.issuer = i || vm.issuers[0];
					vm.updatedUtcTxt = getIssuerText();
				}

				sort(vm.issuers);

			} catch (error) {
				ssToastHlp.error(error.message, 'Error', false);
			} finally {
				vm.loading = false;
			}
		})();

		$scope.$watch('vm.issuer', nv => {
			// if the issuer name is a single character skip (we have alphabetical)
			if (nv == null || nv === -1) { return; }

			vm.updatedUtcTxt = getIssuerText();
			ssAsync(function* () {
				try {
					vm.loading = true;
					vm.creds = yield ssCredentialDataAccessSvc.getCredentialsByIssuer(vm.issuer.id);
					vm.creds.forEach(cred => cred.isEditing = false);
					sort(vm.creds);
				} catch (error) {
					ssToastHlp.error(error.message, 'Error', false);
				} finally {
					vm.loading = false;
				}
			})();
		});

		/**
		 * Saves a local data stream as a file
		 * @param {string} dataUri - The data/url of the API response
		 * @param {*} filename  - The name to assign to the file should the browser support the download attribute
		 */
		function saveLocalExport(dataUri, filename) {
			if (!filename) filename = 'export.csv';
			if (!dataUri) throw new Error("The dataUri is null and the export cannot continue.");

			// Create the dummy anchor and click it to start the download.  This is only doable because
			//	we are able to base64 encode the API result into a string and save that using the browser FileReader API
			var a = document.createElement("a");
			a.style = "display: none";
			a.href = dataUri;
			a.download = filename;

			document.body.appendChild(a);
			a.click();
			document.body.removeChild(a);
		}

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			exportCredentials() {
				// Export Credentials to CSV
				ssCredentialDataAccessSvc.downloadCredentialsCsv(vm.issuer.id).then(response => {
					if (response && response.data) {
						// Create the dummy anchor and click it to start the download.
						var a = document.createElement("a");
						a.style.display = 'none';
						a.href = response.data.data.attributes.string;

						document.body.appendChild(a);
						a.click();
						document.body.removeChild(a);
					} else {
						console && console.error && console.error('invalid response from employee credential endpoint. Unable to save to csv.');
						ssToastHlp.error('Unable to save export.', 'Error', false);
					}
					vm.isExporting = false;
				});
			},
			exportSubjects() {
				// Export Subjects to CSV
				vm.isExporting = true;

				// Get the CSV as post data and push it into a FileReader
				ssCredentialDataAccessSvc.downloadSubjectsCsv(vm.issuer.id).then(response => {
					if (response && response.data) {
						// Create the dummy anchor and click it to start the download.
						var a = document.createElement("a");
						a.style.display = 'none';
						a.href = response.data.data.attributes.string;

						document.body.appendChild(a);
						a.click();
						document.body.removeChild(a);
					} else {
						console && console.error && console.error('invalid response from employee credential endpoint. Unable to save to csv.');
						ssToastHlp.error('Unable to save export.', 'Error', false);
					}
					vm.isExporting = false;
				});
			},
			add() {
				vm.newCred = {
					_name: '',
					_issuerId: vm.issuer.id,
					_credentialTypeId: credentialConstants.OTHERS,
					isEditing: true
				};
				vm.isEditing.value = true;
			},
			onNewCancel() {
				vm.newCred = null;
				vm.isEditing.value = false;
			},
			onAdd(newCred) {
				vm.creds.push(newCred);
				vm.newCred = null;
			},
			onDelete(credential) {
				vm.creds = vm.creds.filter(cred => cred.id !== credential.id);
				vm.isEditing.value = false;
			},
			onSave() {
				vm.isEditing.value = false;
				sort(vm.creds);
			},
		};
		
		function getIssuerText() {
			if (vm.issuer == null || vm.issuer === -1) { return ''; }
			let orgName = routeVm.isStateNational ? 'Frontline Education' : org._name;
			let d = ssDateHlp.isForeverFuture(vm.issuer._updatedUtc) ?
				`Added ${ssDateByOrgHlp.utcIsoToRelativeMoment(vm.issuer._createdUtc, org).format('MM/DD/YYYY')}` :
				`Updated ${ssDateByOrgHlp.utcIsoToRelativeMoment(vm.issuer._updatedUtc, org).format('MM/DD/YYYY')}`;
			return `${d} by ${orgName}`;
		}

		function sort(items) {
			items.sort((a, b) => {
				// ignore case
				var nameA = a._name.toUpperCase();
				var nameB = b._name.toUpperCase();
				if (nameA < nameB) {
					return -1;
				} else if (nameA > nameB) {
					return 1;
				}
				// names must be equal
				return 0;
			});
		}
	}
}

// #-- END Component (cmp-manage-credentials) --# //
// #--------------------------------------------# //

angular.module('ec-settings-components').directive('cmpManageCredentials', () => new ManageCredentials(props, 'manage-credentials'));
