import { Segment } from 'base-segment';
// #--------------------------------------------------------------------------------------# //
// #---- Segment (/permissions/groups/group-detail/profile-and-scope/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, ssArrayHlp, createPermissionsHlp) {
		const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentPermissionsGroupsGroupDetailProfileAndScopeContentLogic'); // eslint-disable-line

		let group = $scope.group;

		const vm = $scope.vm = {
			dim: false,
			disableDim: true,
			permissions: {},
			disableAdd: null
		};

		const go = $scope.go = {

			addScope() {
				vm.disableAdd = false;
				let securityScope = createPermissionsHlp.securityScope();
				group._securityScopes.push(securityScope);
			},

			removeScope(securityScope) {
				if (!securityScope.id) {
					let Ind = group._securityScopes.indexOf(securityScope);
					group._securityScopes.splice(Ind, 1);
				}
				$scope.loadData();
			}
		};

		$scope.loadData = function() {

			if (group._securityScopes == null) {
				group._securityScopes = [];
			}

			// Adds a default security scope to the group if none are assigned
			if (!group._securityScopes.length) {
				go.addScope();
			}

			// shows the 'Add' button only if there's at least one saved security group
			vm.disableAdd = !!group._securityScopes.filter(s => s.permissionProfile.id != null).length;

			// determine the profiles that are currently in use so that we can use that list to check against when a duplicate is selected
			routeVm.uniqueProfiles = getUniqueIds(group._securityScopes, 'permissionProfile');

			// If we have profiles, set up the profile table
			if (routeVm.uniqueProfiles.length > 0) {

				if (routeVm.uniqueProfiles.length > 1) {
					vm.disableDim = false;
				}

				$scope.permissionsVm.contexts.forEach(context => {
					vm.permissions[context.id] = [];

					let registration = context._protectedResourceRegistration;
					routeVm.uniqueProfiles.forEach(profile => {
						if (profile == null) { return; }

						let permissionSet = profile._permissionSets.find(s => s._protectedContextRegistrationId === context.id);

						if (permissionSet == null) {
							permissionSet = createPermissionsHlp.permissionSet();
							permissionSet._protectedContextRegistrationId = context.id;
							permissionSet._permissions.resourceName = registration.resourceName;

							profile._permissionSets.push(permissionSet);
						}

						vm.permissions[context.id].push(permissionSet._permissions);
					});
				});

			}
		};

		// filters out any temporary security scopes and returns the unique profiles among the results
		function getUniqueIds(collection, property) {
			return ssArrayHlp.unique(collection.filter(item => item[property].id != null).map(item => {
				if (item[property] && item[property].id != null) {
					return item[property];
				}
			}));
		}

		$scope.loadData();

	}
}

// #-- END Segment (/permissions/groups/group-detail/profile-and-scope/segment-content) --# //
// #--------------------------------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentPermissionsGroupsGroupDetailProfileAndScopeContent', () => new Content(
	'content'));
