import { Component } from 'base-component';
// #-----------------------------------------# //
// #---- Component (cmp-add-edit-issuer) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	issuer: '=',
	onNewCancel: '&',
	onAdd: '&',
	onDelete: '&',
	onSave: '&',
	singleEditing: '=', // used to only allow editing one issuer at a given time
};

class AddEditIssuer extends Component {
	/*@ngInject*/
	controller($scope, ssAsync, ssCredentialsApi, ssToastHlp, ssValidationHlp, ssDateByOrgHlp, ssModalSvc, messagingSvc) {
		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope); // jshint ignore:line
		var org = routeVm.org;

		// view model for tracking state
		const vm = $scope.vm = {
			isNew: $scope.issuer.id == null,
			updatedUtcTxt: $scope.issuer._updatedUtc != null ? ssDateByOrgHlp.utcIsoToRelativeMoment($scope.issuer._updatedUtc, org).format('MM/DD/YYYY') : null,
			isSaving: false,
			canAddEdit: !routeVm.isStateNational,
		};

		$scope.validator = {
			name: ssValidationHlp.string({ minLength: 1, maxLength: 100, required: true }, [
				(val, field) => {
					let ret = true;
					','.split('').forEach(c => {
						if (ret !== true) { return; }
						if (val.indexOf(c) >= 0) { ret = `${field} cannot contain the '${c}' character`; }
					});
					return ret;
				},
				(val, field) => {
					if (val.trim().length === 0) { return `${field} is required`; }
					return true;
				}
			])
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			edit() {
				ssModalSvc.showDestructive({
					title: messagingSvc.getPlaceholderMessage('credentials', 'editConfirmationTitle', ['Issuer']),
					content: messagingSvc.getPlaceholderMessage('credentials', 'editConfirmationMessage', ['issuer']),
					primaryText: messagingSvc.getMessage('general', 'continueEditing'),
					secondaryText: messagingSvc.getMessage('general', 'cancel'),
				}).then(() => {
					$scope.singleEditing = true;
					$scope.issuer.isEditing = true;
				});
			},
			cancel() {
				if (vm.isNew) {
					$scope.onNewCancel();
				} else {
					$scope.issuer.$revert();
				}
				$scope.singleEditing = false;
				$scope.issuer.isEditing = false;
			},
			remove() {
				ssModalSvc.showDestructive({
					title: messagingSvc.getPlaceholderMessage('credentials', 'deleteConfirmationTitle', ['Issuer']),
					content: messagingSvc.getPlaceholderMessage('credentials', 'deleteConfirmationMessage', ['issuer']),
					primaryText: messagingSvc.getMessage('general', 'delete'),
					secondaryText: messagingSvc.getMessage('general', 'cancel'),
				}).then(() => {
					if (vm.isSaving) { return; }
					vm.isSaving = true;
					ssAsync(function*() {
						try {
							let res = yield $scope.issuer.$remove();
							if (res != null) {
								ssToastHlp.success('', `Issuer "${$scope.issuer._name}" Removed`, false);
								$scope.onDelete({ $issuer: $scope.issuer });
							}
							vm.isSaving = false;
						} catch (error) {
							ssToastHlp.error(error.message, 'Error', false);
							vm.isSaving = false;
						}
					})();
				});
			},
			save() {
				if (vm.isSaving) { return; }
				vm.isSaving = true;
				ssAsync(function*() {
					$scope.issuer._name = $scope.issuer._name.trim();
					let res;
					if (vm.isNew) {
						$scope.issuer._organizationId = org.id;
						res = yield ssCredentialsApi.addIssuer({}, $scope.issuer);
					} else {
						res = yield $scope.issuer.$save();
					}

					if (res != null) {
						if (vm.isNew) {
							res.isEditing = false;
							// this calls the parent component to add the new issuer to the list
							// and null the newIssuer ref to remove this component
							$scope.onAdd({ $newIssuer: res });
						} else {
							vm.updatedUtcTxt = ssDateByOrgHlp.utcIsoToRelativeMoment($scope.issuer._updatedUtc, org).format('MM/DD/YYYY');
							$scope.issuer.isEditing = false;
						}
						if ($scope.onSave) { $scope.onSave(); }
						ssToastHlp.success('', `Issuer "${$scope.issuer._name}" Saved`, false);
					}
					vm.isSaving = false;
				})();
			}
		};
	}
}

// #-- END Component (cmp-add-edit-issuer) --# //
// #-----------------------------------------# //

angular.module('ec-settings-components').directive('cmpAddEditIssuer', () => new AddEditIssuer(props, 'add-edit-issuer'));
