import {Component} from 'base-component';
// #-----------------------------------------# //
// #---- Component (cmp-infographic-svg) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	forceLoad: '@?'
};

class InfographicSvg extends Component {
	/*@ngInject*/
	controller($scope, ssStorage) {
		const vm = $scope.vm = { // jshint ignore:line
			infographic: true
		};

		if ($scope.forceLoad !== 'true') {
			vm.infographic = ssStorage.load('permissions_infographic') ? !ssStorage.load('permissions_infographic').displayed : true;
		}

		const go = $scope.go = { // jshint ignore:line
			dismiss() {
				ssStorage.save('permissions_infographic', {
					displayed: true
				}, {
					persistent: true,
					expires: null
				});
				vm.infographic = !ssStorage.load('permissions_infographic').displayed;
			}
		};
	}
}

// #-- END Component (cmp-infographic-svg) --# //
// #-----------------------------------------# //

angular.module('ec-settings-components').directive('cmpInfographicSvg', () => new InfographicSvg(props, 'infographic-svg'));
