/*@ngInject*/
function logic(ssFlagSvc, enumPropertyAction, propertyActionLabelSvc) {
    /**
     *
     * @param {boolean} input.areOnAViewSelfPage
     * @param {boolean} input.editResourceActionIsNowAllowed
     * @param {boolean} input.viewResourceActionIsNowAllowed
     * @param {number} input.actionLevelEnumValue
     */
    function shouldDimBasedOnViewSelf({areOnAViewSelfPage, editResourceActionIsNowAllowed, viewResourceActionIsNowAllowed, actionLevelEnumValue} = input){
        let result = false;
        // Guard condition
        if(!areOnAViewSelfPage){
            return false
        }
        // END Guard condition
        if(viewResourceActionIsNowAllowed && propertyActionLabelSvc.isAtLeastViewable(actionLevelEnumValue)){
            result = true;
        }
        if(editResourceActionIsNowAllowed && propertyActionLabelSvc.isAtLeastEditable(actionLevelEnumValue)){
            result = true;
        }
        return result;
    }

    /**
     *
     * @param {*} property - a permission object. See the tests for the shape of the data
     */
    function filterActionsByAlwaysAndNever(property){
        if(!property){
            throw new Error("forgot the input");
        }
        /*
            ALWAYS	NEVER	OUTCOME
            T		T		INVALID (Throw exception server is sending wrong prop config)
            T		F		T (Action available)
            F		T		F (Action not available)
            F		F		T (Action available)
        */
        let allowedActions = [enumPropertyAction.enumByValue(enumPropertyAction.notVisible)];
        if(property.alwaysEditable && property.neverEditable) { throw new Error('Invalid server property combination') }
        else if(property.alwaysEditable && !property.neverEditable) { allowedActions.push(enumPropertyAction.enumByValue(enumPropertyAction.edit)); }
        else if(!property.alwaysEditable && !property.neverEditable) { allowedActions.push(enumPropertyAction.enumByValue(enumPropertyAction.edit)); }
        else { /* nothing */ }

        if(property.alwaysVisible && property.neverVisible) { throw new Error('Invalid server property combination') }
        else if(property.alwaysVisible && !property.neverVisible) { allowedActions.push(enumPropertyAction.enumByValue(enumPropertyAction.view)); }
        else if(!property.alwaysVisible && !property.neverVisible) { allowedActions.push(enumPropertyAction.enumByValue(enumPropertyAction.view)); }
        else { /* nothing */ }

        return allowedActions;
    }

    function isARedactablePermission(permissionIdStr){
        // list of row ids where we should show redacted property action options
		let redactablePermissionIds = [
			'Employee_Property_DriversLicense',
			'Employee_Property_Age',
			'Employee_Property_BirthDate',
			'Employee_Property_SeniorityDate',
			'Employee_Property_TenureDate'
        ];

        if (!ssFlagSvc.checkState('fc-emp-ui-secure-ssn')) {
            // push ssn to redactable if we are not using the new secure ssn feature
            redactablePermissionIds.push('Employee_Property_SocialSecurityNumber');
        }

        return redactablePermissionIds.includes(permissionIdStr);
    }

	// logic api
	return {
        shouldDimBasedOnViewSelf: shouldDimBasedOnViewSelf,
        filterActionsByAlwaysAndNever: filterActionsByAlwaysAndNever,
        isARedactablePermission: isARedactablePermission,
	};
}
angular.module('ec-settings-components-logic').factory('cmpProfileTableLogic', logic);
