import { Component } from 'base-component';
// #-----------------------------------------# //
// #---- Component (cmp-manage-subjects) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	issuer: '=',
	singleEditing: '=',
};

class ManageSubjects extends Component {
	/*@ngInject*/
	controller($scope, ssAsync, ssCredentialDataAccessSvc, ssDateByOrgHlp, ssToastHlp) {
		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope); // jshint ignore:line
		var org = routeVm.org; 

		// view model for tracking state
		const vm = $scope.vm = {
			subjects: [],
			loading: false,
			canAddEdit: !routeVm.isStateNational
		};
		
		$scope.$watch('issuer', nv => {
			if (nv == null) { return; }
			ssAsync(function*() {
				try {
					vm.loading = true;
					vm.subjects = yield ssCredentialDataAccessSvc.getSubjectsByIssuer(nv.id);
					vm.subjects.forEach(s => s.isEditing = false);
					sort(vm.subjects);
				} catch (error) {
					ssToastHlp.error(error.message, 'Error', false);
				} finally {
					vm.loading = false;
				}
			})();
		});

		// behavior that needs to be bound to the view
		const go = $scope.go = {
			add() {
				vm.newSubject = {
					_name: '',
					_issuerId: $scope.issuer.id,
					_updatedUtc: ssDateByOrgHlp.utcIsoToRelativeMoment(new Date().toISOString(), org).toISOString(),
					isEditing: true
				};
				$scope.singleEditing.value = true;
			},
			onNewCancel() {
				vm.newSubject = null;
				$scope.singleEditing.value = false;
			},
			onAdd(newSubject) {
				vm.subjects.push(newSubject);
				vm.newSubject = null;
			},
			onDelete(subject) {
				vm.subjects = vm.subjects.filter(s => s.id !== subject.id);
				$scope.singleEditing.value = false;
			},
			onSave() {
				$scope.singleEditing.value = false;
				sort(vm.subjects);
			},
		};

		function sort(items) {
			items.sort((a, b) => {
				// ignore case
				var nameA = a._name.toUpperCase();
				var nameB = b._name.toUpperCase();
				if (nameA < nameB) {
					return -1;
				} else if (nameA > nameB) {
					return 1;
				}
				// names must be equal
				return 0;
			});
		}
	}
}

// #-- END Component (cmp-manage-subjects) --# //
// #-----------------------------------------# //

angular.module('ec-settings-components').directive('cmpManageSubjects', () => new ManageSubjects(props, 'manage-subjects'));
