import { Component } from 'base-component';
// #------------------------------------# //
// #---- Component (cmp-grid-group) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	itemType: '@',
	endpoint: '&',
	config: '=',
	permissions: '=',
	onBeforeSave: '&', // Function which should return an object {bool: success, string: errorMessage}
	onBeforeCancel: '&'
};

class GridGroup extends Component {
	/*@ngInject*/
	controller($scope, $state, $q, $log, ssAsync, ssDebounce, ssEnum, ssToastHlp, ssStringHlp, ssValidationHlp, ssStorage, ssMultiInitiativeFeatureDetectorSvc) {

		$scope.validation = { // jshint ignore:line
			required: ssValidationHlp.required(),
			email: ssValidationHlp.email(),
			externalId: ssValidationHlp.string({ required: !ssMultiInitiativeFeatureDetectorSvc.isHRMSEnabled(), maxLength: 100 }),
			zip: ssValidationHlp.zip(),
			filteredItems: []
		};

		const pageSize = 50;
		let currentRequest = null;

		let sortOptions = null;

		const routeVm = $scope.routeVm = $scope.$root.utils.findInParentScope('routeVm', $scope); // jshint ignore:line

		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
			selected: [],
			loading: false,
			disableEdit: false,
			isEditOrAdd: false,
			permissions: $scope.permissions,
			pages: 0,
			uiFilter: {
				filterActive: false,
				filterValue: ''
			},
			uiSort: {
				sortActive: false,
				sortValue: '+_name'
			}
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line

			editItem(item) {
				item.isEditing = true;
				setDisabledState();
			},

			cancel(item) {
				$scope.onBeforeCancel({item: item})

				item.isEditing = false;
				if (item.isAdding) {
					let index = vm.allItems.indexOf(item);
					if (index >= 0) {
						vm.allItems.splice(index, 1);
					}
				} else {
					item.$revert();
				}
				setDisabledState();
			},

			addNewItem() {
				vm.allItems.unshift({ isEditing: true, isAdding: true });
				setDisabledState();
			},

			saveItem(item) {
				if (!item.isAdding && item.$save) {

					item.isSaving = true;
					
					let onBeforeSaveResult = $scope.onBeforeSave({item: item});
					
					if (onBeforeSaveResult) {
						// If there was an error stop the save process.
						if (!onBeforeSaveResult.success){
							item.isSaving = false;
							ssToastHlp.error(``, `${onBeforeSaveResult.errorMessage}`, false);
							return;
						}
					}

					item.$save().then(() => {
						ssToastHlp.success(``, `${ssStringHlp.toTitleCase($scope.itemType)} "${item._name}" Saved`, false);
						item.isEditing = false;
						setDisabledState();

						item.isSaving = false;
						ssStorage.clear($scope.config.cacheKey);
						$state.reload();
					}).catch((error) => {
						// ssToastHlp.error(``,`error while saving ${$scope.itemType} "${item._name}"`, false);
						$log.error(`error while saving ${$scope.itemType} "${item._name}"`, error);
						item.isSaving = false;
					});
				} else {
					var onBeforeSaveResult = $scope.onBeforeSave({item: item})
					
					if (onBeforeSaveResult) {
						// If there was an error stop the save process.
						if (!onBeforeSaveResult.success){
							item.isSaving = false;
							ssToastHlp.error(``, `${onBeforeSaveResult.errorMessage}`, false);
							return;
						}
					}
					vm.allItems.$save(false).then(() => {
						ssToastHlp.success(``, `New ${ssStringHlp.toTitleCase($scope.itemType)} "${item._name}" Added`, false);
						fetchData({ currentPage: 1 });

						// reset the cache and reload the state to get out of edit mode
						ssStorage.clear($scope.config.cacheKey);
						$state.reload();
					}).catch((error) => {
						// ssToastHlp.error(``,`error while saving ${$scope.itemType} "${item._name}"`, false);
						$log.error(`error while saving ${$scope.itemType} "${item._name}"`, error);

						item.isSaving = false;
					});
				}
			},

			removeItem(target) {
				var toastMsg;
				var totalCount = 0;
				var successCount = 0;
				var itemsRemoved = [];

				//	Make sure the targets are an array
				if (!angular.isArray(target)) {
					target = [target];
				}

				//	method to loop through the target array and attempt to remove each item
				function remove() {
					var deferred = $q.defer();

					target.forEach((item) => {

						//	make sure the individual item is found in the collection
						let foundItem = vm.allItems.find((f) => {
							return f.id === item.id;
						});

						//	attempt removal of items and resolve after all have run
						if (foundItem) {
							foundItem.$remove().then((res) => {
								itemsRemoved.push(item);
								successCount++;
							}).catch((error) => {
								// ssToastHlp.error(``,`error while removing ${item._name._name}`, false);
								$log.error(`error while deleting "${item._name}"`, error);
							}).finally(() => {
								totalCount++;
								if (target.length === totalCount) {
									deferred.resolve(successCount);
								}
							});
						}
					});
					return deferred.promise;
				}

				remove().then((successCount) => {
					toastMsg = successCount > 1 ? `${successCount} ${$scope.itemType}s` : `"${itemsRemoved[0]._name}"`;
					ssToastHlp.success(``, `${toastMsg} deleted`, false);
					$state.reload();
					fetchData({ currentPage: 1 });
				});
			},

			fetchData,

			sort({sortBy, sortDirection}) {
				if ($scope.config.useUiFilter){
					vm.uiSort.sortActive = true
					vm.uiSort.sortValue = (sortDirection == 'asc') ? '+_name' : '-_name'
				}else {
					sortOptions = {
						field: sortBy,
						dir: sortDirection
					};
					fetchData({ currentPage: 1 });
				}
					
			},
		};

		const debounce = ssDebounce(() => {
			fetchData({ currentPage: 1 });
		}, 350);

		//	reference $scope.go in order to pass its methods to other scopes
		$scope.actions = $scope.go;
		
		// watch the search input and filter out the results
		debounce()

		$scope.$watch('vm.search', (nv, ov) => {
			if ($scope.config.useUiFilter){
				vm.uiFilter.filterActive = true
				vm.uiFilter.filterValue	= vm.search
			}else {
				debounce()
			}
		});

		// watch the selected length and disable all edit functionality if any are selected
		$scope.$watch('vm.selected.length', function(nv, ov) {
			if (nv === ov) { return; }
			vm.disableEdit = nv > 0;
		});

		function extendModel() {

			if (!vm.allItems.length) { return; }

			angular.forEach(vm.allItems, function(item) {

				item.isEditing = false;
				item.isSaving = false;

				// check to see if there is a '_dataCategory' property and set another property on the object with a value of the corresponding '_dataCategory' enum
				if (item._dataCategory != null) {
					if (!$scope.config.hasOwnProperty('dataCategoryEnum')) { throw 'No data category found.' }
					const enumType = $scope.config.dataCategoryEnum;
					item.selectedDataCategory = ssEnum[enumType].byValue(item._dataCategory);
				}

				if (item['name']) {
					item['_name'] = item['name'];
					delete item['name'];
				}

				// adds any additional properties to the item
				if ($scope.config.customExtend) {
					item = $scope.config.customExtend(item);
				}
			});
		}

		// disables the add & select functionality if any items are either currently being added or edited and runs the fetchData function only when that value is false (no items currently being added or edited)
		function setDisabledState() {
			// set a state value based on whether or not any of the items are currently being added or edited
			vm.isEditOrAdd = vm.allItems.some(i => i.isAdding || i.isEditing);
		}

		function fetchData(event) {
			vm.loading = true;

			vm.currentPage = event.currentPage;
			if (currentRequest != null) {
				currentRequest.$abort();
				currentRequest = null;
			}

			return ssAsync(function*() {
				try {
					let filter = Object.assign({}, {
							offset: (vm.currentPage - 1) * pageSize,
							limit: pageSize,
						},
						searchFilter(vm.search, 'name'),
						sortOrNull(sortOptions || {field: 'name', dir: 'asc'})
					);

					vm.allItems = yield $scope.endpoint({ filter });

					if (vm.allItems != null) {
						// set up pagination fields
						vm.totalResults = parseInt(vm.allItems.$headers['total-results'], 10);
						vm.totalResults = isNaN(vm.totalResults) ? 0 : vm.totalResults;
						vm.pages = Math.ceil(vm.totalResults / pageSize);
					} else {
						vm.totalResults = 0;
						vm.pages = 0;
					}

					extendModel();

				} catch (error) {
					ssToastHlp.error('', `Error Retrieving ${$scope.itemType}s`, false);
					console.error(`Error Retrieving ${$scope.itemType}s:`, error);
				} finally {
					vm.loading = false;
					setDisabledState();
					vm.selected = [];
				}
			})();
		}

		/**
		 *
		 * @param {string} val - the value so search for
		 * @param {string} type - the field name that the val is mapped to
		 * @returns {object?} - the search object that will be sent to the filter request (OR NULL)
		 */
		function searchFilter(val, type) {
			var o = {};

			if (val != null) {
				o['filter[' + type + ']'] = val;
				return o;
			} else {
				return null;
			}
		}

		/**
		 * sortOrNull - Sets the sort object for a filter request
		 * @param {object} sort - object describing a sort field and direction
		 * @returns {object} - the sort object for a filter request
		 */
		function sortOrNull(sort) {
			if (sort == null) { return; }
			var o = {};
			o['sort[' + sort.field + ']'] = sort.dir;
			return o;
		}
	}
}

// #-- END Component (cmp-grid-group) --# //
// #------------------------------------# //

angular.module('ec-settings-components').directive('cmpGridGroup', () => new GridGroup(props, 'grid-group'));
