import {State} from 'base-state';
// #-------------------------------------# //
// #---- State (/permissions/groups) ----# //

// view model that is shared by all segments on this route,
// used for segment coordination. Define initial state here.
const routeVm = {};

const stateName = 'permissions.groups';

// config object for ui-router
// http://angular-ui.github.io/ui-router/site/#/api/ui.router.state.$stateProvider#methods_state
const stateConfig = {
	url: '/groups',
	templateUrl: 'routes/permissions/groups/groups.html',
	resolve: {
		data: /*@ngInject*/ (ssAsync, ssSecurityGroupsApi, ssPermissionProfilesApi) => {
			return ssAsync(function*() {
				routeVm.groups = yield ssSecurityGroupsApi.query({
					include: 'securityScopes',
					limit: 50
				});
				/*
					limit: 50
					Is the max number of record the end point can send at a time
					further records can be retrieved iterativelty.

					We are showing the list of Permission Profiles in the dropdown and
					we have to either iteratively load all the Profiles and then show them
					in drop down or drop downs have to support lazy loading.

					Mel has mentioned in the ticket https://frontlinetechnologies.atlassian.net/browse/PLAT-4804
					that she is fine with limit of 50 and expect that districts will not going
					to exceed the limit.
				*/
				routeVm.profiles = yield ssPermissionProfilesApi.query({
					include: 'permissionSets',
					limit: 50
				});

			//	filter out the locked profiles
				routeVm.groups = routeVm.groups.filter(g => g._groupName !== "MinimalAllEmployees");
				routeVm.profiles = routeVm.profiles.filter(g => g._permissionProfileName !== "Minimal-View-Self");

				routeVm.groups.forEach(g => {
					g._securityScopes.forEach(s => {
						s.permissionProfile = routeVm.profiles.find(p => s._permissionProfileId === p.id);

						if (s.permissionProfile != null) {
							let profile = s.permissionProfile;
							if (!angular.isArray(profile.groups)) {
								profile.groups = [];
							}
							if (profile.groups.indexOf(g) < 0) {
								profile.groups.push(g);
							}
						}
					});
				});
			})();
		},
		$title: /*@ngInject*/ () => {
			return 'Frontline Central - Permissions Groups';
		}
	}
};

// #-- END State (/permissions/groups) --# //
// #-------------------------------------# //

angular.module('ec-settings').config( /*@ngInject*/ ($stateProvider, $urlRouterProvider) => {
	const defaultRoute = '/ec-settings/permissions/groups/all';
	const baseRoute = '/ec-settings/permissions/groups';

	$urlRouterProvider.when(baseRoute, defaultRoute);
	$urlRouterProvider.when(`${baseRoute}/`, defaultRoute);

	$stateProvider.state(`base.${stateName}`, new State(stateConfig, routeVm));
});
