import {Component} from 'base-component';
// #--------------------------------------------# //
// #---- Component (cmp-protected-resource) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	resource: '='
};

class ProtectedResource extends Component {
	/*@ngInject*/
	controller($scope) {
		// view model for tracking state
		const vm = $scope.vm = { // jshint ignore:line
		};

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
		};
	}
}

// #-- END Component (cmp-protected-resource) --# //
// #--------------------------------------------# //

angular.module('ec-settings-components').directive('cmpProtectedResource', () => new ProtectedResource(props, 'protected-resource') );
