import {Segment} from 'base-segment';
// #---------------------------------------------------------# //
// #---- Segment (/permissions/profiles/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope) {
		const routeVm = $scope.routeVm;

		// hookup segment logic
		const logic = $injector.get('segmentPermissionsProfilesContentLogic');// eslint-disable-line

		$scope.profilesVm = routeVm;

		const vm = $scope.vm = { // eslint-disable-line
			autoCreatedProfiles: [],
			customCreatedProfiles: [],
		};

		$scope.$watchCollection('routeVm.profiles', () => {
			vm.autoCreatedProfiles = routeVm.profiles.filter(x => x._autoCreated);
			vm.customCreatedProfiles = routeVm.profiles.filter(x => !x._autoCreated);
		});

		const go = $scope.go = { // eslint-disable-line
		};
	}
}

// #-- END Segment (/permissions/profiles/segment-content) --# //
// #---------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentPermissionsProfilesContent', () => new Content('content'));
