/*@ngInject*/
function messagingSvc() {
	// #--------------------------------# //
	// #---- Service (messagingSvc) ----# //

	const NOT_FOUND = 'NOT FOUND';

	// Oh yes I did.
	const messages = {
		general: {
			save: 'Save',
			cancel: 'Cancel',
			edit: 'Edit',
			continue: 'Continue',
			continueEditing: 'Continue Editing',
			delete: 'Delete'
		},
		credentials: {

		},
		settings: {
			performanceWarning: 'This page may run more slowly when the number of lanes or steps exceeds 50.'
		},
		generic: {
			credentials: {
				editConfirmationTitle: 'Edit Custom {0}',
				editConfirmationMessage: 'Are you sure you want to edit this {0}? This {0} may already be associated with user credentials.',
				deleteConfirmationTitle: 'Delete Custom {0}',
				deleteConfirmationMessage: 'Are you sure you want to delete this {0}? This {0} may already be associated with users\' credentials.  Deleting this item will prevent users from using this item across Frontline Central.',
			}
		}
	}

	function getMessage(area, key) {
		let ret = messages[area][key];
		if (ret == null) {
			console.info(`Could not find message from area '${area}' and key '${key}'.`);
			ret = NOT_FOUND;
		}
		return ret;
	}

	/**
	 * Gets a generic message and fills the placeholders with additional strings
	 * @param {string} area The area type of the message string (General, Credentials, etc)
	 * @param {string} key The key of the string to fetch
	 * @param {array} args an array of strings that will be used in the placeholder message
	 * @todo Not sure how to properly backfill this in JS right now.
	 */
	function getPlaceholderMessage(area, key, args) {
		let ret = NOT_FOUND;
		try {
			ret = messages.generic[area][key];
		}
		finally {
			if (ret == null || ret == 'NOT FOUND') {
				console.info(`Could not find message from area '${area}' and key '${key}'.`);
			}
		}

		return sFormat(ret, args);
	}

	/**
	 * A basic C# string.Format()-like method used to do string replacements on strings with ordered placeholders
	 * @param {string} input The input string to have its placeholders replaced
	 * @param {array} args The string array of strings to use to replace the input string's placeholders in order 0-N
	 * @example: input: 'The {0} has {1}'
	 * 						args: ['sky','clouds'] => "The sky has clouds"
	 * 					 input: 'The {0} has {0}'
	 * 						args: ['sky','clouds'] => "The sky has sky"
	 */
	function sFormat(input, args) {
		if (input == null) { throw new Error(`Invalid argument exception: provided input was: '${input}'`); }
		if (typeof args !== typeof []) { throw new Error('Expected array of replacement values'); }
		let place = input; // the text with the inputs to replace

		args.forEach((element, idx) => {
			let r = new RegExp(`\\{${idx}\}`, 'gim'); // dynamically build the regex for replacing ordered elements
			place = place.replace(r, element);
		});

		return place;
	}
	// External service API
	return {
		getMessage,
		getPlaceholderMessage
	};

	// #-- END Service (messagingSvc) --# //
	// #--------------------------------# //
}

angular.module('ec-settings-services').factory('messagingSvc', messagingSvc);
