/*@ngInject*/
function logic(ssArrayHlp) {
	// logic api
	return {
	//	Reorder the dislayed groups so that the locked groups appear first
		reorderGroups(_groups) {
			let locked = ssArrayHlp.where([..._groups], {_locked: true});
			let autoCreated = ssArrayHlp.where([..._groups], {_autoCreated: true, _locked: false});
			let others = ssArrayHlp.where([..._groups], {_autoCreated: false, _locked: false});
			return [...locked,...autoCreated,...others];
		}
	};
}
angular.module('ec-settings-segment-logic').factory('segmentPermissionsGroupsContentLogic', logic);
