import {Segment} from 'base-segment';
// #-----------------------------------------------------------------# //
// #---- Segment (/permissions/groups/group-all/segment-content) ----# //

class Content extends Segment {
	/*@ngInject*/
	controller($injector, $scope, $state, ssAsync, ssSecurityGroupsApi, ssToastHlp, ssStorage) {
		// view model that is shared by all segments on this route,
		// used for segment coordination
		const routeVm = $scope.routeVm;  // eslint-disable-line

		// hookup segment logic
		const logic = $injector.get('segmentPermissionsGroupsGroupAllContentLogic'); // eslint-disable-line

		const groupsVm = $scope.groupsVm;

		var addGroup;

		const vm = $scope.vm = {
			formOpened: false,
			errors: '',
			// reorderedGroups: logic.reorderGroups(groupsVm.groups),
			defaultGroup: {
				_groupName: '',
				_groupDescription: '',
				_employeeIds: []
			},
			loading: false,
			saving: false
		};

		const go = $scope.go = { // eslint-disable-line
			add() {
				if (addGroup == null) {
					vm.saving = true;
					addGroup = ssAsync(function*() {
						try {
						//	create the group
							let group = yield ssSecurityGroupsApi.add({
								'include': 'securityScopes'
							}, vm.defaultGroup, false);

							if (group.id != null) {
								groupsVm.groups.push(group);
								$state.go('base.permissions.groups.detail', {
									groupId: group.id
								});
							} else {
								addGroup = null;
								vm.saving = false;
								ssToastHlp.error('There was an error creating the group. Please try again later.');
							}
						} catch (err) {
							ssToastHlp.error('There was an error creating the group. Please try again later.');
							addGroup = null;
							vm.saving = false;
						}
					})();
				}
			},

			close() {
				vm.formOpened = false;
				vm.errors = '';
				$scope.$emit('ss-menu-close');
			}

		};

		$scope.$on('ss-menu-closed', (event, elem, ddElem) => {
			if (!vm.formOpened) {return;}
			if (ddElem.hasClass('create-form')) {
				vm.formOpened = false;
				$scope.$root.safeDigest($scope);
			}
		});
	}
}

// #-- END Segment (/permissions/groups/group-all/segment-content) --# //
// #-----------------------------------------------------------------# //

angular.module('ec-settings-segments').directive('segmentPermissionsGroupsGroupAllContent', () => new Content('content') );
