import {Component} from 'base-component';
// #-----------------------------# //
// #---- Component (cmp-tag) ----# //

// props is the directive's isolate scope object
// http://onehungrymind.com/angularjs-sticky-notes-pt-2-isolated-scope
const props = {
	profile: '@?',
	group: '@?',
	individual: '@?',
	id: '@?'
};

class Tag extends Component {
	/*@ngInject*/
	controller($scope, $state) {
		// view model for tracking state
		const vm = $scope.vm = {
			classes: '',
			label: '',
			link: null,
			iconType: ''
		};

		if ($scope.profile != null) {
			vm.label = $scope.profile;
			vm.classes = 'profile';
			vm.iconType = 'key';
			vm.link = {
				base: 'base.permissions.profiles.detail',
				attr: {
					profileId: $scope.id
				}
			};
		} else if ($scope.group != null) {
			vm.label = $scope.group;
			vm.classes = 'group';
			vm.iconType = 'users';
			vm.link = {
				base: 'base.permissions.groups.detail',
				attr: {
					groupId: $scope.id
				}
			};
		} else if ($scope.individual != null) {
			vm.label = $scope.individual;
			vm.classes = 'individual';
		}

		// behavior that needs to be bound to the view
		const go = $scope.go = { // jshint ignore:line
			click() {
				if (vm.link == null || $scope.id == null) {return;}
				$state.go(vm.link.base, vm.link.attr);
			}
		};
	}
}

// #-- END Component (cmp-tag) --# //
// #-----------------------------# //

angular.module('ec-settings-components').directive('cmpTag', () => new Tag(props, 'tag'));
